<template>
  <div id="app" class="app-container">
    <div class="header">
      <h1 class="title">
        <div v-on:click="goToHomepage" style="cursor: pointer; display: inline-block"><img src="../static/logo.png" style="height: 50px; width:50px; margin-right: 20px"/>Schrodinger </div>
        <span style="font-size: 13px; margin-left: 15px">TiDB Cluster Stability Test</span>
        <el-button style=" float: right; padding: 13px 5px; margin-left: 5px" @click.native="grafanaMinotor()"><img src="../static/grafana.png" style="margin-right: 10px; width: 10px; height: 10px"/>Schrodinger Grafana Monitor</el-button>
      </h1>


    </div>
    <div class="tabs" v-if="showTabs && activeTab != 'bench'" style="margin-top: 15px">
      <Tabs type="card" v-model="activeTab" @on-click="tabClicked" style="font-size: 16px;">
        <TabPane label="Summary" name="summary"></TabPane>
        <TabPane label="Box" icon="ios-cube" name="box"></TabPane>
        <TabPane label="Cat Pool" icon="logo-octocat" name="cat"></TabPane>
        <TabPane label="Experiment Pool" icon="ios-flask" name="experiment"></TabPane>
        <TabPane label="Test Case Pool" icon="ios-build" name="testcase"></TabPane>
        <TabPane label="Config Pool" icon="ios-cog" name="config"></TabPane>
      </Tabs>
    </div>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>

export default {
  name: 'app',

  data() {
    let activeTab = 'summary';
    location.hash && (activeTab = location.hash.slice(2));
    console.log('active Tab', activeTab)

    return {
      catid: this.$route.query.id,
      experimentid: this.$route.query.experimentid,
      boxid: this.$route.query.boxid,
      activeTab,
      showTabs: true,
      run: 'left'
    };
  },

  methods: {
    tabClicked(tab) {
      this.$router.push({path: this.activeTab})
    },

    goToHomepage() {
      this.activeTab = 'summary'
      this.$router.push({path: this.activeTab})
    },

    grafanaMinotor() {
      window.open('http://172.16.30.11:30000')
    }

  },
    watch: {
    '$route' (to, from) {
      if((to.fullPath).includes('boxdetail') || (to.fullPath).includes('experimentdetail') || (to.fullPath).includes('catdetail') || (to.fullPath).includes('admin') || (to.fullPath).includes('bench')) {
        this.showTabs = false
      } else {
        this.showTabs = true
        location.hash && (this.activeTab = location.hash.slice(2));
      }
    }
  }
}
</script>

<style lang="scss">
 @import './styles/index.scss';
</style>



