<template>
 <div>
   <!-- admin form -->
   <el-form>
     <el-form-item label="Set Experiment Number: " prop="generalExperimentSize">
       <div v-if="experimentSizeSubmitted" style="display: inline-block; margin-right: 20px">{{generalExperimentSize}}</div>
       <el-button v-if="experimentSizeSubmitted" @click="editExperiment">Edit</el-button>
       <el-input v-model.number="generalExperimentSize" v-if="!experimentSizeSubmitted" style="width: 200px"></el-input>
       <el-button v-if="!experimentSizeSubmitted" type="primary" @click="submitExperimentSize()">Confirm</el-button>
     </el-form-item>
     <el-form-item label="Set Bench Number: " prop="benchExperimentSize">
       <div v-if="benchSizeSubmitted" style="display: inline-block; margin-right: 20px">{{benchExperimentSize}}</div>
       <el-button v-if="benchSizeSubmitted" @click="editBench">Edit</el-button>
       <el-input v-model.number="benchExperimentSize" v-if="!benchSizeSubmitted" style="width: 200px"></el-input>
       <el-button v-if="!benchSizeSubmitted" type="primary" @click="submitBenchSize()">Confirm</el-button>
     </el-form-item>
     <el-form-item label="TiKV Number: " prop="tikvNo">
       {{tikvNo}}
     </el-form-item>
     <el-form-item label="TiDB Number: " prop="tidbNo">
       {{tidbNo}}
     </el-form-item>

     <el-form-item label="PD Number: " prop="pdNo">
       {{pdNo}}
     </el-form-item>
   </el-form>
 </div>
</template>

<script>
import ajax from '../request/index'
export default {
  data() {
    return {
      experimentSizeSubmitted: true,
      benchSizeSubmitted: true,
      generalExperimentSize: 0,
      benchExperimentSize: 0,
      allExperimentSize: 0,
      tikvNo: 0,
      tidbNo: 0,
      pdNo: 0
    }
  },

  created() {
    this.getExperimentSize()
    this.getBenchSize()
    this.getResource()
  },

  methods: {
    // click edit general experiment size
    editExperiment() {
      this.experimentSizeSubmitted = false
    },

    // click edit bench experiment size
    editBench(){
      this.benchSizeSubmitted = false
    },

    // submit general experiment size
    submitExperimentSize() {
      console.log('click submit general experiment size')
      ajax.setExperimentSize({size: this.generalExperimentSize}).then(result => {
        console.log('this.general siziiiie,' , this.generalExperimentSize)
        if(result.data.code == 200) {
          console.log('successful')
          this.experimentSizeSubmitted = true
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Update General Experiment Size Successfully'
          })
        } else {
          console.log('this.general size,' , this.generalExperimentSize)
          this.$notify.error({
            title: 'Error',
            message: 'Update General Experiment Size Failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // submit bench experiment size
    submitBenchSize() {
      ajax.setBenchSize({size: this.benchExperimentSize}).then(result => {
        if(result.data.code == 200) {
          console.log('successful')
          this.benchSizeSubmitted = true
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Update Bench Size Successfully'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // get general experiment size
    getExperimentSize() {
      ajax.getExperimentSize().then(result => {
        if(result.data.code == 200) {
          console.log('yes! result is', result)
          this.generalExperimentSize = result.data.data
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get Experiment Running Size Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // get bench experiment size
    getBenchSize() {
      ajax.getBenchSize().then(result => {
        if(result.data.code == 200) {
          console.log('yes! result is', result)
          this.benchExperimentSize = result.data.data
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get Experiment Running Size Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // get tikv, tidb and pd size
    getResource() {
      console.log('get resources')
      ajax.getResourceUsage().then(result => {
        if(result.data.code == 200) {
          this.tikvNo = result.data.data.tikv_size
          this.tidbNo = result.data.data.tidb_size
          this.pdNo = result.data.data.pd_size
        }else {
          this.$notify.error({
            title: 'Error',
            message: 'Get Total Resource Usage Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    }
  }
}
</script>

<style>

</style>



