<template>
<div>
  <Divider orientation="left" style="width: 50%">Experiment: {{experimentDetail.name}}</Divider>

  <el-card class="catDetailCard">
    <div slot="header" class="clearfix">Experiment Basic Info</div>
    <el-form inline class="basicForm" :data="experimentDetail">
      <el-form-item label="Name: "> {{experimentDetail.name}}</el-form-item>
      <Divider type="vertical"/>
      <el-form-item label="Status: ">
        <el-tag type="success" v-if="experimentDetail.status == 'NORMAL' && experimentDetail.destroyed == false">Normal </el-tag>
        <el-tag type="success" v-if="experimentDetail.destroyed && experimentDetail.status == 'NORMAL'">Normal & Stopped</el-tag>
        <el-tag type="warning" v-if="experimentDetail.destroyed && experimentDetail.status == 'ABNORMAL'">Abnormal & Stopped </el-tag>
        <el-tag type="danger" v-if="experimentDetail.status == 'ABNORMAL' && experimentDetail.destroyed == false">Abnormal</el-tag>
      </el-form-item>
      <Divider type="vertical"></Divider>
      <el-form-item label="Stage: ">
        <el-tag type="success" v-if="experimentDetail.status == 'NORMAL' && experimentDetail.destroyed == false" style="margin-top: 5px">{{(experimentDetail.stage).charAt(0) + (experimentDetail.stage).toLowerCase().slice(1)}}</el-tag>
        <el-tag type="success" v-if="experimentDetail.destroyed && experimentDetail.status == 'NORMAL'" style="margin-top: 5px">{{(experimentDetail.stage).charAt(0) + (experimentDetail.stage).toLowerCase().slice(1)}}</el-tag>
        <el-tag type="warning" v-if="experimentDetail.destroyed && experimentDetail.status == 'ABNORMAL'" style="margin-top: 5px">{{(experimentDetail.stage).charAt(0) + (experimentDetail.stage).toLowerCase().slice(1)}}</el-tag>
        <el-tag type="danger" v-if="experimentDetail.status == 'ABNORMAL' && experimentDetail.destroyed == false" style="margin-top: 5px">{{(experimentDetail.stage).charAt(0) + (experimentDetail.stage).toLowerCase().slice(1)}}</el-tag>
      </el-form-item>
      <Divider type="vertical"></Divider>
      <el-form-item label="Create Time: "> {{experimentDetail.create_time}}</el-form-item>
      <Divider type="vertical"></Divider>
      <el-form-item label="Update Time: "> {{experimentDetail.update_time}}</el-form-item>
    </el-form>
  </el-card>
  <el-card class="catDetailCard">
    <div slot="header" class="clearfix">Cat Basic info
      <el-button v-if="experimentDetail.stage != 'PENDING'" type="primary" style="float: right; padding: 13px 5px; margin-left: 5px" @click.native="handleCatDetailClick(experimentDetail.cat.id)"><Icon type="ios-paper-outline" style="margin-right:10px;" />More Detail</el-button>
      <el-button v-if="experimentDetail.stage != 'PENDING'" style="float: right; padding: 13px 5px; margin-left: 5px" @click.native="grafanaMinotor(experimentDetail.cat.report.cluster.grafana_service.node_port)">
        <img src="../../static/grafana.png" style="margin-right: 10px; width: 10px; height: 10px"/>Cat Grafana Monitor</el-button>
    </div>
    <el-form v-if="experimentDetail.cat !=null" inline class="basicForm" :data="experimentDetail">
      <el-form-item label="Cat Name:">{{experimentDetail.cat.name}}</el-form-item>
      <Divider type="vertical"/>
      <el-form-item label="Cat Status: ">
        <el-tag type="success" v-if="experimentDetail.cat.status == 'NORMAL' && experimentDetail.cat.destroyed == false">Normal </el-tag>
        <el-tag type="success" v-if="experimentDetail.cat.destroyed && experimentDetail.cat.status == 'NORMAL'">Normal & Stopped</el-tag>
        <el-tag type="warning" v-if="experimentDetail.cat.destroyed && experimentDetail.cat.status == 'ABNORMAL'">Abnormal & Stopped </el-tag>
        <el-tag type="danger" v-if="experimentDetail.cat.status == 'ABNORMAL' && experimentDetail.cat.destroyed == false">Abnormal</el-tag>
      </el-form-item>
      <Divider type="vertical"/>
      <el-form-item label="Cat Stage: ">
        <el-tag type="success" v-if="experimentDetail.cat.status == 'NORMAL' && experimentDetail.cat.destroyed == false" style="margin-top: 5px">{{(experimentDetail.cat.stage).charAt(0) + (experimentDetail.cat.stage).toLowerCase().slice(1)}}</el-tag>
        <el-tag type="success" v-if="experimentDetail.cat.destroyed && experimentDetail.cat.status == 'NORMAL'" style="margin-top: 5px">{{(experimentDetail.cat.stage).charAt(0) + (experimentDetail.cat.stage).toLowerCase().slice(1)}}</el-tag>
        <el-tag type="warning" v-if="experimentDetail.cat.destroyed && experimentDetail.cat.status == 'ABNORMAL'" style="margin-top: 5px">{{(experimentDetail.cat.stage).charAt(0) + (experimentDetail.cat.stage).toLowerCase().slice(1)}}</el-tag>
        <el-tag type="danger" v-if="experimentDetail.cat.status == 'ABNORMAL' && experimentDetail.cat.destroyed == false" style="margin-top: 5px">{{(experimentDetail.cat.stage).charAt(0) + (experimentDetail.cat.stage).toLowerCase().slice(1)}}</el-tag>
      </el-form-item>

      <el-row :gutter="12" style="margin-top: 20px">

        <el-col :span="8" v-for="(cluster, index) in config_ver_array" :key="index">
          <el-card shadow="hover" class="catDetailCard">
            <div slot="header">{{clusterNames[index]}} (size = {{cluster_sizes[index]}})</div>

            <div class="cat__detail-card-items">
              <div class="cat__basicInfo-items"> Compile Platform </div>
              <div v-if="experimentDetail.report.config != null && experimentDetail.Config.Cat[config_ver_array[index]].version.platform" class="no-copy-block">{{experimentDetail.Config.Cat[config_ver_array[index]].version.platform}}</div>
              <div v-else class="no-copy-block">--</div>
              <div class="underline"></div>
            </div>

            <div class="cat__detail-card-items" v-if="basicInfoTextArr.length != 0 && basicInfoTextArr[index] != ''">
              <div class="cat__basicInfo-items"> Config Version </div>
               <el-tooltip>
                <div slot="content">{{basicInfoTextArr[index]}}</div>
                <div class="no-copy-block">
                  <span>hash: </span>
                  <span class="cat__basicInfo-copy-text">{{(basicInfoTextArr[index]).substring(0,7)}}...</span>
                  <span class="command__copy-table" v-clipboard="basicInfoTextArr[index]" @success="handleSuccess">
                    <el-tooltip placement="top">
                      <div slot="content">copy</div>
                      <Icon class="command__copy-icon" type="md-copy"/>
                    </el-tooltip>
                  </span>
                </div>
              </el-tooltip>
            </div>

            <div class="cat__detail-card-items" v-else-if="basicInfoTextArr.length != 0 && basicInfoTextArr[index] == ''">
              <div class="cat__basicInfo-items"> Config Version </div>
              <div class="no-copy-block"> {{experimentDetail.Config.Cat[config_ver_array[index]].version.type}}:{{experimentDetail.Config.Cat[config_ver_array[index]].version.value}} </div>
              <div class="underline"></div>
            </div>

            <div class="cat__detail-card-items" v-if="experimentDetail.report.run_version && basicInfoTextArr.length != 0 && basicInfoTextArr[index+3] != ''">
              <div class="cat__basicInfo-items"> Run Version </div>
              <el-tooltip>
                <div slot="content">{{basicInfoTextArr[index+3]}}</div>
                <div class="no-copy-block">
                  <span>hash: </span>
                  <span class="cat__basicInfo-copy-text">{{(basicInfoTextArr[index+3]).substring(0,7)}}...</span>
                  <span class="command__copy-table" v-clipboard="basicInfoTextArr[index+3]" @success="handleSuccess">
                    <el-tooltip placement="top">
                      <div slot="content">copy</div>
                      <Icon class="command__copy-icon" type="md-copy"/>
                    </el-tooltip>
                  </span>
                </div>
              </el-tooltip>
              <div class="underline"></div>
            </div>

            <div class="cat__detail-card-items" v-else>
              <div class="cat__basicInfo-items"> Run Version </div>
              <div class="no-copy-block"> -- </div>
              <div class="underline"></div>
            </div>
          </el-card>
        </el-col>
      </el-row>

    </el-form>

    <div v-else>Waiting experiment to run...</div>
  </el-card>

  <el-card class="catDetailCard">
    <div slot="header">Test</div>
      <el-form class="testTable" :data="experimentDetail">
        <el-form-item v-if="experimentDetail.Config != null" label="Execution Method: ">
          <el-tag v-if="experimentDetail.Config.Tests.in_order">Serial Execution</el-tag>
          <el-tag v-else>Parallel Execution</el-tag>
        </el-form-item>
        <el-form-item label="Test List: ">
          <el-tag v-for="(test, index) in testNames" :key="index" style="margin-left: 5px">{{test}}</el-tag>
        </el-form-item>
      </el-form>

      <el-table :highlight-current-row="true" :data="testReport">
        <el-table-column label="Test Name" prop="name"></el-table-column>
        <el-table-column label="Stage" prop="status"></el-table-column>
        <el-table-column label="Create Time" prop="create_time"></el-table-column>
        <el-table-column label="Update Time" prop="update_time"></el-table-column>
        <el-table-column label="Pod IP">
          <template slot-scope="scope">
            <el-tooltip>
              <template slot="content" v-if="scope.row.report == null || scope.row.report.pod_ip == ''">No Pod IP</template>
              <div v-if="scope.row.report == null || scope.row.report.pod_ip == ''"> -- </div>
            </el-tooltip>
            <div v-if="scope.row.report != null && scope.row.report.pod_ip != ''">{{scope.row.report.pod_ip}}</div>
          </template>
        </el-table-column>
        <el-table-column label="Log Address">
          <template slot-scope="scope">
            <el-tooltip v-if="scope.row.report != null && scope.row.report.log_path != ''" placement="bottom">
              <div slot="content">{{scope.row.report.log_path}}</div>
              <div v-if="scope.row.report != null && scope.row.report.log_path != ''">
                <div class="command command-table">
                    <div type="text" class="command__line">{{(scope.row.report.log_path).substring(0,7)}}...</div>
                    <div class="command__copy-table" v-clipboard="scope.row.report.log_path" @success="handleSuccess">
                      <el-tooltip placement="top">
                        <div slot="content">copy</div>
                        <Icon class="command__copy-icon" type="md-copy"/>
                      </el-tooltip>
                    </div>
                </div>
              </div>
            </el-tooltip>
            <div v-else> -- </div>
          </template>
        </el-table-column>
        <el-table-column label="Log">
          <template slot-scope="scope">
            <el-tooltip>
              <template v-if="scope.row.report == null || scope.row.report.log == ''" slot="content">No Log</template>
              <template v-else slot="content">Log Detail</template>
              <div v-if="scope.row.report == null ||  scope.row.report.log == ''">--</div>
              <el-button v-else circle type="info"><Icon type="ios-paper-outline" @click.native="handleLogClick(scope.row.report.log)"></Icon></el-button>
            </el-tooltip>

          </template>
        </el-table-column>
      </el-table>
  </el-card>
    <!-- </el-row> -->

    <el-dialog title="Test Log" :close-on-click-modal='false' :visible.sync="testLogDialog">
      {{testLog}}
    </el-dialog>

</div>
</template>

<script>
import ajax from '../request/index'
import _ from 'lodash'

export default {
  data() {
    return {
      experimentDetail: '',
      experimentID: this.$route.query.experimentid,
      boxID: this.$route.query.boxid,
      testNames:  [],
      testReport: [],
      testLogDialog: false,
      testLog: '',
      updateBasicInfoIndex: 1,
      itemNames: ['Compile Platform', 'Config Version', 'Run Version'],
      config_ver_array: ['tidb_config', 'tikv_config', 'pd_config'],
      run_ver_array: ['tidb_version', 'tikv_version', 'pd_version'],
      cluster_sizes: [],
      clusterNames: ['TiDB', 'TiKV', 'PD'],
      basicInfoTextArr: []
    }
  },

  created() {
    this.getExperimentDetail(this.boxID, this.experimentID)
  },

  updated() {
    // get copy text in basic info section
    if(this.updateBasicInfoIndex == 1) {
      this.updateBasicInfoIndex++
      this.cluster_sizes.push(this.experimentDetail.cat.tidb, this.experimentDetail.cat.tikv, this.experimentDetail.cat.pd)
      let tempConfig_ver = this.experimentDetail.Config.Cat
      console.log('temconfig ver: ', tempConfig_ver)
      let tempRun_ver = this.experimentDetail.report.run_version
      for(let i = 0; i < this.config_ver_array.length; i++) {
        console.log('tempConfig_ver[this.config_ver_array[i]].version' ,tempConfig_ver[this.config_ver_array[i]].version)
        if(tempConfig_ver[this.config_ver_array[i]].version.type == 'hash') {
          this.basicInfoTextArr.push(tempConfig_ver[this.config_ver_array[i]].version.value)
        } else {
          this.basicInfoTextArr.push('')
        }
      }
      console.log('basicinfotext array: ', this.basicInfoTextArr)

      if(!tempRun_ver) {
        this.basicInfoTextArr.push('', '', '')
      } else {
        for(let j = 0; j < this.run_ver_array.length; j++) {
          if(tempRun_ver[this.run_ver_array[j]].type == 'hash') {
            this.basicInfoTextArr.push(tempRun_ver[this.run_ver_array[j]].value)
          } else {
            this.basicInfoTextArr.push('')
          }
        }
      }
    }
  },

  methods: {
    handleSuccess() {
      console.log('hello from success')
      this.$message.success('Copied!')
    },

    getExperimentDetail(boxID, experimentID) {
      console.log('get experiment detail')
      console.log('boxID is: ', boxID)
      console.log('experiment Id is: ', experimentID)
      ajax.getExperimentsDetailByID (boxID, experimentID).then(result => {
        if(result.data.code == 200) {
          console.log('The experiment detail is: ', result.data.data)
          this.experimentDetail = result.data.data
          ajax.getTestByID(boxID, experimentID).then(result => {
            if(result.data.code == 200) {
              if(result.data.data.list == null) {
                this.testReport = []
              } else {
                this.testReport = result.data.data.list
                console.log('the tests are: ', this.testReport)
              }
              this.testNames = _.values(this.experimentDetail.Config.Tests.tests)
              console.log('the test report is: ', this.testNames)
            } else {
              this.$notify.error({
                title: 'Error',
                message: 'Get Tests Failed'
              })
            }
          }).catch (resp => {
              this.$notify.error({
                title: 'Error',
                message: resp.message
              })
            })
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Failed to get experiment detail'
          })
        }
      }).catch (resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },


    handleStopExperiment(boxID, experimentID, experimentIndex,destroy_tidb_cluster) {
      console.log('click handle stop experiment')
      var warningMsg = ''
      if(destroy_tidb_cluster) {
        console.log('destroy_tidb_cluster is true')
        warningMsg = warningMsg + 'The cat testing in this experiment will be destroyed if you continue stopping experiment #'+ experimentIndex
      } else {
        warningMsg = warningMsg + 'Continue stopping experiment # ' + experimentIndex
      }
      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        console.log('click yes')
        ajax.destroyExperimentByID(boxID, experimentID).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Stop Experiment: ' + experimentIndex + ' Successfully'
            })
            // this.getExperiments(boxID, (this.currentPage - 1) * this.pageSize, this.pageSize)
            this.getAllExperiments()
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Stop Experiment: ' + experimentIndex
            })
          }
        }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

    handleLogClick(log) {
      console.log('hancle log click, log is: ', log)
      this.testLogDialog = true
      this.testLog = log
    },

    handleCatDetailClick(catID) {
      console.log('click handleCatDetailClick, id is: ', catID)
      let route = this.$router.resolve({path: '/catdetail?id='+catID})
      // window.open(route.href, '_blank')
      location.href = route.href
    },

    goBack() {
      console.log('go back clicked')
      this.$router.go(-1)
    },

    grafanaMinotor(nodePort) {
      let route = 'http://172.16.30.11:'+nodePort
      console.log('route href is: ', route)
      window.open(route)
    }
  }
}
</script>
<style>
  .experimentDetailCard {
    /* width: 100%; */
    margin-bottom: 20px;
  }

  .experimentDetailCard .el-card__header {
    padding: 10px 20px;
    /* border-bottom: none; */
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    font-size: 18px;
    font-weight: 350;
  }

  .experimentDetailCard .el-form .el-form-item {
    margin-bottom: 0px;
  }

  .ivu-divider-inner-text {
    font-size: 25px;
    font-weight: 400;
  }

  .basicForm .el-form-item .el-form-item__label{
    font-size: 15px;
  }

  .basicForm .el-form .ivu-divider, .ivu-divider-vertical {
    /* margin: 2px 2px; */
    width: 2px;
    height: 2em;
    top: 0.5em;
  }

  /* .testTable .el-form-item .el-form-item__label  {
    width: 15%;
  } */

.experimentDetailCard .el-table .cell {
  padding: 1px;
}
</style>




