<template>
  <div>
    <!-- Box Operations -->
    <el-row>
      <el-button v-on:click="goBack" style="cursor: pointer; margin-top: 18px"><Icon type="md-arrow-back" />Go Back</el-button>
      <div style="float: right">
        <div style="display: inline-block; font-size: 18px; margin-right: 10px">Box Operations</div>

        <el-tooltip effect="dark">
          <div slot="content">Edit box configuration</div>
          <el-button circle @click.native="handleEdit"><Icon type="md-create" size="15"></Icon> </el-button>
        </el-tooltip>

        <el-tooltip  effect="dark">
          <div v-if="showBoxConfig" slot="content">Hide box config</div>
          <div v-else slot="content">Show box configuration</div>
          <el-button type="primary" v-if="showBoxConfig" circle @click.native="hideConfigCards"><Icon type="ios-construct" size="15"></Icon></el-button>
            <el-button type="primary" v-else circle @click.native="showConfigCards"><Icon type="ios-construct" size="15"></Icon></el-button>
        </el-tooltip>

        <el-tooltip v-if="boxDetail.status == 'ONLINE'" effect="dark">
          <div slot="content">Tigger an experiment immediately with same config</div>
          <el-button type="warning" circle @click.native="handleManualTrigger"><Icon type="ios-flask" size="15"></Icon> </el-button>
        </el-tooltip>

        <el-tooltip v-if="boxDetail.status == 'ONLINE'"  effect="dark">
          <div slot="content">Pause box generating experiments</div>
          <el-button type="danger" circle @click.native="handleStop">
            <Icon size="15" type="ios-pause" ></Icon>
          </el-button>
        </el-tooltip>

        <el-tooltip v-if="boxDetail.status == 'OFFLINE'" effect="dark">
          <div slot="content">Resume box to generate new experiment with same box config</div>
          <el-button type="danger" circle @click.native="handleResume">
            <Icon size="15" type="ios-play"></Icon>
          </el-button>
        </el-tooltip>

        <el-tooltip  effect="dark">
          <div slot="content">Delete box</div>
          <el-button type="danger" circle @click.native="handleDelete"><Icon type="md-trash" size="15"></Icon></el-button>
        </el-tooltip>
      </div>
    </el-row>

    <!-- Experiments in this box -->
    <el-container>

      <!-- show box config -->
      <el-header style="height: none">
        <el-row v-if="showBoxConfig == true" type="flex" :gutter="10" style="padding: 20px 0; height: 400px">
          <el-col :span="6" v-if="boxDetail.cat != null">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span style="font-size: 1.2rem">Cat: {{catDetail.name}}</span>
              </div>
              <el-form class="configForm" v-if="boxDetail.cat.selected_cat != ''">
                <el-form-item label="PD Version:">
                  <el-tooltip>
                    <div slot="content">{{catDetail.pd_config.version.type}}-{{catDetail.pd_config.version.value}}</div>
                    <div>{{(catDetail.pd_config.version.type.substring(0,7))}}-{{(catDetail.pd_config.version.value).substring(0,7)}}</div>
                  </el-tooltip>
                </el-form-item>
                <el-form-item label="TiKV Version:">
                  <el-tooltip>
                    <div slot="content">{{catDetail.tikv_config.version.type}}-{{catDetail.tikv_config.version.value}}</div>
                    <div>{{(catDetail.tikv_config.version.type.substring(0,7))}}-{{(catDetail.tikv_config.version.value).substring(0,7)}}</div>
                  </el-tooltip>
                </el-form-item>
                <el-form-item label="TiDB Version:">
                  <el-tooltip>
                    <div slot="content">{{catDetail.tidb_config.version.type}}-{{catDetail.tidb_config.version.value}}</div>
                    <div>{{(catDetail.tidb_config.version.type.substring(0,7))}}-{{(catDetail.tidb_config.version.value).substring(0,7)}}</div>
                  </el-tooltip>
                </el-form-item>
                <el-form-item label="PD Size: ">{{catDetail.pd_config.size}} </el-form-item>
                <el-form-item label="TiKV Size: ">{{catDetail.tikv_config.size}} </el-form-item>
                <el-form-item label="TiDB Size: ">{{catDetail.tidb_config.size}} </el-form-item>
              </el-form>

              <el-form class="configForm" v-if="boxDetail.cat.selected_cat == ''">
                <el-form-item label="PD Version:">
                  <el-tooltip>
                    <div slot="content">{{catDetail.pd_config.version.type}}-{{catDetail.pd_config.version.value}}</div>
                    <div>{{(catDetail.pd_config.version.type.substring(0,7))}}-{{(catDetail.pd_config.version.value).substring(0,7)}} <br></div>
                  </el-tooltip>
                </el-form-item>
                <el-form-item label="TiKV Version:">
                  <el-tooltip>
                    <div slot="content">{{catDetail.tikv_config.version.type}}-{{catDetail.tikv_config.version.value}}</div>
                    <div>{{(catDetail.tikv_config.version.type.substring(0,7))}}-{{(catDetail.tikv_config.version.value).substring(0,7)}} <br></div>
                  </el-tooltip>
                </el-form-item>
                <el-form-item label="TiDB Version:">
                  <el-tooltip>
                    <div slot="content">{{catDetail.tidb_config.version.type}}-{{catDetail.tidb_config.version.value}}</div>
                    <div>{{(catDetail.tidb_config.version.type.substring(0,7))}}-{{(catDetail.tidb_config.version.value).substring(0,7)}} <br></div>
                  </el-tooltip>
                </el-form-item>
                <el-form-item label="PD Size: ">{{catDetail.pd_config.size}} </el-form-item>
                <el-form-item label="TiKV Size: ">{{catDetail.tikv_config.size}} </el-form-item>
                <el-form-item label="TiDB Size: ">{{catDetail.tidb_config.size}} </el-form-item>
              </el-form>
            </el-card>
          </el-col>

          <el-col :span="6">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span style="font-size: 1.2rem">Tests: {{boxDetail.tests.tests.length}}</span>
              </div>
              <el-form class="configForm">
                <el-form-item label="Execution Method: ">
                  <div v-if="boxDetail.tests.in_order == false">Parallel execution</div>
                  <div v-else-if="boxDetail.tests.in_order == true">Serial execution</div>
                </el-form-item>
                <el-form-item label="Tests: ">
                  <div v-if="boxDetail.tests.in_order" style="display: inline-block">
                    <div style="display: inline-block; margin-bottom: 5px" v-for="(item, index) in boxDetail.tests.tests" :key="index" >
                      <el-tag style="margin-left: 7px">{{item}} </el-tag> ->
                    </div>
                  </div>
                  <div v-if="!boxDetail.tests.in_order" style="display: inline-block; ">
                    <div v-for="(item, index) in boxDetail.tests.tests" :key="index" style="margin-bottom: 5px">
                      -><el-tag style="margin-left: 7px">{{item}} </el-tag>
                    </div>
                  </div>
                </el-form-item>
              </el-form>
            </el-card>
          </el-col>

          <el-col :span="6">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span style="font-size: 1.2rem">Rules: </span>
              </div>
              <el-form class="configForm">
                <el-form-item label="Rule Type">{{boxDetail.rules[0].type}}</el-form-item>
                <el-form-item  v-if="boxDetail.rules[0].value != ''" label="Value">{{boxDetail.rules[0].value}}</el-form-item>
              </el-form>
            </el-card>
          </el-col>

          <el-col :span="6">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span style="font-size: 1.2rem">Misc Config: </span>
              </div>
              <el-form class="configForm">
                <el-form-item label="Slack Channel: ">{{boxDetail.config.slack}}</el-form-item>
                <el-form-item label="Type: "> {{boxDetail.config.type}}</el-form-item>
                <el-form-item label="Destroy Cat When Stop Experiment:">{{boxDetail.config.destroy_tidb_cluster}}</el-form-item>
                <el-form-item label="Timeout: ">{{(boxDetail.config.timeout)/(Math.pow(10, 9)*3600)}} hours</el-form-item>
              </el-form>
            </el-card>
          </el-col>
        </el-row>
      </el-header>

      <!-- Divider -->
      <div style="margin-bottom: 0px; margin-left: 25px; margin-top: 10px">
        <Divider orientation="left" style="font-size:20px;"  height="5px">Experiments Generated in Box: {{boxDetail.name}}</Divider>
      </div>

      <!-- Experiment filter items -->
      <div style="margin-left: 20px; margin-bottom: 10px">
        <div style="font-size: 15px; margin-top: 5px; margin-bottom: 10px">Filter Experiments: </div>
        <el-tag style="display: inline-block;">Status</el-tag>
        <el-checkbox-group style="display: inline-block" v-model="experimentStatusCheckList" @change="handleExperimentStatusCheckListChange">
          <el-checkbox label="Normal"></el-checkbox>
          <el-checkbox label="Abnormal" style="margin-left: 10px"></el-checkbox>
        </el-checkbox-group>
        <el-tag style="display: inline-block; margin-left: 30px ">Stage</el-tag>
          <el-checkbox-group style="display: inline-block" v-model="experimentStageCheckList" @change="handleExperimentStageCheckListChange">
            <el-checkbox label="Pending"></el-checkbox>
            <el-checkbox label="Preparing" style="margin-left: 10px"></el-checkbox>
            <el-checkbox label="Running" style="margin-left: 10px"></el-checkbox>
            <el-checkbox label="Finishing" style="margin-left: 10px"></el-checkbox>
            <el-checkbox label="Finished" style="margin-left: 10px"></el-checkbox>
          </el-checkbox-group>
        <el-tag style="display: inline-block; margin-left: 30px">Resources</el-tag>
        <el-checkbox-group style="display: inline-block" v-model="experimentDestroyedCheckList" @change="handleExperimentDestroyedCheckListChange">
          <el-checkbox label="Released"></el-checkbox>
          <el-checkbox label="Not Released" style="margin-left: 10px"></el-checkbox>
        </el-checkbox-group>
      </div>

      <!-- Experiment list table -->
      <el-main style="padding: 5px; height: 100%">
        <el-table :highlight-current-row="true" v-loading="loading" element-loading-text="Loading" class="experimentTable" :data='filteredExperiment' :header-cell-style="{background: '#ebeef5'}" height="650px">
          <el-table-column label="Experiment#" prop="box_index" header-align="center" align="left"></el-table-column>
          <el-table-column label="Status/Stage" header-align="center" align="center">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 'NORMAL'" type="success">{{(scope.row.stage).charAt(0) + (scope.row.stage).toLowerCase().slice(1)}}</el-tag>
              <el-tag v-else-if="scope.row.status == 'ABNORMAL'" type="danger">{{(scope.row.stage).charAt(0) + (scope.row.stage).toLowerCase().slice(1)}}</el-tag>
            </template>
          </el-table-column>

          <el-table-column header-align="center" align="center" label="Release Resources" >
            <template slot-scope="scope">
              <el-tooltip v-if="scope.row.stage == 'PENDING'" class="item" effect="dark" content="Waiting experiment to run">
                <div>--</div>
              </el-tooltip>
              <el-tooltip v-else-if="scope.row.destroyed == true" content="Resources released">
                <Icon type="ios-color-fill-outline" style="font-size: 30px" color="green"/>
              </el-tooltip>
              <el-tooltip v-else-if="scope.row.destroyed == false" content="Resources not released">
                <Icon type="ios-beaker" style="font-size: 30px" color="#f4c842"/>
              </el-tooltip>
            </template>
          </el-table-column>

          <el-table-column label="Create Time" prop="create_time" header-align="center" align="center"> </el-table-column>
          <el-table-column label="Update Time" prop="update_time" header-align="center" align="center"> </el-table-column>
          <el-table-column label="Cat Version">
            <template slot-scope="scope">
              <el-tooltip transition="none" effect="dark">
                <div slot="content" v-if="scope.row.stage == 'PENDING'">Waiting experiment to run</div>
                <div slot="content" v-else-if="scope.row.run_version == null"> Waiting cat to run </div>
                <div v-if="scope.row.stage == 'PENDING'"> -- </div>
                <div v-else-if="scope.row.run_version == null"> -- </div>
              </el-tooltip>

              <el-tooltip transition="none" placement="top">
                <div slot="content" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">Config version: {{scope.row.config.pd_config.version.type}}-{{scope.row.config.pd_config.version.value}}
                      <br>Run version: {{scope.row.run_version.pd_version.type}}-{{scope.row.run_version.pd_version.value}}</div>
                <el-tag size="mini" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">PD: ({{(scope.row.config.pd_config.version.value).substring(0,7)}})({{(scope.row.run_version.pd_version.value).substring(0,7)}})</el-tag>
              </el-tooltip>

              <el-tooltip transition="none" placement="top">
                <div slot="content" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">Config version: {{scope.row.config.tikv_config.version.type}}-{{scope.row.config.tikv_config.version.value}}
                      <br>Run version: {{scope.row.run_version.tikv_version.type}}-{{scope.row.run_version.tikv_version.value}}</div>
                <el-tag size="mini" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null" style="margin-top: 5px">TiKV: ({{(scope.row.config.tikv_config.version.value).substring(0,7)}})({{(scope.row.run_version.tikv_version.value).substring(0,7)}})</el-tag>
              </el-tooltip>

              <el-tooltip transition="none" placement="top">
                <div slot="content" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">Config version: {{scope.row.config.tidb_config.version.type}}-{{scope.row.config.tidb_config.version.value}}
                      <br>Run version: {{scope.row.run_version.tidb_version.type}}-{{scope.row.run_version.tidb_version.value}}</div>
                <el-tag size="mini" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null" style="margin-top: 5px">TiDB: ({{(scope.row.config.tidb_config.version.value).substring(0,7)}})({{(scope.row.run_version.tidb_version.value).substring(0,7)}})</el-tag>
              </el-tooltip>
            </template>
          </el-table-column>
          <el-table-column label="Operations" header-align="center" align="center">
            <template slot-scope="scope">
              <el-tooltip  effect="dark">
                <div v-if="scope.row.stage == 'PENDING'" slot="content">Cancle experiment</div>
                <div v-else-if="scope.row.status == 'ABNORMAL'" slot="content">Handle abnormal</div>
                <div v-else-if="scope.row.status == 'NORMAL'" slot="content">Stop experiment</div>
                <el-button v-if="scope.row.stage == 'PENDING'" circle @click.native="cancelPendingExperiment(boxDetail.id ,scope.row.id)"><Icon type="ios-close" size="150px"></Icon></el-button>
                <el-button v-else-if="scope.row.status == 'ABNORMAL' && scope.row.destroyed == false" type="danger" circle @click.native='handleStopExperiment(boxDetail.id ,scope.row.id, scope.row.box_index, scope.row.destroy_cat)'><Icon type="ios-pause" size="100px"></Icon></el-button>
                <el-button v-else-if="scope.row.status == 'NORMAL' && scope.row.destroyed == false" type="danger" circle @click.native='handleStopExperiment(boxDetail.id ,scope.row.id, scope.row.box_index, scope.row.destroy_cat)'><Icon type="ios-pause" size="100px"></Icon></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="Redo this experiment with same box config">
                <el-button v-if="scope.row.destroyed == false && scope.row.stage != 'PENDING'" type="warning" circle @click.native="handleExperimentRedoClick(scope.row.id)"><Icon type="md-refresh"></Icon></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="Experiment detail">
                <el-button v-if="scope.row.stage != 'PENDING'" type="primary" circle @click.native="handleExperimentDetailClick(scope.row.id, scope.row.box)"><Icon type="ios-paper-outline"></Icon></el-button>
              </el-tooltip>
              <el-tooltip  effect="dark" content="Abnormal experiment annotation">
                <el-button v-if="scope.row.author" circle @click.native="getAnnotationInfo(scope.row.box, scope.row.id, scope.row.annotation, scope.row.author)"><Icon type="ios-chatbubbles-outline"></Icon></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>

        <div style="text-align: center; margin-top: 15px">
          <el-pagination layout="prev, pager, next" :page-size="pageSize" :total="total_experiment_count" @current-change="handleCurrentChange"></el-pagination>
        </div>
      </el-main>
    </el-container>

    <!-- Update box config dialog -->
    <el-dialog :close-on-click-modal='false' title="Box Form" :visible.sync="updateBoxDialog" top="10vh" :lock-scroll='true'>
      <el-form :model="boxForm" :rules="validationRules" ref="boxForm" label-width="12rem">
        <el-row>
          <el-col>
            <el-form-item label="Box Name:" prop="name">
              <el-input v-model="boxForm.name" placeholder="Enter box name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col>
            <el-form-item label="Creator:" prop="creator">
              <el-input v-model="boxForm.creator" placeholder="Enter creator"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col>
            <el-form-item label="Box Description:" prop="description">
              <el-input v-model="boxForm.description" placeholder="Briefly describe the purpose of this box"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-collapse v-model="activeName" class="catForm">
          <el-collapse-item title='CAT' name='catSection'>
            <el-form-item prop="catSection.choice" required>
              <el-radio-group v-model="boxForm.catSection.choice" @change="handleCatChoiceChange">
                <el-radio label="create">Create new cat</el-radio>
                <el-radio label="select">Pick cat from cat pool</el-radio>
                <el-radio label="without_cat">Without cat</el-radio>
              </el-radio-group>
            </el-form-item>

            <div v-if="boxForm.catSection.choice == 'create' ">
              <el-form-item prop="catSection.labels">
                <template slot="label">Cat Labels:
                  <el-tooltip  effect="dark" content="Enter the node, which cat will be testing">
                    <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                  </el-tooltip>
                 </template>
                <el-input v-model="boxForm.catSection.labels" placeholder="eg: node=general, selector=beijing"></el-input>
              </el-form-item>

              <Divider orientation="left" class="horizontal__divider">PD Configuration</Divider>
              <el-form-item prop="catSection.pd_ver.type" label="Type:" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.pd_ver.type" @change="handlePDVerTypeChange">
                  <el-radio label="branch" value="branch"></el-radio>
                  <el-radio label="hash" value="hash"></el-radio>
                  <el-radio label="tag" value="tag"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.pd_ver.type == 'branch'" prop="catSection.pd_ver.value" label="Branch Value:">
                <el-input v-model="boxForm.catSection.pd_ver.value" placeholder="eg. master or release-1.0"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.pd_ver.type == 'hash'" prop="catSection.pd_ver.value" label="Hash Value:">
                <el-input v-model="boxForm.catSection.pd_ver.value" placeholder="eg. 05b9e47b617b68aeb868bcaec8355961a181581d"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.pd_ver.type == 'tag'" prop="catSection.pd_ver.value" label="Tag Value:">
                <el-input v-model="boxForm.catSection.pd_ver.value" placeholder="eg. v2.0.6"></el-input>
              </el-form-item>

              <el-form-item label="PD Size:" prop="catSection.pd_size">
                <el-input v-model.number="boxForm.catSection.pd_size" placeholder="int type only"></el-input>
              </el-form-item>

              <el-form-item prop="catSection.pd_ver.platform" label="Compile to Platform: " required>
                <el-select v-model="boxForm.catSection.pd_ver.platform" placeholder="Compile to platform">
                  <el-option label="Centos7" value="centos7"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="Log Level:" prop="catSection.pd_ver.log_level" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.pd_ver.log_level">
                  <el-radio label="info" value="info"></el-radio>
                  <el-radio label="debug" value="debug"></el-radio>
                  <el-radio label="warn" value="warn"></el-radio>
                  <el-radio label="error" value="error"></el-radio>
                  <el-radio label="fatal" value="fatal"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-row>
                <el-form-item label="Config Template Name:">
                  <el-select v-model="boxForm.catSection.pd_ver.config_template_name" placeholder="Select a config">
                    <el-option v-if="pdConfigNames" v-for="(config, index) in pdConfigNames" :key="index" :label="pdConfigNames[index]" :value="pdConfigNames[index]"></el-option>
                  </el-select>
                </el-form-item>
              </el-row>

              <el-form-item label="Machine Resources">
              </el-form-item>

              <div class="machine_resource">
                <span class="colHeader1">CPU</span>
                <span class="colHeader2">Memory</span>
                <span class="colHeader3">Storage Class</span>
              </div>

              <el-form-item label="Requests:" class="three__input">
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.requests.cpu" placeholder="eg. 2" ></el-input>
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.requests.memory" placeholder="eg. 4Gi" ></el-input>
                <el-select v-model="boxForm.catSection.pd_ver.resource_config.requests.storage_class">
                  <el-option v-for="(opt, index) in storageClassOpts" :key="index" :label="storageClassOpts[index]" :value="storageClassOpts[index]"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="Limits:" class="three__input">
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.limits.cpu" placeholder="Enter limit cpu, eg. 4"></el-input>
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.limits.memory" placeholder="Enter limit memory, eg. 8Gi"></el-input>
                <el-select v-model="boxForm.catSection.pd_ver.resource_config.limits.storage_class">
                  <el-option v-for="(opt, index) in storageClassOpts" :key="index" :label="storageClassOpts[index]" :value="storageClassOpts[index]"></el-option>
                </el-select>
              </el-form-item>

              <Divider orientation="left">TiKV Configuration</Divider>
              <el-form-item prop="catSection.tikv_ver.type" label="Type:" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.tikv_ver.type" @change="handleTiKVVerTypeChange">
                  <el-radio label="branch" value="branch"></el-radio>
                  <el-radio label="hash" value="hash"></el-radio>
                  <el-radio label="tag" value="tag"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.tikv_ver.type == 'branch'" prop="catSection.tikv_ver.value" label="Branch Value:">
                <el-input v-model="boxForm.catSection.tikv_ver.value" placeholder="eg. master or release-1.0"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.tikv_ver.type == 'hash'" prop="catSection.tikv_ver.value" label="Hash Value:">
                <el-input v-model="boxForm.catSection.tikv_ver.value" placeholder="eg. 05b9e47b617b68aeb868bcaec8355961a181581d"></el-input>
              </el-form-item>

