<script>
import _ from 'lodash'
import ajax from '../request/index'

export default {
  data() {
    var checkArrayEmpty = ((rule, value, callback) => {
      if(value.length <= 0) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkEmpty = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkName = ((rule, value, callback) => {
      var regx = /^[a-z0-9]([-a-z0-9]*[a-z0-9])?$/
      if(!regx.test(value)) {
        return callback(new Error('Must be letters(lower case) or numbers or \'-\', \'-\' can neither be the first place nor the last place'))
      } else {
        callback()
      }
    })

    var checkString = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      if(Number.isInteger(value)) {
        return callback(new Error('Must be string'))
      } else {
        callback()
      }
    });

    var checkNumber = ((rule, value, callback) => {
      if(value != 0 && !value) {
        return callback(new Error('Cannot be empty'))
      }
      if(isNaN(value)) {
        return callback(new Error('Must be a number'))
      } else {
        callback()
      }
    });
    return {
      markExperimentID: '',
      markBoxID: '',
      existPendingExperiment: false,
      filteredExperiment: [],
      createdTestNames: [],
      activeName: 'cat',
      createdCatNames:[],
      configMaps: [],
      testTemplateList: [],
      catDetail: '',
      updateBoxDialog: false,
      experimentStatusCheckList: [],
      experimentStageCheckList: [],
      experimentDestroyedCheckList: [],
      loading: false,
      currentPage: 1,
      filteredString: '',
      statusString: '',
      stageString: '',
      destroyedString: '',
      pageSize: 10,
      boxID: this.$route.query.boxid,
      total_experiment_count: 0,
      boxDetail: '',
      boxStopped: false,
      showBoxConfig: false,
      experimentCheckList: [],
      experimentStageArray: [],
      experimentDestroyedArray: [],
      tikvConfigNames: [],
      tidbConfigNames: [],
      pdConfigNames: [],
      storageClassOpts: ['sas', 'ssd', 'shared-sas', 'shared-ssd', 'benchbot', 'benchbot-pd'],
      boxForm: {
        name: '',
        creator: '',
        description: '',
        miscConfigSection: {
          slack: '',
          destroy_tidb_cluster: true,
          type: 'general',
          day: 7,
          hour: 0,
          min: 0
        },
        catSection: {
          labels: 'node=general',
          choice: 'create',
          selected_cat:'',
          pd_ver: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            log_level: 'info',
            config_template_name: '',
            resource_config: {
              requests: {
                cpu: "2",
                memory: "4Gi",
                storage_class: 'shared-sas'
              },
              limits: {
                cpu: "4",
                memory: "8Gi",
                storage_class: 'shared-sas'
              }
            }
          },
          tikv_ver: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            compile_option: '',
            log_level: 'info',
            config_template_name: '',
            resource_config: {
              requests: {
                cpu: "4",
                memory: "8Gi",
                storage_class: 'sas'
              },
              limits: {
                cpu: "4",
                memory: "8Gi",
                storage_class: 'sas'
              }
            }
          },
          tidb_ver: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            log_level: 'info',
            config_template_name: '',
            resource_config: {
              requests: {
                cpu: "4",
                memory: "8Gi"
              },
              limits: {
                cpu: "4",
                memory: "8Gi"
              }
            }
          },
          pd_size: 1,
          tidb_size: 1,
          tikv_size: 3,
        },
        testSection: {
          in_order: false,
          testList: [],
          labels: ''
        },

        ruleSection: [{
          type: 'IMMEDIATELY',
          value: ''
        }]
      },
      validationRules: {
        'name': [{required: true, validator: checkName, trigger: 'blur'}],
        'creator': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.selected_cat': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_ver.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_ver.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_ver.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tikv_ver.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tikv_ver.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tikv_ver.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tidb_ver.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tidb_ver.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tidb_ver.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'catSection.tikv_size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'catSection.tidb_size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'testSection.testList':[{required: true, validator: checkArrayEmpty, trigger: 'change'}],
        'miscConfigSection.day': [{validator: checkNumber, trigger: 'blur'}],
        'miscConfigSection.hour': [{validator: checkNumber, trigger: 'blur'}],
        'miscConfigSection.min': [{validator: checkNumber, trigger: 'blur'}],
      },
      annotationInfo: {
        annotation: '',
        author: ''
      },
      makeAnnotation: false,
      annotationRule: {
        'author': [{required: true, validator: checkName, trigger: 'blur'}],
      }
    }
  },

  created() {
    this.fetchBoxDetail(this.boxID)
  },

  mounted() {
    this.getConfigTemplates()
  },

  methods: {
    getConfigTemplates() {
      ajax.getConfigTemplatesByTag('tikv').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.tikvConfigNames.push(c.name)
            })
          }
        }
      })
      ajax.getConfigTemplatesByTag('tidb').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.tidbConfigNames.push(c.name)
            })
          }
        }
      })
      ajax.getConfigTemplatesByTag('pd').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.pdConfigNames.push(c.name)
            })
          }
        }
      })
    },

    handleCurrentChange(current_page) {
      this.currentPage = current_page;
      this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString);
    },

    pollStage(boxId) {
      var timeId = setInterval(() => {
        if(this.stopPolling){
          clearInterval(timeId)
        }
        this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)}
      ,10000)
    },

    // fetch box details
    fetchBoxDetail(boxID) {
      console.log('inside fetch boxdetail')
      ajax.getBoxDetailByID(boxID).then(result => {
        if(result.data.code == 200) {
          this.boxDetail = result.data.data
          console.log('get box detail by id: ', this.boxDetail)
          if(this.boxDetail.cat && this.boxDetail.cat.selected_cat != '') {
            ajax.getCatDetailByName(this.boxDetail.cat.selected_cat).then(result => {
              if(result.data.code == 200) {
                this.catDetail = result.data.data
                console.log('the cat detail is: ', this.catDetail)
              } else {
                this.$notify.error({
                  title: 'ERROR',
                  message: 'Get cat detail By Catname Failed'
                })
              }
            }).catch(resp => {
              this.$notify.error({
                title: 'ERROR',
                message: resp.message
              })
            })
          } else if(this.boxDetail.cat && this.boxDetail.cat.selected_cat == '' ) {
            this.catDetail = this.boxDetail.cat
            console.log('the cat detail is: ', this.catDetail)
          } else {
            this.catDetail = ''
          }

          this.getFilteredExperiment(boxID, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
        } else {
          this.$notify.error({
            title: 'ERROR',
            message: 'Get Box By BoxID Failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'ERROR',
          message: resp.message
        })
      })
    },

    // get experiments with filter conditions and paginations
    getFilteredExperiment(boxID, offset = 0, size = 5, filteredString = '') {
      this.loading = true
      ajax.getFilteredExperimentByBoxID(boxID, offset, size, this.filteredString).then((result) => {
        if(result.data.code == 200) {
          console.log('the result is: ', result.data.data)
          if(result.data.data.list == null) {
            this.filteredExperiment = []
            this.total_experiment_count = result.data.data.total
            this.loading = false
            console.log('this.fileterExperiment: ', this.filteredExperiment)
          } else {
            this.filteredExperiment = result.data.data.list
            this.total_experiment_count = result.data.data.total
            this.loading = false
          }
        } else {
          this.$notify.error({
            title: 'ERROR',
            message: 'Get Filtered Experiment By BoxID Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'ERROR',
          message: resp.message,
          duration: 0
        })
      })
    },

    // fetch created test case
    fetchCreatedTestTemplate() {
      ajax.getTestTemplate(0, 1000, '').then((result) => {

        if(result.data.code == 200) {
          var testList = []
          this.createdTestNames = []
          if(result.data.data.list == null) {
            this.createdTestNames = []
          } else {
            testList = result.data.data.list
            testList.forEach(c => {
              var testCase = c
              if(testCase.status == 'DONE') {
                this.createdTestNames.push(testCase.name)
              }
            })
          }
        }else {
          this.$notify.error({
            title: 'Error',
            message: resp.message,
            duration: 0
          })
        }
        console.log('total case', this.createdTestNames)
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // fetch created cat
    fetchCreatedCatPool() {
      ajax.getAllFilteredCat(0, 10000, '').then((result) => {
        var totalCat = []
        if(result.data.data.list == null) {
          this.createdCatNames = []
        } else {
          totalCat = result.data.data.list
          totalCat.forEach(i => {
          var item = i
          if(item.status == 'NORMAL' && item.stage == 'RUNNING' && item.destroyed == false) {
            this.createdCatNames.push(item.name)
          }
        })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    // fetch config maps
    fetchConfigMaps() {
      ajax.getConfigMaps().then(result => {
        if(result.data.code == 200) {
          this.configMaps = result.data.data
        } else {
          this.$notify.error ({
            title: 'Error',
            message: 'Get config maps failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    // fetch experiment stage for polling
    getExperimentsStages() {
      var res= _.values(this.filteredExperiment)
      res.forEach((item, index) => {
        var i = item
        this.experimentStageArray.push(i.stage)
      })
      if((!this.experimentStageArray.includes('PREPARING')) && (!this.experimentStageArray.includes('PENDING'))) {
        this.stopPolling = true
      } else {
        // this.pollStage(boxId)
      }
    },

    // get each experiment destroyed status
    getExperimentsDestroyed() {
      var res= _.values(this.filteredExperiment)
      res.forEach((item, index) => {
        var i = item
        this.experimentDestroyedArray.push(i.destroyed)
      })
    },

