    //
    handleExperimentRedoClick(experimentId) {
      var warningMsg = 'Continue redoing this experiment with same box configuration'
      this.$confirm(warningMsg, 'warning', {
        confirmButtonText: 'Continue',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        ajax.redoExperimentByID(this.boxDetail.id, experimentId).then((result) => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Redo Experiment: ' +  experimentId + ' Successfully'
            })
            this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
          } else {
            this.$notify.error({
              title: 'Failed',
              message: 'Failed to Redo Experiment: ' +  experimentId
            })
          }

        }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })

    },

    // manual trigger a new experiment
    handleManualTrigger() {
      var warningMsg = ''
      if(this.filteredExperiment == null) {
        this.existPendingExperiment = false
      } else {
        this.filteredExperiment.forEach(exp => {
          var e = exp
          if(e.stage == 'PENDING') {
            this.existPendingExperiment = true
          }
        })
      }

      if(this.existPendingExperiment) {
        warningMsg = warningMsg + 'Exist pending experiment in the queue. Continue triggering a new experiment will cover the pending experiment.'
      } else {
        warningMsg = warningMsg + 'Continue triggering a new experiment immediately'
      }

      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Continue',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        this.existPendingExperiment = false
        ajax.triggerBoxByID(this.boxDetail.id).then((result) =>{
          if(result.data.code !== 200) {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Manual Trigger Box Failed'
            })
            return
          }
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Manual Trigger Box: ' + this.boxDetail.name + ' Successfully'
          })
          this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
        }).catch((resp) => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })

    },

    handleStop() {
      this.$confirm('Continue stopping generating experiments in Box: ' + this.boxDetail.name +'?', 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        this.boxStopped = true
        ajax.stopBoxByID(this.boxDetail.id).then((result) => {
          if(result.data.code !== 200) {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Stop Box'
            })
            return
          }
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Stop Box: ' + this.boxDetail.name + ' Successfully'
          })
          this.fetchBoxDetail(this.boxDetail.id)
          location.reload()
        }).catch((resp) => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

    handleResume() {
      this.$confirm('Continue resuming the box to generate new experiment?', 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        this.boxStopped = false
        ajax.resumeBoxByID(this.boxDetail.id).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Resume Box '+ this.boxDetail.name + ' Successfully'
            })
            // this.refreshPage()
            this.fetchBoxDetail(this.boxDetail.id)
            location.reload()
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Resume Box '+ this.boxDetail.name,
            })
          }
        }).catch(resp => {
          this.$notify.error({
            title: 'Error in resume box by id',
            message: resp.message,
            duration: 0
          })
        })
      }).catch((resp) => {
        this.$notify.error({
          title: 'Resume box Failed',
          message: resp.message,
          duration: 0
        })
        // console.log('click cancel')
        return
      })
    },

    handleDelete() {
      // this.getExperimentsStages()
      this.getExperimentsDestroyed()
      var warningMsg = ''
      if(this.experimentDestroyedArray.includes(false)) {
        warningMsg = warningMsg + 'Box contains RUNNING experiment. Delete box will stop running experiments in this box.'
      }
      this.$confirm(warningMsg + 'Continue deleting Box: '+ this.boxDetail.name + '?', 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        ajax.deleteBoxByID(this.boxDetail.id).then((result) => {
          if(result.data.code !== 200) {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Delete Box'
            })
            return
          }
          this.$notify({
            title: ' Success',
            type: 'success',
            message: 'Delete Box: '+ this.boxDetail.name + ' Successfully'
          })
          let route = this.$router.resolve({path: '/box'})
          location.href = route.href
        }).catch((resp) => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

