    handleStopExperiment(boxID, experimentID, experimentIndex,destroy_tidb_cluster) {
      var warningMsg = ''
      if(destroy_tidb_cluster) {
        console.log('destroy_tidb_cluster is true')
        warningMsg = warningMsg + 'The cat testing in this experiment will be destroyed if you continue stopping experiment #'+ experimentIndex
      } else {
        warningMsg = warningMsg + 'Continue stopping experiment # ' + experimentIndex
      }
      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        console.log('click yes')
        ajax.destroyExperimentByID(boxID, experimentID).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Stop Experiment: ' + experimentIndex + ' Successfully'
            })
            // this.getExperiments(boxID, (this.currentPage - 1) * this.pageSize, this.pageSize)
            this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
            // this.getExperiments(this.boxDetail.id)
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Stop Experiment: ' + experimentIndex,
              // message: resp.message,
              duration: 0
            })
          }
        }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message,
            duration: 0
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

    filterExperimentStatus(value, row) {
      return row.status === value
    },

    filterExperimentStage(value, row) {
      return row.stage === value
    },

    hideConfigCards() {

      this.showBoxConfig = false
      console.log('click hide box config ', this.showBoxConfig)
    },

    showConfigCards() {

      this.showBoxConfig = true
      console.log('click show box config ', this.showBoxConfig)
    },

    filterTestStatus(value, row) {
      return row.status === value
    },

    handlePDVerTypeChange() {
      this.boxForm.catSection.pd_ver.value = ''
    },

    handleTiDBVerTypeChange() {
      this.boxForm.catSection.tidb_ver.value = ''
    },

    handleTiKVVerTypeChange() {
      this.boxForm.catSection.tikv_ver.value = ''
    },

    handleTriggerRuleChange() {
      if(this.boxForm.ruleSection[0].type == 'IMMEDIATELY') {
        this.boxForm.ruleSection[0].value == ''
      }
    },

