    resetForm(formName) {
      if (this.$refs[formName] != null) {
        console.log('inside resetform')
        console.log('this refs:', this.$refs[formName])
        this.$refs[formName].resetFields()
        console.log('this form after reset: ', this.boxForm)
        if(formName == 'annotationInfo') {
          this.clearAnnotationForm()
        }
      }
    },

    cancelPendingExperiment(boxID, experimentID) {
      console.log('click cancelPendingExperiment')
      ajax.cancelPendingExperiment(boxID, experimentID).then(result => {
        if(result.data.code == 200) {
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Cancel Experiment: ' +  experimentID + ' Successfully'
          })
          // this.getExperiments(boxID, (this.currentPage - 1)* this.pageSize, this.pageSize)
          this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
          // this.getExperiments(this.boxDetail.id)
        } else {
          this.$notify.error({
            title: 'Failed',
            message: 'Cancel Experiment: ' +  experimentId + 'Failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.messages
        })
      })
    },

    unique(experiments){
      var n = []
      for(var i = 0;i < experiments.length; i++){
          if(n.indexOf(experiments[i]) == -1) n.push(experiments[i]);
      }
      console.log('n is: ', n)
      return n;
    },

    goBack() {
      console.log('go back clicked')
      this.$router.go(-1)
    },

    handleExperimentCheckListChange(statusString, stageString, destroyedString) {
      this.filteredString = this.statusString + this.stageString + this.destroyedString
      console.log('handle cat checked list change , filteredString is: ', this.filteredString)
      this.getFilteredExperiment(this.boxDetail.id, (this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
    },

    handleExperimentStatusCheckListChange() {
      this.currentPage = 1
      console.log('the current page after changing check list', this.currentPage)
      console.log('checked cat status are: ', this.experimentStatusCheckList)
      this.statusString = ''
      this.experimentStatusCheckList.forEach(i => {
        var item = i.toLowerCase()

        this.statusString = this.statusString + '&status=' + item
      })

      console.log('statusString: ', this.statusString)
      this.handleExperimentCheckListChange(this.statusString, this.stageString, this.destroyedString)
    },

    handleExperimentStageCheckListChange() {
      this.currentPage = 1
      console.log('the current page after changing check list', this.currentPage)
      console.log('checked cat stage are: ', this.experimentStageCheckList)
      this.stageString = ''
      this.experimentStageCheckList.forEach(i => {
        var item = i.toLowerCase()

        this.stageString = this.stageString + '&stage=' + item
      })

      console.log('stageString: ', this.stageString)
      this.handleExperimentCheckListChange(this.stageString, this.stageString, this.destroyedString)
    },

    handleExperimentDestroyedCheckListChange() {
      this.currentPage = 1
      console.log('the current page after changing check list', this.currentPage)
      console.log('checked cat destroyed are: ', this.experimentDestroyedCheckList)
      this.destroyedString = ''
      this.experimentDestroyedCheckList.forEach(i => {
        var item = i
        if(item == 'Released') {
          this.destroyedString = this.destroyedString + '&destroyed=true'
        } else {
          this.destroyedString = this.destroyedString + '&destroyed=false'
        }
      })

      console.log('destroyedString: ', this.destroyedString)
      this.handleExperimentCheckListChange(this.destroyedString, this.stageString, this.destroyedString)
    },

    getAnnotationInfo(boxID, experimentID, annotationMsg, authorMsg) {
      this.makeAnnotation = true
      this.markExperimentID = experimentID
      this.markBoxID = boxID
      this.annotationInfo = {
        annotation: annotationMsg,
        author: authorMsg
      }
    },

    markProcessExperiment(formName) {
      console.log('summbit annotation')
      this.$refs[formName].validate((valid) => {
        if (valid) {
          ajax.markProcessExperiment(this.markBoxID, this.markExperimentID, this.annotationInfo).then(result => {
            if(result.data.code == 200) {
              console.log('submit hhhhhh')
              this.$notify({
                title: "Successful",
                type: 'success',
                message: 'Make Annotation Successfully'
              })
              this.makeAnnotation = false
              this.fetchBoxDetail(this.boxDetail.id)
            } else {
              this.$notify.error({
                title: 'Error',
                message: 'Submit annotation Failed',
                duration: 0
              })
            }
          }).catch(resp => {
            this.$notify.error({
              title: 'Error',
              message: resp.message,
              duration: 0
            })
          })
        } else {
          this.$message.error('Please check the form, some form items are invalid')
          return false;
        }
      })
    }
  }
}
