<template>
<div>
  <!-- Display test template lists -->
  <div class="table-animation">
    <el-row>
      <div style="display: inline-block"><el-button type="primary" style="margin-bottom: 15px" @click="clickCreateTestTemplate"> + Create Test Case</el-button></div>
      <!-- search test case -->
      <!-- <div style="display: inline-block; float: right">
        <el-input v-model="searchTestName" @keyup.enter.native="searchNameFunc" placeholder="Search test case"  ref="search" style="width: 300px">
          <el-button slot="append" icon="el-icon-search" @click="searchNameFunc"></el-button>
        </el-input>
      </div> -->
    </el-row>

    <el-table :highlight-current-row="true" v-loading="loading" element-loading-text="Loading" :fit="true" :data="createdTestTemplateList.list" :header-cell-style="{background: '#ebeef5'}">
      <el-table-column header-align="center" align="left" label="Name">
        <template slot-scope="scope">{{scope.row.name}}</template>
      </el-table-column>
      <el-table-column header-align="center" align="center" v-for="(item, index) in createdTestTemplateList.prop" :key="index" :label="createdTestTemplateList.label[index]" :prop="item"></el-table-column>

      <el-table-column header-align="center" align="center" label="Compile Stage" prop="status"
                    :filters="[{text: 'NEW', value:'NEW'}, {text:'BUILDING', value: 'BUILDING'}, {text: 'ERROR', value: 'ERROR'}, {text: 'DONE', value: 'DONE'}]"
                    :filter-method="filterStatus" filter-placement="bottom-end">
        <template slot-scope="scope">
          <div v-if="scope.row.status === 'ERROR'">
            <el-tooltip placement="top">
              <div slot="content">{{scope.row.error_info}}</div>
              <el-tag type="danger">{{scope.row.status}}</el-tag>
            </el-tooltip>
          </div>
          <div v-else>
            <el-tag type="primary">{{scope.row.status}}</el-tag>
          </div>
        </template>
      </el-table-column>

      <el-table-column header-align="center" align="center" label="Operation">
        <template slot-scope="scope">
          <el-tooltip content="Edit test Case">
            <el-button icon="el-icon-edit" circle @click="clickEditTestTemplate(scope.row.name)"></el-button>
          </el-tooltip>

          <el-tooltip class="item" effect="dark" content="Delete test Case">
            <el-button type="danger" icon="el-icon-delete" circle @click="deleteTestTemplateByName(scope.row.name)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <div style="text-align: center; margin-top: 15px">
      <el-pagination v-if="searchTestName == ''" layout="prev, pager, next" :page-size="pageSize" :current-page="currentPage" :total="total_test_count" @current-change="handleCurrentChange"></el-pagination>
    </div>
  </div>

  <!-- create and update test dialog -->
  <el-dialog :close-on-click-modal='false' title="Test Case" :visible.sync="createTestTemplateDialog" top="10vh" :lock-scroll='true'>
    <el-form :model="testTemplateForm" :rules="rules" ref="testTemplateForm" label-width="11rem" :status-icon='true'	class="demo-form-inline">
      <el-row>
        <el-col :span="12">
          <el-form-item label="Test Name:" prop="name">
            <el-input v-model="testTemplateForm.name" placeholder="Enter test name"></el-input>
          </el-form-item>
        </el-col>

        <el-col :span="12">
          <el-form-item label="Test Creator:" prop="creator">
            <el-input v-model="testTemplateForm.creator" placeholder="Enter test creator"></el-input>
          </el-form-item>
        </el-col>
      </el-row>

      <el-form-item prop="type">
        <template slot="label">Type:
          <el-tooltip class="item" effect="dark" content="Define test case usage">
            <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
          </el-tooltip>
        </template>
        <el-radio-group v-model="testTemplateForm.type">
          <el-radio label="prepare">prepare
            <el-tooltip class="item" effect="dark" content="Backup cluster before cluster testing">
              <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
            </el-tooltip>
          </el-radio>

          <el-radio label="test">test
            <el-tooltip class="item" effect="dark" content="Test cluster stability">
              <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
            </el-tooltip>
          </el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="Description:" prop="desc">
        <el-input v-model="testTemplateForm.desc" placeholder="Briefly descripe the purpose of this test"></el-input>
      </el-form-item>

      <div class="sch-source" style="margin-bottom: 15px; margin-top: 40px">
        <big>
          <!-- <strong> -->
            <span style="font-weight: 500;">Resource Config: </span>
          <!-- </strong> -->
        </big>
      </div>

      <el-form-item label="Machine Resources">
      </el-form-item>

      <div class="machine_resource">
        <span class="colHeader1">CPU</span>
        <span class="colHeader2">Memory</span>
      </div>

      <el-form-item label="Requests:">
        <el-input v-model="testTemplateForm.resource_config.requests.cpu" placeholder="eg. 0.5" style="width: 30%;"></el-input>
        <el-input v-model="testTemplateForm.resource_config.requests.memory" placeholder="eg. 1Gi" style="width: 30%;"></el-input>
      </el-form-item>

      <el-form-item label="Limits:">
        <el-input v-model="testTemplateForm.resource_config.limits.cpu" placeholder="eg. 1" style="width: 30%;"></el-input>
        <el-input v-model="testTemplateForm.resource_config.limits.memory" placeholder="eg. ssd" style="width: 30%;"></el-input>
      </el-form-item>

      <div class="sch-source" style="margin-bottom: 15px; margin-top: 40px">
        <big>
          <!-- <strong> -->
            <span style="font-weight: 500;">Source: </span>
          <!-- </strong> -->
            <span style="font-size: 12px">select an existing binary test file or compile one from git</span>
        </big>
      </div>
      <el-form-item label="Source Type:" prop="source.type">
        <el-radio-group v-model="testTemplateForm.source.type" @change="handleSourceTypeChange">
          <el-radio label="git">git</el-radio>
          <el-radio label="bin">bin</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="Binary Name:" prop="source.binary_name" style="width: 60%">
        <el-input v-model="testTemplateForm.source.binary_name" placeholder="Enter name of the binary test file"></el-input>
      </el-form-item>

      <div v-if="testTemplateForm.source.type == 'git'">
        <el-form-item label="Git Repo:" prop="source.git_repo">
          <el-input v-model="testTemplateForm.source.git_repo" placeholder="Enter git repo"></el-input>
        </el-form-item>

        <el-form-item prop="source.git_value.git_type" label="Git Type:">
          <el-radio-group v-model="testTemplateForm.source.git_value.git_type" @change="hangleGitTypeChange">
            <el-radio label="branch">branch</el-radio>
            <el-radio label="hash">hash</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="Git Value" prop="source.git_value.value">
          <el-input v-if="testTemplateForm.source.git_value.git_type == 'branch'" v-model="testTemplateForm.source.git_value.value" placeholder="eg. master or release-1.0"></el-input>
          <el-input v-else-if="testTemplateForm.source.git_value.git_type == 'hash'" v-model="testTemplateForm.source.git_value.value" placeholder="eg.05b9e47b617b68aeb868bcaec8355961a181581d"></el-input>
         </el-form-item>
      </div>


      <div v-else>
        <el-form-item label="Binary URL:" prop="source.url">
          <el-input v-model="testTemplateForm.source.url" placeholder="Enter url to download binary test file"></el-input>
        </el-form-item>
      </div>

      <el-form-item label="Docker Image" prop="source.image">
        <el-input v-model="testTemplateForm.source.image" placeholder="Enter docker execution image"></el-input>
      </el-form-item>

      <el-form-item label="Args:" prop="args">
        <el-input v-model="testTemplateForm.args" placeholder="Enter execution arguments"></el-input>
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="createTestTemplateDialog = false">Cancel</el-button>
      <el-button @click="resetForm('testTemplateForm')">Reset</el-button>
      <el-button v-if="updateTestTemplateDialog == true" type="primary" @click="submitEditForm('testTemplateForm')">Save</el-button>
      <el-button v-else type="primary" @click="submitNewForm('testTemplateForm')">Create</el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import ajax from '../request/index'
import _ from 'lodash'

export default {
  data() {
    var checkEmpty = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkString = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(regx.test(value)) {
        return callback(new Error('Must be string'))
      } else {
        callback()
      }
    })

    var checkNumber = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(!regx.test(value)) {
        return callback(new Error('Must be a number'))
      } else {
        callback()
      }
    })

    let activeTab = 'createtesttemplate';
    location.hash && (activeTab = location.hash.slice(2));
    console.log('active Tab', activeTab)

    return {
      loading: false,
      activeTab,
      allTest: [],
      allTestNames: [],
      pageSize: 10,
      searchTestName: '',
      currentPage: 1,
      total_test_count: 0,
      stopPolling: false,
      testStatusArray: [],
      createdTestTemplateDetail: '',
      createTestTemplateDialog: false,
      updateTestTemplateDialog: false,
      confirmDeleteTestTemplate: false,
      filteredString: '',
      createdTestTemplateList: {
        label: ['Source Type','Create Time', 'Update Time'],
        prop: ['source.type', 'create_time', 'update_time'],
        list: []
      },
      testTemplateForm: {
        name: '',
        creator: '',
        desc: '',
        args: '',
        type: 'test',
        resource_config: {
          requests: {
            cpu: '0.5',
            memory: '1Gi'
          },
          limits: {
            cpu: '1',
            memory: '2Gi'
          }

        },
        source: {
          binary_name: '',
          image: '',
          type: 'git',
          url: '',
          git_value: {
            git_type: 'branch',
            value: 'master'
          },
          git_repo: ''
        }
      },
      rules: {
        name: [{required: true, validator: checkString, trigger: 'blur'}],
        'source.binary_name': [{required: true, validator: checkString, trigger: 'blur'}],
        'source.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'source.url': [{required: true, validator: checkString, trigger: 'blur'}],
        'source.git_value.git_type': [{required: true, validator: checkString, trigger: 'blur'}],
        'source.git_value.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'source.git_repo': [{required: true, validator: checkString, trigger: 'blur'}]
      }
    }
  },

  created() {
    this.loading = true
    this.fetchAllTest()
    this.fetchTestTemplates()
    this.pollStatus()
  },

  //  mounted() {
  //   this.$refs.search.focus()
  // },

  methods: {
    fetchAllTest() {
      ajax.getTestTemplate(0, 1000, '').then(result => {
        if(result.data.code == 200) {
          this.allTest = result.data.data.list
          console.log('all test: ', this.allTest)
          this.allTest.forEach(t => {
            var test =t
            this.allTestNames.push(test.name)
          })
          console.log('all allTestNames are: ', this.allTestNames)
        } else {
          this.$notify.error({
            title: 'ERROR',
            message: 'Fetch all test template Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'ERROR',
          message: resp.message,
          duration: 0
        })
      })
    },

    tabClicked(tab) {
      console.log('get part after #', location.hash.slice(2))
      console.log('hello from tab clicked', tab)
      console.log('hello form this.activetab: ', this.activeTab)
      this.$router.push({path: this.activeTab})
    },
    handleCurrentChange(current_page) {
      console.log('current_page: ',current_page)
      this.currentPage = current_page;
      this.fetchTestTemplates((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString);
    },

    pollStatus() {
      console.log('polling status')
      var timeId = setInterval(() => {
        if(this.stopPolling){
          clearInterval(timeId)
          console.log('stop polling status')
        }
       this.fetchTestTemplates(0,10,this.filteredString)}
      ,30000)
    },

    fetchTestTemplates(offset = 0, size =10, filteredString) {
      this.testStatusArray = []
      console.log('inside fetching test tempaltes')
      this.loading = true
      ajax.getTestTemplate(offset, size, this.filteredString).then((result) => {
        if(result.data.data.list == null) {
          this.createdTestTemplateList.list = []
          return
        } else {
          this.createdTestTemplateList.list = result.data.data.list
          this.total_test_count = result.data.data.total
          console.log('created test template lists: ',this.createdTestTemplateList.list)
          console.log('total test: ', this.total_test_count)
          this.loading = false
          var res= _.values(this.createdTestTemplateList.list)
          res.forEach((item, index) => {
            var i = item
            this.testStatusArray.push(i.status)
          })
          console.log('this test Statsus array: ', this.testStatusArray)
          if((!this.testStatusArray.includes('NEW')) && (!this.testStatusArray.includes('BUILDING'))) {
            this.stopPolling = true
          }
        }
      })

    },

    clickCreateTestTemplate() {
      console.log('click create new test template')
      this.resetForm('testTemplateForm')
      this.clearTestCaseForm()
      this.updateTestTemplateDialog = false
      this.createTestTemplateDialog = true
    },

    clickEditTestTemplate(testTemplateName) {
      this.resetForm('testTemplateForm')
      this.clearTestCaseForm()
      this.createTestTemplateDialog = true
      this.updateTestTemplateDialog = true
      ajax.getTestTemplateDetailByName(testTemplateName).then(result => {
        if(result.data.code == 200) {
          this.createdTestTemplateDetail = result.data.data
          console.log('created Test template: ' , this.createdTestTemplateDetail)
          if (this.createdTestTemplateDetail.resource_config.requests == null) {
            this.createdTestTemplateDetail.resource_config = {
              requests: {
                cpu: '0.5',
                memory: '1Gi'
              },
              limits: {
                cpu: '1',
                memory: '2Gi'
              }
            }
          }
          this.testTemplateForm = {
            name: this.createdTestTemplateDetail.name,
            creator: this.createdTestTemplateDetail.creator,
            desc: this.createdTestTemplateDetail.desc,
            args: this.createdTestTemplateDetail.args,
            type: this.createdTestTemplateDetail.type,
            resource_config: {
              requests: {
                cpu: this.createdTestTemplateDetail.resource_config.requests.cpu,
                memory: this.createdTestTemplateDetail.resource_config.requests.memory
              },
              limits: {
                cpu: this.createdTestTemplateDetail.resource_config.limits.cpu,
                memory: this.createdTestTemplateDetail.resource_config.limits.memory
              }
            },
            source: {
              binary_name: this.createdTestTemplateDetail.source.binary_name,
              type: this.createdTestTemplateDetail.source.type,
              git_repo: this.createdTestTemplateDetail.source.git_repo,
              git_value: {
                git_type: this.createdTestTemplateDetail.source.git_value.git_type,
                value: this.createdTestTemplateDetail.source.git_value.value,
              },
              url: this.createdTestTemplateDetail.source.url,
              image: this.createdTestTemplateDetail.source.image
            }
          }
          console.log('get the clicked test template value ', this.testTemplateForm)
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get test template detail by name Failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })

    },

    submitNewForm(formName) {
      console.log('submit new form')
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log('form is valid, the form is :', this.testTemplateForm)
          ajax.createTestTemplate(this.testTemplateForm).then((result) => {
            if (result.data.code != 200) {
              console.log('result.data.code', result.data.code)
              this.$notify.error({
                title: "ERROR",
                message: result.data.message,
                duration: 0
              });
              return
            }
            this.createTestTemplateDialog = false
            this.updateTestTemplateDialog = false
            this.createdTestTemplateList.list.unshift(this.testTemplateForm)
            this.$notify({
              title: "SUCCESS",
              type: 'success',
              message: 'Create Case Template Success!'
            });
            this.fetchTestTemplates(0,10,this.filteredString)
            this.pollStatus()
          }).catch((resp) => {
            this.$notify({
              title: "ERROR 2",
              type: 'error',
              message: resp.message,
              duration: 0
            });
          })
        } else {
          this.$message.error('Please check the form, some form items are invalid')
          this.activeName = ['miscConfigSection', 'catSection', 'testSection', 'ruleSection']
          return false;
        }
      })
    },

    submitEditForm (formName) {
      console.log('submit update form, this.createdTestTemplateDetail.name: ', this.createdTestTemplateDetail.name)
      // debugger
      console.log('inside submit edit form: ', this.testTemplateForm)
      this.$refs[formName].validate((valid) => {
        // debugger
        if (valid) {
          ajax.updateTestTemplate(this.createdTestTemplateDetail.name, this.testTemplateForm).then(result => {

            if(result.data.code == 200) {
              this.$notify({
                title: "SUCCESS",
                type: 'success',
                message: 'Update Test Template Successfully!'
              })
              this.fetchTestTemplates(0,10,this.filteredString)
              this.pollStatus()
              this.updateTestTemplateDialog = false
              this.createTestTemplateDialog = false
            } else {
              this.$notify.error({
                title: "ERROR",
                message: 'Update Test Template Failed!'
              })
            }
          }).catch (resp => {
            this.$notify.error({
              title: "ERROR",
              message: resp.message
            })
          })
        } else {
          this.$message.error('Please check the form, some form items are invalid')
          this.activeName = ['miscConfigSection', 'catSection', 'testSection', 'ruleSection']
          return false;
        }
      })
    },

    deleteTestTemplateByName(testName) {
      this.$confirm('Continue to Delete Test: '+ testName, 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        console.log('click delete test, test name is ', testName)
        ajax.deleteTestTemplateByName(testName).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Delete Test: ' + testName + ' Success'
            })
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Delete Test: ' + testName + ' Failed',
              duration: 0
            })
          }
          this.fetchTestTemplates(0,10,this.filteredString)
        }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message,
            duration: 0
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

    clearTestCaseForm() {
      this.testTemplateForm = {
        name: '',
        creator: '',
        desc: '',
        args: '',
        type: 'test',
        resource_config: {
          requests: {
            cpu: '0.5',
            memory: '1Gi',
          },
          limits: {
            cpu: '1',
            memory: '2Gi',
          }
        },
        source: {
          binary_name: '',
          image: '',
          type: 'git',
          url: '',
          git_value: {
            git_type: 'branch',
            value: 'master'
          },
          git_repo: 'git@github.com:pingcap/schrodinger-test.git'
        }
      }
    },

    resetForm(formName) {
      if (this.$refs[formName] != null) {
        this.$refs[formName].resetFields()
      }
    },

    hangleGitTypeChange(){
      console.log('handle git type change. git type is', this.testTemplateForm.source.git_value.git_type)
      this.testTemplateForm.source.git_value.value = ''
    },

    handleSourceTypeChange() {
      if(this.testTemplateForm.source.type == 'git') {
        console.log('type is git')
        this.testTemplateForm.source.url = ''
        this.testTemplateForm.source.git_value.git_type = 'branch'
        this.testTemplateForm.source.git_value.value = 'master'
        console.log('this.testTemplateForm.source.url and type is ', this.testTemplateForm.source.url, this.testTemplateForm.source.type)
      } else {
        console.log('this.testTemplateForm.source.type is ', this.testTemplateForm.source.type)
        this.testTemplateForm.source.git_repo = ''
        this.testTemplateForm.source.url = ''
        this.testTemplateForm.source.git_value.git_type = ''
        this.testTemplateForm.source.git_value.value = ''
        console.log('this.testTemplateForm.source.url ', this.testTemplateForm.source.url)
      }
      console.log('handle suorce type change')
    },

    filterStatus(value, row) {
      return row.status === value
    },

    handleTypeChange() {
      this.testTemplateForm.type = ''
    },

    searchNameFunc() {
      if(this.searchTestName == '') {
        console.log('name is empty')
        this.fetchTestTemplates(0,10,this.filteredString)
      } else {
        console.log('hello from enter key')
        var returnTests = []
        this.allTestNames.forEach((n, index) => {
          var name = n
          if(name.toLowerCase().includes(this.searchTestName.toLowerCase())) {
            console.log('cat is', index, this.allTest[index])
            returnTests.push(this.allTest[index])
          }
        })
        this.createdTestTemplateList.list = []
        this.createdTestTemplateList.list = returnTests
        // this.total_test_count = this.createdTestTemplateList.list.length
      }
    },
  },

  // computed: {
  //   filterTest() {
  //     if(this.searchCatName == '') {
  //       this.total_cat_count = this.total
  //       return this.filteredCat
  //     } else {
  //       this.total_cat_count = (this.allCats.filter(cat => { return cat.name.toLowerCase().includes(this.searchCatName.toLowerCase())})).length
  //       return this.allCats.filter(cat => {
  //         return cat.name.toLowerCase().includes(this.searchCatName.toLowerCase())
  //       })
  //     }
  //   }
  // }
}
</script>
<style>
  .el-form-item {
    margin-bottom: 20px;
  }
  .el-table__column-filter-trigger i {
    color: black;
    font-size: 20px;
    -webkit-transform: scale(.75);
    transform: scale(.75);
  }

  .test_card .el-card__body {
    padding: 0px;
  }

  .card__header {
    float: right;
    position: relative;
    left: -50%; /* or right 50% */
    text-align: left;
    font-size: 18px;
    font-weight: 500;
    margin-top: 10px;
  }

  .test_card .el-card__header {
    padding: 10px 10px
  }
</style>




