<script>
import _ from 'lodash'
import ajax from '../request/index'
import index from '../request/index';
import VueJsonPretty from 'vue-json-pretty'

export default {
  components: {
    VueJsonPretty
  },

  data() {
    var checkEmpty = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkString = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(regx.test(value)) {
        return callback(new Error('Must be string'))
      } else {
        callback()
      }
    })

    var checkName = ((rule, value, callback) => {
      var regx = /^[a-z0-9]([-a-z0-9]*[a-z0-9])?$/
      if(!regx.test(value)) {
        return callback(new Error('Must be letters(lower case) or numbers or \'-\', \'-\' can neither be the first place nor the last place'))
      } else {
        callback()
      }
    })

    var checkNumber = ((rule, value, callback) => {
      if(value != 0 && !value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(!regx.test(value)) {
        return callback(new Error('Must be a number'))
      } else {
        callback()
      }
    })

    var checkUnique = ((rule, value, callback) => {
      let isExist = this.createdCatNames.includes(value)
      if(isExist) {
        return callback(new Error('The Name already exists, please try another one'))
      } else {
        callback()
      }
    })

    return {
      total: 0,
      allCats: [],
      allCatsNames: [],
      pageSize: 10,
      catDetail: '',
      configMaps: [],
      currentPage: 1,
      loading: false,
      filteredCat: [],
      stageString: [],
      catCheckList: [],
      statusString: [],
      searchCatName: '',
      filteredString:'',
      searchResultCats: [],
      total_cat_count: 0,
      destroyedString: [],
      createdCatNames: [],
      catStageCheckList: [],
      catStatusCheckList: [],
      catDetailDialog: false,
      catDestroyedCheckList: [],
      createCatFormDialog: false,
      updateCatFormDialog: false,
      tikvConfigNames: [],
      tidbConfigNames: [],
      pdConfigNames: [],
      // configLists: ['', '', ''],
      configTempTags: ['tidb', 'tikv', 'pd'],
      storageClassOpts: ['sas', 'ssd', 'shared-sas', 'shared-ssd', 'benchbot', 'benchbot-pd'],
      catForm: {
        name: '',
        creator: '',
        labels: 'node=general',
        pd_config: {
          version: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
          },
          config_template_name: '',
          size: 1,
          log_level: 'info',
          resource_config: {
            requests: {
              cpu: "2",
              memory: '4Gi',
              storage_class: 'shared-sas'
            },
            limits: {
              cpu: "4",
              memory: "8Gi",
              storage_class: 'shared-sas'
            }
          }
        },
        tikv_config: {
          version: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            compile_option: '',
          },
          config_template_name: '',
          size: 3,
          log_level: 'info',
          resource_config: {
            requests: {
              cpu: '4',
              memory: '8Gi',
              storage_class: 'sas'
            },
            limits: {
              cpu: "4",
              memory: "8Gi",
              storage_class: 'sas'
            }
          }
        },
        tidb_config: {
          version: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
          },
          config_template_name: '',
          size: 1,
          log_level: 'info',
          resource_config: {
            requests: {
              cpu: '4',
              memory: '8Gi'
            },
            limits: {
              cpu: "4",
              memory: "8Gi"
            }
          }
        },
      },
      validationRules: {
        'name': [{required: true, validator: checkName, trigger: 'blur'}, {validator: checkUnique, trigger: 'blur'}],
        'creator': [{required: true, validator: checkEmpty, trigger: 'blur'}],
        'pd_config.version.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'pd_config.version.alue': [{required: true, validator: checkString, trigger: 'blur'}],
        'pd_config.log_level': [{required: true, validator: checkString, trigger: 'blur'}],
        'pd_config.version.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'tikv_config.version.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'tikv_config.version.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'tikv_config.vrsion.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'tikv_config.log_level': [{required: true, validator: checkString, trigger: 'blur'}],
        'tidb_config.version.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'tidb_config.version.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'tidb_config.version.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'tidb_config.log_level': [{required: true, validator: checkString, trigger: 'blur'}],
        'pd_config.size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'tikv_config.size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'tidb_config.size': [{required: true, validator: checkNumber, trigger:'blur'}],
      }
    }
  },

  created() {
    this.fetchFilteredCat()
    // this.fetchAllCats()
  },

  mounted() {
    this.getConfigTemplates()
  },

  updated() {
  },

  methods: {
    getConfigTemplates() {
      ajax.getConfigTemplatesByTag('tikv').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.tikvConfigNames.push(c.name)
            })
          }
        }
      })
      ajax.getConfigTemplatesByTag('tidb').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.tidbConfigNames.push(c.name)
            })
          }
        }
      })
      ajax.getConfigTemplatesByTag('pd').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.pdConfigNames.push(c.name)
            })
          }
        }
      })
    },

    fetchAllCats() {
      ajax.getAllFilteredCat(0, 700, '').then(result => {
        this.allCats = result.data.data.list

        this.allCats.forEach(c => {
          var cat = c
          this.allCatsNames.push(cat.name)
        })
      })
    },

    // fetch cat again when current page changed
    handleCurrentChange(current_page) {
      this.currentPage = current_page;
      this.fetchFilteredCat((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString);
    },

    // fetch cat when the status of all cats contain NORMAL
    pollStatus() {
      var timeId = setInterval(() => {
        // if(this.stopPolling){
        //   clearInterval(timeId)
        //   console.log('stop polling status')
        // }
        this.fetchCreatedCat()}
      ,10000)
    },

    // fetch config maps
    fetchConfigMaps() {
      ajax.getConfigMaps().then(result => {
        if(result.data.code == 200) {
          this.configMaps = result.data.data
        } else {
          this.$notify.error ({
            title: 'Error',
            message: 'Get Config Maps Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // fetch cat with fiters and pagination
    fetchFilteredCat(offset = 0, size = 10, filteredString) {
      this.filteredCat = []
      this.loading =true
      ajax.getAllFilteredCat(offset, size, this.filteredString).then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list == null) {
            this.filteredCat = []
            this.total = result.data.data.total
            this.total_cat_count = this.total
            this.loading = false
          } else {
            this.filteredCat = result.data.data.list
            this.total = result.data.data.total
            this.total_cat_count = this.total
            this.loading = false

            console.log('this catdetail: ',this.filteredCat)
          }
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get Filtered Cat Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    // callback when any filter type of cat being changed
    handleCatCheckListChange(statusString, stageString, destroyedString) {
      this.filteredString = this.statusString + this.stageString + this.destroyedString
      this.fetchFilteredCat((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
      // this.fetchAllCats()
    },

    // handle filter of cat status changed
    handleCatStatusCheckListChange() {
      var filterResultCats = this.searchResultCats
      if(this.searchCatName != '') {
        this.searchResultCats.forEach((c,index) => {
          var cat = c
          if(this.catStatusCheckList.indexOf(cat.status.charAt(0) + cat.status.toLowerCase().slice(1)) > -1) {
            console.log('in array', this.catStatusCheckList)
          } else {
            filterResultCats.splice(index, 1);
          }
        })
        this.filteredCat = filterResultCats
      } else {
        this.currentPage = 1
        this.statusString = ''
        this.catStatusCheckList.forEach(i => {
          var item = i.toLowerCase()
          this.statusString = this.statusString + '&status=' + item
        })
        this.handleCatCheckListChange(this.statusString, this.stageString, this.destroyedString)
      }

    },

    // handle filter of cat stage changed
    handleCatStageCheckListChange() {
      this.currentPage = 1
      this.stageString = ''
      this.catStageCheckList.forEach(i => {
        var item = i.toLowerCase()
        this.stageString = this.stageString + '&stage=' + item
      })
      this.handleCatCheckListChange(this.statusString, this.stageString, this.destroyedString)
    },

    // handle filter of cat destroyed changed
    handleCatDestroyedCheckListChange() {
      this.currentPage = 1
      this.destroyedString = ''
      this.catDestroyedCheckList.forEach(i => {
        var item = i
        if(item == 'Released') {
          this.destroyedString = this.destroyedString + '&destroyed=true'
        } else {
          this.destroyedString = this.destroyedString + '&destroyed=false'
        }
      })
      this.handleCatCheckListChange(this.statusString, this.stageString, this.destroyedString)
    },

    // get all created cat names for cat name validation checking
    getCreatedCatNames() {
      this.createdCatNames = []
      const res = _.values(this.filteredCat)
      res.forEach(item => {
        var i = item
        this.createdCatNames.push(i.name)
      })
    },

    // handle create new cat
    clickCreateCat() {
      this.clearCatForm()
      this.updateCatFormDialog = false
      this.createCatFormDialog = true
      this.fetchConfigMaps()

    },

    // handle submit new form
    submitNewForm(formName) {
      console.log('this catform is: ', this.catForm)
      this.$refs[formName].validate((valid) => {
        if(valid) {
          ajax.createCat(this.catForm).then(result => {
            if(result.data.code == 200) {
              this.createCatFormDialog = false
              this.currentPage = 1
              this.fetchFilteredCat((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
              // this.fetchAllCats()
              this.$notify({
                title: "SUCCESS",
                type: 'success',
                message: 'Create New Cat Successfully!'
              });
            } else {
              this.$notify.error({
                title: 'ERROR',
                message: 'Submit New Cat Failed'
              })
            }
          }).catch((resp) => {
            this.$notify.error({
              title: 'Error',
              message: resp.message,
              duration: 0
            })
          })
        } else {
          this.$message.error('Please check the form, some form items are invalid')
          return false;
        }
      })
    },

    // handle upgrade cat
    clickUpdateCat(catID, owner) {
      var warningMsg = ''
      if(owner != '') {
        warningMsg = warningMsg + 'This cat is testing in experiment ' + owner +'. Continue updating?'
      } else {
        warningMsg = warningMsg + 'Continue updating this cat?'
      }
      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        this.clearCatForm()
        this.updateCatFormDialog = true
        this.createCatFormDialog = true
        this.fetchConfigMaps()
        let tempTiKVRC = ''
        let tempPDRC = ''
        let tempTiDBRC = ''
        ajax.getCatDetailByID(catID).then(result =>{
          if(result.data.code == 200) {
            console.log('the catdetail: ', result.data.data)
            this.catDetail = result.data.data
            if(!this.catDetail.config.pd_config.resource_config) {
              tempTiKVRC = {
                requests: {
                  cpu: "4",
                  memory: "8Gi",
                  storage_class: 'sas'
                },
                limits: {
                  cpu: "4",
                  memory: "8Gi",
                  storage_class: 'sas'
                }
              }

              tempPDRC = {
                requests: {
                  cpu: "2",
                  memory: "4Gi",
                  storage_class: 'shared-sas'
                },
                limits: {
                  cpu: "4",
                  memory: "8Gi",
                  storage_class: 'shared-sas'
                }
              }

              tempTiDBRC = {
                requests: {
                  cpu: "4",
                  memory: "8Gi"
                },
                limits: {
                  cpu: "4",
                  memory: "8Gi"
                }
              }
            } else {
              tempTiKVRC = {
                requests: {
                  cpu: this.catDetail.config.tikv_config.resource_config.requests.cpu,
                  memory: this.catDetail.config.tikv_config.resource_config.requests.memory,
                  storage_class: this.catDetail.config.tikv_config.resource_config.requests.storage_class
                },
                limits: {
                  cpu: this.catDetail.config.tikv_config.resource_config.limits.cpu,
                  memory: this.catDetail.config.tikv_config.resource_config.limits.memory,
                  storage_class: this.catDetail.config.tikv_config.resource_config.limits.storage_class
                }
              },
              tempPDRC = {
                requests: {
                  cpu: this.catDetail.config.pd_config.resource_config.requests.cpu,
                  memory: this.catDetail.config.pd_config.resource_config.requests.memory,
                  storage_class: this.catDetail.config.pd_config.resource_config.requests.storage_class
                },
                limits: {
                  cpu: this.catDetail.config.pd_config.resource_config.limits.cpu,
                  memory: this.catDetail.config.pd_config.resource_config.limits.memory,
                  storage_class: this.catDetail.config.pd_config.resource_config.limits.storage_class
                }
              },

              tempTiDBRC = {
                requests: {
                    cpu: this.catDetail.config.tidb_config.resource_config.requests.cpu,
                    memory: this.catDetail.config.tidb_config.resource_config.requests.memory,
                },
                limits: {
                    cpu: this.catDetail.config.tidb_config.resource_config.limits.cpu,
                    memory: this.catDetail.config.tidb_config.resource_config.limits.memory,
                }
              }
            }

            this.catForm = {
              name: this.catDetail.name,
              creator: this.catDetail.creator,
              labels: this.catDetail.labels,
              pd_config: {
                version: {
                  type: this.catDetail.config.pd_config.version.type,
                  value: this.catDetail.config.pd_config.version.value,
                  platform: this.catDetail.config.pd_config.version.platform,
                },
                config_template_name: this.catDetail.config.pd_config.config_template_name,
                size: this.catDetail.config.pd_config.size,
                log_level: this.catDetail.config.pd_config.log_level,
                config_template_name: this.catDetail.config.pd_config.config_template_name,
                resource_config: tempPDRC
              },
              tikv_config: {
                version: {
                  type: this.catDetail.config.tikv_config.version.type,
                  value: this.catDetail.config.tikv_config.version.value,
                  platform: this.catDetail.config.tikv_config.version.platform,
                  compile_option: this.catDetail.config.tikv_config.version.compile_option,
                },
                config_template_name: this.catDetail.config.tikv_config.config_template_name,
                log_level: this.catDetail.config.tikv_config.log_level,
                size: this.catDetail.config.tikv_config.size,
                config_template_name: this.catDetail.config.tikv_config.config_template_name,
                resource_config: tempTiKVRC
              },
              tidb_config: {
                version: {
                  type: this.catDetail.config.tidb_config.version.type,
                  value: this.catDetail.config.tidb_config.version.value,
                  platform: this.catDetail.config.tidb_config.version.platform,
                },
                config_template_name: this.catDetail.config.tidb_config.config_template_name,
                size: this.catDetail.config.tidb_config.size,
                log_level: this.catDetail.config.tidb_config.log_level,
                config_template_name: this.catDetail.config.tidb_config.config_template_name,
                resource_config: tempTiDBRC
              }
            }
            console.log('catForm: ', this.catForm)
            var catName = this.catDetail.name
            var idx = this.createdCatNames.indexOf(catName)
            this.createdCatNames.splice(idx, 1)
          } else {
            this.$notify.error({
              title: 'ERROR',
              message: 'Get cat detail Failed',
              duration: 0
            })
          }
        }).catch(resp => {
          this.$notify.error({
            title: 'Error in clickupdatecat',
            message: resp.message,
            duration: 0
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })

    },

    // handle submit upgrade cat form
    submitUpdateForm(formName) {
      this.$refs[formName].validate((valid) =>{
        if(valid) {
          ajax.upgradeCatByID(this.catDetail.id, this.catForm).then(result =>{
            if(result.data.code == 200) {
              this.$notify({
                title: 'Success',
                type: 'success',
                message: 'Update Cat ' + this.catDetail.name + ' Success'
              })
              this.updateCatFormDialog = false
              this.createCatFormDialog = false
              this.fetchFilteredCat((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
              this.fetchAllCats()
            } else {
              this.$notify.error({
                title: 'Error',
                message: 'Update Cat ' + this.catDetail.name + ' Failed',
                duration: 0
              })
            }
          }).catch(resp => {
            this.$notify.error({
              title: 'Error in submitUpdateForm catch',
              message: resp.message,
              duration: 0
            })
          })
        } else {
          this.$message.error('Please check the form, some form items are invalid')
          return false;
        }
      })
    },

    // Destroy cluster
    destroyCat(catID, owner, destroyed) {
      var warningMsg = ''
      if(owner != '') {
        warningMsg = warningMsg + 'Destroy this cat will affect the experiment, which is testing this cat. Continue destroying the cat'
      } else {
        warningMsg = warningMsg + 'Continue destroying the cat'
      }
      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        ajax.destroyCat(catID).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Destroy Cat ' + catID + ' Successfully',
            })
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Destroy Cat '+ catID + ' Failed',
              duration: 0
            })
          }
          this.fetchFilteredCat((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
          // this.fetchAllCats()
        }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message,
            duration: 0
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

    // jump to cat detail page
    handleCatDetailClick(catID) {
      let route = this.$router.resolve({path: '/catdetail?id='+catID})
      location.href = route.href
    },

    // clear cat form before creating or updating cat
    clearCatForm() {
      this.catForm = {
        name: '',
        creator: '',
        labels: 'node=general',
        pd_config: {
          version: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
          },
          config_template_name: '',
          size: 1,
          log_level: 'info',
          resource_config: {
            requests: {
              cpu: "2",
              memory: '4Gi',
              storage_class: 'shared-sas'
            },
            limits: {
              cpu: "4",
              memory: "8Gi",
              storage_class: 'shared-sas'
            }
          }
        },
        tikv_config: {
          version: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            compile_option: '',
          },
          config_template_name: '',
          size: 3,
          log_level: 'info',
          resource_config: {
            requests: {
              cpu: '4',
              memory: '8Gi',
              storage_class: 'sas'
            },
            limits: {
              cpu: "4",
              memory: "8Gi",
              storage_class: 'sas'
            }
          }
        },
        tidb_config: {
          version: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
          },
          config_template_name: '',
          size: 1,
          log_level: 'info',
          resource_config: {
            requests: {
              cpu: '4',
              memory: '8Gi'
            },
            limits: {
              cpu: "4",
              memory: "8Gi"
            }
          }
        },
      }
    },

    // rest cat form
    resetForm(formName) {
      if (this.$refs[formName] != null) {
        this.$refs[formName].resetFields()
      }
    },

    // set version value to empty when type changed
    handlePDVerTypeChange() {
      this.catForm.pd_config.value = ''
    },

    // set version value to empty when type changed
    handleTiDBVerTypeChange() {
      this.catForm.tidb_config.value = ''
    },

    // set version value to empty when type changed
    handleTiKVVerTypeChange() {
      this.catForm.tikv_config.value = ''
    },

    searchNameFunc() {
      if(this.searchCatName == '') {
        this.fetchFilteredCat()
      } else {
        var returnCats = []
        this.allCatsNames.forEach((n, index) => {
          var name = n
          if(name.toLowerCase().includes(this.searchCatName.toLowerCase())) {
            returnCats.push(this.allCats[index])
          }
        })
        this.filteredCat = []
        this.filteredCat = returnCats
        this.searchResultCats = this.filteredCat
        this.total_cat_count = this.filteredCat.length
      }
    },
  }
}
</script>
<style>
.filterCat .el-form-item__label {
  font-size: 15px;
}

.cat_card .el-card__body {
  padding: 0px;
}

.el-form .Pd_type .el-form-item__content {
  line-height: 48px;
}

.ivu-divider .ivu-divider-horizontal .ivu-divider-with-text-left .ivu-divider-inner-text{
  font-size: 15px;
}
</style>




