<template>
  <div>
    <!-- Create new box button -->
    <el-row>
      <div style="display: inline-block"><el-button type="primary" style="margin-bottom: 15px" @click.native="handlCreateNewBox"> + Create New Box</el-button></div>
      <div style="display: inline-block; float: right">
        <div style="display: inline-block; float: right">
          <el-input v-model="searchBoxName" @keyup.enter.native="searchNameFunc" placeholder="Search box" ref="search" style="width: 300px">
            <el-button slot="append" icon="el-icon-search" @click="searchNameFunc"></el-button>
          </el-input>
        </div>
      </div>
    </el-row>

    <!-- Box list-->
    <div class="animated fadeInUp">
      <masonry :cols="{default: 3, 2200: 4, 1900: 3, 1250: 2, 500: 1}" :gutter="{default: '10px', 700: '10px'}" v-loading="loading" element-loading-text="Loading">
        <div style="margin-bottom: 10px" v-for="(box, index) in allBoxes" :key="index">
          <el-card class="box_card">
            <div slot="header" style="font-size: 15px" class="clearfix">
              <el-col :span="19"><div v-on:click="handleBoxDetailClick(box.id)" style="cursor: pointer; font-weight: 450">{{box.name}}</div></el-col>
              <el-col :span="5">
                <div style="float: right; display: inline-block"  class="animated fadeInRight">
                  <el-tooltip transition="none">
                    <div v-if="box.status == 'ONLINE'" slot="content">Tigger an experiment immediately with same config</div>
                    <el-button v-if="box.status == 'ONLINE'" style="float: right; margin-left: 3px;" size="mini" circle @click.native="handleManualTrigger(box.id, box.name, box.summary.has_pending)"><Icon type="ios-flask" /></el-button>
                  </el-tooltip>
                  <el-tooltip content="Experiment detail" transition="none">
                    <el-button type="primary" style="float: right; margin-left: 3px;" size="mini" circle @click.native="handleBoxDetailClick(box.id)"><Icon type="ios-paper-outline" /></el-button>
                  </el-tooltip>
                </div>
              </el-col>
            </div>

            <el-col :span="11">
              <el-row>
                <div v-if="box.summary.experiments == null"><div style="font-weight: 500; display: inline-block; margin-right: 5px">Recent 0 experiments</div><br> Time-based triggering...</div>
                <div v-else><div style="font-weight: 500; display: inline-block; margin-right: 5px">Recent {{box.summary.experiments.length}} experiments</div> </div>
                <div style="display: inline-block;" v-for="(experiment, index) in box.summary.experiments" :key="index" class="experimentContainer">
                  <el-tooltip transition="none">
                    <div slot="content">
                      <div v-if="experiment.stage == 'PENDING'">Pending</div>
                      <div v-if="experiment.stage != 'PENDING' && experiment.destroyed == true && experiment.status == 'NORMAL'">Normal & Destroyed</div>
                      <div v-else-if="experiment.stage != 'PENDING'"><Icon type="ios-flask"/>{{(experiment.status).charAt(0)+(experiment.status).toLowerCase().slice(1)}}<br/>
                      <Icon type="lo
                      -octocat"/>{{(experiment.cat_status).charAt(0)+(experiment.cat_status).toLowerCase().slice(1)}}</div>
                    </div>
                    <div id="square" style="background: #f4db81" v-if="experiment.stage == 'PENDING'"></div>
                    <div id="square" style="cursor: pointer; background: #d1cfca" v-else-if="experiment.destroyed == true && experiment.status == 'NORMAL'" v-on:click="clickExperimentDetail(box.id, experiment.id)"></div>
                    <div id="square" class="experiment-blik" style="cursor: pointer; background: red" v-else-if="experiment.status == 'ABNORMAL' && experiment.destroyed == false" v-on:click="clickExperimentDetail(box.id, experiment.id)"></div>
                    <div id="square" style="cursor: pointer; background: red" v-else-if="experiment.status == 'ABNORMAL' && experiment.destroyed == true" v-on:click="clickExperimentDetail(box.id, experiment.id)"></div>
                    <div id="square" class="experiment-blik" style="cursor: pointer; background: #77c932" v-else-if="experiment.status == 'NORMAL' && experiment.destroyed == false" v-on:click="clickExperimentDetail(box.id, experiment.id)"></div>
                  </el-tooltip>
                </div>
              </el-row>

              <el-row>
                <el-row v-if="box.summary.test_in_order == true" style="margin-bottom: 5px">
                  <div>
                    <div style="font-weight: 500; display: inline-block">Cases execution method:</div>
                    <el-tooltip transition="none" content="Serial execution">
                      <img src="../../static/serial.svg" style="width: 10px; margin-left: 5px; height: 10px"/>
                    </el-tooltip>
                  </div>
                </el-row>
                <el-row v-if="box.summary.test_in_order == false" style="margin-bottom: 5px">
                  <div>
                    <div style="font-weight: 500; display: inline-block">Cases execution method:</div>
                    <el-tooltip transition="none" content="Parallel execution">
                      <img src="../../static/parallel.svg" style="width: 10px; margin-left: 5px; height: 10px"/>
                    </el-tooltip>
                  </div>
                </el-row>
              </el-row>
            </el-col>

            <el-col :span="13">
              <el-row>
                <div style="font-weight: 500; display: inline-block;">Creator: {{box.creator}}</div>
              </el-row>

              <el-row v-if="showBenchEntry(box)">
                <div style="font-weight: 500; display: inline-block;">Bench Result:
                  <el-button type="primary" style="padding: 3px 3px; margin-left: 5px; margin-left: 5px;" size="mini" @click="benchResult(box.id)">Go ></el-button>
                </div>
              </el-row>

              <el-row v-if="box.summary.experiments != null && !box.summary.experiments[box.summary.experiments.length - 1].author && box.summary.experiments[box.summary.experiments.length - 1].status == 'ABNORMAL' && box.summary.experiments[box.summary.experiments.length - 1].destroyed == false">
                <div style="font-weight: 500; display: inline-block;">Process abnormal experiment: </div>
                <el-button type="primary" style="padding: 3px 3px; margin-left: 5px; margin-left: 5px;" size="mini" @click="processAbnormalExperiment(box.id, box.summary.experiments[box.summary.experiments.length - 1].id)">Go ></el-button>
              </el-row>
              <el-row v-if="box.summary.experiments != null && box.summary.experiments[box.summary.experiments.length - 1].author && box.summary.experiments[box.summary.experiments.length - 1].status == 'ABNORMAL' && box.summary.experiments[box.summary.experiments.length - 1].destroyed == false">
                <div style="font-weight: 500; display: inline-block;">Processing abnormal experiment: </div>
                  <Icon type="ios-chatbubbles" color="#42bc2d" size="100px" style="cursor: pointer; margin-left: 5px;" @click="getAnnotationInfo(box.id, box.summary.experiments[box.summary.experiments.length - 1].id, box.summary.experiments[box.summary.experiments.length - 1].annotation, box.summary.experiments[box.summary.experiments.length - 1].author)"/>
              </el-row>
            </el-col>
          </el-card>
        </div>
      </masonry>
    </div>

    <!-- create box dialog -->
    <el-dialog :close-on-click-modal='false' :visible.sync="createBoxDialog" top="10vh" :lock-scroll='true'>
      <div slot="title" style="font-size: 20px; font-weight: 500">Box Form</div>
      <el-form :model="boxForm" :rules="validationRules" ref="boxForm" label-width="12rem">
        <el-row>
          <el-col>
            <el-form-item label="Box Name:" prop="name">
              <el-input v-model="boxForm.name" placeholder="Enter box name"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col>
            <el-form-item label="Creator:" prop="creator">
              <el-input v-model="boxForm.creator" placeholder="Enter creator"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row>
          <el-col>
            <el-form-item label="Box Description:" prop="description">
              <el-input v-model="boxForm.description" placeholder="Briefly describe the purpose of this box"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-collapse v-model="activeName" class="catForm">
          <el-collapse-item title='CAT' name='catSection'>
            <el-form-item prop="catSection.choice" required>
              <el-radio-group v-model="boxForm.catSection.choice" @change="handleCatChoiceChange">
                <el-radio label="create">Create new cat</el-radio>
                <el-radio label="select">Pick cat from cat pool</el-radio>
                <el-radio label="without_cat">Without Cat</el-radio>
              </el-radio-group>
            </el-form-item>

            <div v-if="boxForm.catSection.choice == 'create' ">
              <el-form-item prop="catSection.labels">
                <template slot="label">Cat Labels:
                  <el-tooltip  effect="dark" content="Enter the node, which cat will be testing. eg: node=general, node=bench">
                    <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                  </el-tooltip>
                 </template>
                <el-input v-model="boxForm.catSection.labels" placeholder="eg: node=general, selector=beijing"></el-input>
              </el-form-item>

              <Divider orientation="left" ><span class="pd_config-divider">PD Configuration</span></Divider>
              <el-form-item prop="catSection.pd_ver.type" label="Type:" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.pd_ver.type" @change="handlePDVerTypeChange">
                  <el-radio label="branch" value="branch"></el-radio>
                  <el-radio label="hash" value="hash"></el-radio>
                  <el-radio label="tag" value="tag"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.pd_ver.type == 'branch'" prop="catSection.pd_ver.value" label="Branch Value:">
                <el-input v-model="boxForm.catSection.pd_ver.value" placeholder="eg. master or release-1.0"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.pd_ver.type == 'hash'" prop="catSection.pd_ver.value" label="Hash Value:">
                <el-input v-model="boxForm.catSection.pd_ver.value" placeholder="eg. 05b9e47b617b68aeb868bcaec8355961a181581d"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.pd_ver.type == 'tag'" prop="catSection.pd_ver.value" label="Tag Value:">
                <el-input v-model="boxForm.catSection.pd_ver.value" placeholder="eg. v2.0.6"></el-input>
              </el-form-item>

              <el-form-item label="PD Size:" prop="catSection.pd_size">
                <el-input v-model.number="boxForm.catSection.pd_size" placeholder="int type only"></el-input>
              </el-form-item>

              <el-form-item prop="catSection.pd_ver.platform" label="Compile to Platform: " required>
                <el-select v-model="boxForm.catSection.pd_ver.platform" placeholder="Compile to platform">
                  <el-option label="Centos7" value="centos7"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="Log Level:" prop="catSection.pd_ver.log_level" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.pd_ver.log_level">
                  <el-radio label="info" value="info"></el-radio>
                  <el-radio label="debug" value="debug"></el-radio>
                  <el-radio label="warn" value="warn"></el-radio>
                  <el-radio label="error" value="error"></el-radio>
                  <el-radio label="fatal" value="fatal"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-row>
                <el-form-item label="Config Template Name:">
                  <el-select v-model="boxForm.catSection.pd_ver.config_template_name" placeholder="Select a config">
                    <el-option v-if="pdConfigNames" v-for="(config, index) in pdConfigNames" :key="index" :label="pdConfigNames[index]" :value="pdConfigNames[index]"></el-option>
                  </el-select>
                </el-form-item>
              </el-row>

              <el-form-item label="Machine Resources">
              </el-form-item>

              <div class="machine_resource">
                <div class="colHeader1">CPU</div>
                <div class="colHeader2">Memory</div>
                <div class="colHeader3">Storage Class</div>
              </div>

              <el-form-item label="Requests:"  class="three__input">
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.requests.cpu" placeholder="eg. 2"></el-input>
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.requests.memory" placeholder="eg. 4Gi"></el-input>
                <el-select v-model="boxForm.catSection.pd_ver.resource_config.requests.storage_class">
                  <el-option v-for="(opt, index) in storageClassOpts" :key="index" :label="storageClassOpts[index]" :value="storageClassOpts[index]"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="Limits:" class="three__input">
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.limits.cpu" placeholder="Enter limit cpu, eg. 4"></el-input>
                <el-input class="resInput" v-model="boxForm.catSection.pd_ver.resource_config.limits.memory" placeholder="Enter limit memory, eg. 8Gi"></el-input>
                <el-select v-model="boxForm.catSection.pd_ver.resource_config.limits.storage_class">
                  <el-option v-for="(opt, index) in storageClassOpts" :key="index" :label="storageClassOpts[index]" :value="storageClassOpts[index]"></el-option>
                </el-select>
              </el-form-item>

              <Divider orientation="left">TiKV Configuration</Divider>
              <el-form-item prop="catSection.pd_ver.type" label="Type:" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.tikv_ver.type" @change="handleTiKVVerTypeChange">
                  <el-radio label="branch" value="branch"></el-radio>
                  <el-radio label="hash" value="hash"></el-radio>
                  <el-radio label="tag" value="tag"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.tikv_ver.type == 'branch'" prop="catSection.tikv_ver.value" label="Branch Value:">
                <el-input v-model="boxForm.catSection.tikv_ver.value" placeholder="eg. master or release-1.0"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.tikv_ver.type == 'hash'" prop="catSection.tikv_ver.value" label="Hash Value:">
                <el-input v-model="boxForm.catSection.tikv_ver.value" placeholder="eg. 05b9e47b617b68aeb868bcaec8355961a181581d"></el-input>
              </el-form-item>

              <el-form-item v-if="boxForm.catSection.tikv_ver.type == 'tag'" prop="catSection.tikv_ver.value" label="Tag Value:">
                <el-input v-model="boxForm.catSection.tikv_ver.value" placeholder="eg. v2.0.6"></el-input>
              </el-form-item>

              <el-form-item label="TiKV Size:" prop="catSection.tikv_size">
                <el-input v-model.number="boxForm.catSection.tikv_size" placeholder="int type only"></el-input>
              </el-form-item>

              <el-form-item prop="catSection.tikv_ver.platform" label="Compile to Platform: " required>
                <el-select v-model="boxForm.catSection.tikv_ver.platform" placeholder="Compile to platform">
                  <el-option label="Centos7" value="centos7"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item prop="catSection.tikv_ver.compile_option" label="Compile Option: ">
                <el-select v-model="boxForm.catSection.tikv_ver.compile_option" placeholder="Choose compile option">
                  <el-option label="Unportable" value=""></el-option>
                  <el-option label="Portable" value="portable"></el-option>
                  <el-option label="Failpoint" value="failpoint"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="Log Level:" prop="catSection.tikv_ver.log_level" class="Pd_type">
                <el-radio-group v-model="boxForm.catSection.tikv_ver.log_level">
                  <el-radio label="info" value="info"></el-radio>
                  <el-radio label="debug" value="debug"></el-radio>
                  <el-radio label="warn" value="warn"></el-radio>
                  <el-radio label="error" value="error"></el-radio>
                  <el-radio label="fatal" value="fatal"></el-radio>
                </el-radio-group>
              </el-form-item>

              <el-row>
                <el-form-item label="Config Template Name:">
                  <el-select v-model="boxForm.catSection.tikv_ver.config_template_name" placeholder="Select a config">
                    <el-option v-if="tikvConfigNames" v-for="(config, index) in tikvConfigNames" :key="index" :label="tikvConfigNames[index]" :value="tikvConfigNames[index]"></el-option>
                  </el-select>
                </el-form-item>
              </el-row>

              <el-form-item label="Machine Resources">
              </el-form-item>

              <div class="machine_resource">
                <span class="colHeader1">CPU</span>
                <span class="colHeader2">Memory</span>
                <span class="colHeader3">Storage Class</span>
              </div>

              <el-form-item label="Requests:" class="three__input">
                <el-input class="resInput" v-model="boxForm.catSection.tikv_ver.resource_config.requests.cpu" placeholder="eg. 2"></el-input>
                <el-input class="resInput" v-model="boxForm.catSection.tikv_ver.resource_config.requests.memory" placeholder="eg. 4Gi"></el-input>
                <el-select v-model="boxForm.catSection.tikv_ver.resource_config.requests.storage_class">
                  <el-option v-for="(opt, index) in storageClassOpts" :key="index" :label="storageClassOpts[index]" :value="storageClassOpts[index]"></el-option>
                </el-select>
              </el-form-item>

              <el-form-item label="Limits:" class="three__input">
                <el-input class="resInput" v-model="boxForm.catSection.tikv_ver.resource_config.limits.cpu" placeholder="Enter limit cpu, eg. 4"></el-input>
                <el-input class="resInput" v-model="boxForm.catSection.tikv_ver.resource_config.limits.memory" placeholder="Enter limit memory, eg. 8Gi"></el-input>
                <el-select v-model="boxForm.catSection.tikv_ver.resource_config.limits.storage_class">
                  <el-option v-for="(opt, index) in storageClassOpts" :key="index" :label="storageClassOpts[index]" :value="storageClassOpts[index]"></el-option>
                </el-select>
              </el-form-item>

              <Divider orientation="left">TiDB Configuration</Divider>
                <el-form-item prop="catSection.tidb_ver.type" label="Type:" class="Pd_type">
                  <el-radio-group v-model="boxForm.catSection.tidb_ver.type" @change="handleTiDBVerTypeChange">
                    <el-radio label="branch" value="branch"></el-radio>
                    <el-radio label="hash" value="hash"></el-radio>
                    <el-radio label="tag" value="tag"></el-radio>
                  </el-radio-group>
                </el-form-item>

                <el-form-item v-if="boxForm.catSection.tidb_ver.type == 'branch'" prop="catSection.tidb_ver.value" label="Branch Value:">
                  <el-input v-model="boxForm.catSection.tidb_ver.value" placeholder="eg. master or release-1.0"></el-input>
                </el-form-item>

                <el-form-item v-if="boxForm.catSection.tidb_ver.type == 'hash'" prop="catSection.tidb_ver.value" label="Hash Value:">
                  <el-input v-model="boxForm.catSection.tidb_ver.value" placeholder="eg. 05b9e47b617b68aeb868bcaec8355961a181581d"></el-input>
                </el-form-item>

                <el-form-item v-if="boxForm.catSection.tidb_ver.type == 'tag'" prop="catSection.tidb_ver.value" label="Tag Value:">
                  <el-input v-model="boxForm.catSection.tidb_ver.value" placeholder="eg. v2.0.6"></el-input>
                </el-form-item>

                <el-form-item label="TiDB Size:" prop="catSection.tidb_size">
                  <el-input v-model.number="boxForm.catSection.tidb_size" placeholder="int type only"></el-input>
                </el-form-item>

                <el-form-item prop="catSection.tidb_ver.platform" label="Compile to Platform: " required>
                  <el-select v-model="boxForm.catSection.tidb_ver.platform" placeholder="Compile to platform">
                    <el-option label="Centos7" value="centos7"></el-option>
                  </el-select>
                </el-form-item>

                <el-form-item label="Log Level:" prop="catSection.tidb_ver.log_level" class="Pd_type">
                  <el-radio-group v-model="boxForm.catSection.tidb_ver.log_level">
                    <el-radio label="info" value="info"></el-radio>
                    <el-radio label="debug" value="debug"></el-radio>
                    <el-radio label="warn" value="warn"></el-radio>
                    <el-radio label="error" value="error"></el-radio>
                    <el-radio label="fatal" value="fatal"></el-radio>
                  </el-radio-group>
                </el-form-item>

                <el-row>
                  <el-form-item label="Config Template Name:">
                    <el-select v-model="boxForm.catSection.tidb_ver.config_template_name" placeholder="Select a config">
                      <el-option v-if="tidbConfigNames" v-for="(config, index) in tidbConfigNames" :key="index" :label="tidbConfigNames[index]" :value="tidbConfigNames[index]"></el-option>
                    </el-select>
                  </el-form-item>
                </el-row>

                <el-form-item label="Machine Resources">
                </el-form-item>

                <div class="machine_resource">
                  <span class="colHeader1">CPU</span>
                  <span class="colHeader2">Memory</span>
                </div>

                <el-form-item label="Requests:">
                  <el-input v-model="boxForm.catSection.tidb_ver.resource_config.requests.cpu" placeholder="Enter request CPU, eg. 2" style="width: 40%;"></el-input>
                  <el-input v-model="boxForm.catSection.tidb_ver.resource_config.requests.memory" placeholder="Enter request memory, eg. 4Gi" style="width: 45%;"></el-input>
                </el-form-item>

                <el-form-item label="Limits:">
                  <el-input v-model="boxForm.catSection.tidb_ver.resource_config.limits.cpu" placeholder="Enter limit cpu, eg. 4" style="width: 40%;"></el-input>
                  <el-input v-model="boxForm.catSection.tidb_ver.resource_config.limits.memory" placeholder="Enter limit memory, eg. 8Gi" style="width: 45%;"></el-input>
                </el-form-item>
            </div>

            <div v-if="boxForm.catSection.choice == 'select'">
              <el-form-item label="Pick Cat" prop="catSection.selected_cat"  class="cat">
                <el-select v-model="boxForm.catSection.selected_cat" placeholder="Please select cat" style="width: 400px;">
                  <el-option v-for="(item, index) in createdCatNames" :key="index" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </div>
            <div v-if="boxForm.catSection.choice == 'without_cat'">
            </div>
          </el-collapse-item>

          <el-collapse-item title="Tests" name="tests">
            <el-form-item prop="testSection.in_order" required>
              <el-radio-group v-model="boxForm.testSection.in_order">
                <el-radio :label=false>Parallel execution</el-radio>
                <el-radio :label=true>Serial execution</el-radio>
              </el-radio-group>
            </el-form-item>
            <div style="position: relative; margin-top: 20px">
              <el-form-item prop="testSection.testList" required>
                <template slot="label">Pick Tests:</template>
                <el-select v-model="boxForm.testSection.testList" multiple placeholder="Please select test" style="width: 400px;">
                  <el-option v-for="(item, index) in createdTestTemplateNames" :key="index" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </div>
             <el-form-item prop="testSection.labels">
                <template slot="label">Test Labels:
                  <el-tooltip  effect="dark" content="Enter the node, which cat will be testing. eg: node=general, node=bench">
                    <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                  </el-tooltip>
                 </template>
                <el-input v-model="boxForm.testSection.labels" placeholder="eg: node=general, selector=beijing"></el-input>
              </el-form-item>
          </el-collapse-item>

          <el-collapse-item title="Trigger Rules" name="ruleSection" class="Pd_type">
            <el-form-item v-for="(rule, index) in boxForm.ruleSection" :key="rule.key" label="Rule Type: " :prop="'ruleSection.'+index+'.type'" required>
              <el-radio-group v-model="boxForm.ruleSection[index].type" @change="handleTriggerRuleChange">
                <el-radio label="IMMEDIATELY">Immediately Job
                  <el-tooltip  effect="dark" content="Trigger an experiment with box config immediately">
                    <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                  </el-tooltip>
                </el-radio>
                <el-radio label="CRONTAB">Crontab Job
                  <el-tooltip  effect="dark" content="Trigger an experiment at customized time">
                    <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                  </el-tooltip>
                </el-radio>
                <el-radio v-if="boxForm.catSection.choice == 'create'" label="GIT">Git Update
                  <el-tooltip  effect="dark" content="Trigger an experiment when tiDB code been updated">
                    <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                  </el-tooltip>
                </el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item v-if="boxForm.ruleSection[index].type == 'CRONTAB'" v-for="(rule, index) in boxForm.ruleSection" :key="rule.key" :prop="'ruleSection.'+index+'.type'" required="" label-width="11rem">
