                <el-tooltip effect="dark">
                  <div slot="content">Format:* * * * * *<br> 1st col: second, <br> 2nd col: minute, <br>3rd col: hour, <br>4th col: date, <br>5th col: month, <br>6th col: year <br><br><br>
                      <a href="https://blog.csdn.net/bsf5521/article/details/76522222">Click me for more details</a></div>
                  <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                </el-tooltip>
              </template>
              <el-input v-model="boxForm.ruleSection[index].value" class="input-with-select" placeholder="eg. 30 30 6 * * * (Means trigger an experiment at 6:30:30AM everyday)"></el-input>
            </el-form-item>
          </el-collapse-item>

          <el-collapse-item title="Misc Config" name="miscConfigSection">
            <el-form-item prop="miscConfigSection.slack" label-width="11rem">
              <template slot="label">Slack Channel:
                <el-tooltip  effect="dark" content="Send experiment reports to Slack channel #">
                  <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                </el-tooltip>
              </template>
              <el-input v-model="boxForm.miscConfigSection.slack" style="width: 400px;"></el-input>
            </el-form-item>

            <el-form-item prop="miscConfigSection.type" label-width="11rem">
              <template slot="label">Type:
                <el-tooltip  effect="dark" content="Type of generated experiment">
                  <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                </el-tooltip>
              </template>
              <el-radio-group v-model="boxForm.miscConfigSection.type">
                <el-radio label="general">General</el-radio>
                <el-radio label="bench">Bench</el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item prop="miscConfigSection.destroy_tidb_cluster" label-width="11rem">
              <template slot="label">Destroy Cat:
                <el-tooltip  effect="dark" content="Destroy TiDB cluster after finishing an experiment">
                  <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                </el-tooltip>
              </template>
              <el-radio-group v-model="boxForm.miscConfigSection.destroy_tidb_cluster">
                <el-radio border :label=true>Yes</el-radio>
                <el-radio border :label=false>No</el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item prop="timeout" label-width="11rem" class="time_out">
              <template slot="label">Time Out:
                <el-tooltip  effect="dark" content="Once timed out, the experiment will be stopped">
                  <Icon type="ios-information-circle" style="margin-left: 5px"></Icon>
                </el-tooltip>
              </template>
              <el-input v-model.number="boxForm.miscConfigSection.day" style="width: 30%">
                <template slot="append">day</template>
              </el-input>
              <el-input v-model.number="boxForm.miscConfigSection.hour" style="width: 30%">
                <template slot="append">hour</template>
