            </el-form-item>
          </el-collapse-item>
        </el-collapse>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="createBoxDialog = false; resetForm('boxForm')">Cancel</el-button>
        <el-button @click="resetForm('boxForm')">Reset</el-button>
        <el-button type="primary" @click="submitNewForm('boxForm')">Create</el-button>
      </div>
    </el-dialog>

    <!-- make an annotation dialog -->
    <el-dialog title="Make an Annotation" :close-on-click-modal='false' :visible.sync="makeAnnotation" top="10vh" :lock-scroll='true'>
