      </div>
    </el-dialog>
  </div>
</template>

<script>
import ajax from '../request/index'

export default {

  data() {
    var checkArrayEmpty = ((rule, value, callback) => {
      if(value.length <= 0) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkEmpty = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkName = ((rule, value, callback) => {
      var regx = /^[a-z0-9]([-a-z0-9]*[a-z0-9])?$/
      if(!regx.test(value)) {
        return callback(new Error('Must be letters(lower case) or numbers or \'-\', \'-\' can neither be the first place nor the last place'))
      } else {
        callback()
      }
    })

    var checkString = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      if(Number.isInteger(value)) {
        return callback(new Error('Must be string'))
      } else {
        callback()
      }
    });

    var checkNumber = ((rule, value, callback) => {
      if(value != 0 && !value) {
        return callback(new Error('Cannot be empty'))
      }
      if(isNaN(value)) {
        return callback(new Error('Must be a number'))
      } else {
        callback()
      }
    });

    var checkLength = ((rule, value, callback) => {
      if((value.length > 60)) {
        return callback(new Error('Please enter less than 60 characters'))
      } else {
        callback()
      }
    })

    return {
      totalBoxes: [],
      searchBoxName: '',
      allBoxesNames: [],
      markExperimentID: '',
      markBoxID: '',
      loading: false,
      activeName: 'cat',
      experimentStageArray: [],
      createBoxDialog: false,
      showBoxConfig: false,
      boxInstanceList: [],
      boxInstanceDetail: {},
      createdCatNames:[],
      createdTestTemplateNames: [],
      showExperimentDetail: false,
      configMaps: [],
      experimentDetail: '',
      testTemplateList: [],
      catDetail: '',
      filteredString: '',
      experimentTable: [],
      total_experiment_count: 0,
      experimentDestroyedArray: [],
      checkName: [],
      allBoxes: [],
      total_box_count: 0,
      without_cat: false,
      tikvConfigNames: [],
      tidbConfigNames: [],
      pdConfigNames: [],
      storageClassOpts: ['sas', 'ssd', 'shared-sas', 'shared-ssd', 'benchbot', 'benchbot-pd'],
      boxForm: {
        name: '',
        creator: '',
        description: '',
        miscConfigSection: {
          slack: '',
          destroy_tidb_cluster: true,
          type: 'general',
          day: 7,
          hour: 0,
          min: 0
        },
        catSection: {
          name:'',
          labels: 'node=general',
          choice: 'create',
          selected_cat:'',
          pd_ver: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            log_level: 'info',
            config_template_name: '',
            resource_config: {
              requests: {
                cpu: "2",
                memory: "4Gi",
                storage_class: 'shared-sas',
              },
              limits: {
                cpu: "4",
                memory: "8Gi",
                storage_class: 'shared-sas',
              }
            }
          },
          tikv_ver: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            compile_option: '',
            log_level: 'info',
            config_template_name: '',
            resource_config: {
              requests: {
                cpu: "4",
                memory: "8Gi",
                storage_class: 'sas',
              },
              limits: {
                cpu: "4",
                memory: "8Gi",
                storage_class: 'sas',
              }
            }
          },
          tidb_ver: {
            type: 'branch',
            value: 'master',
            platform: 'centos7',
            log_level: 'info',
            config_template_name: '',
            resource_config: {
              requests: {
                cpu: "4",
                memory: "8Gi",
              },
              limits: {
                cpu: "4",
                memory: "8Gi",
              }
            }
          },
          pd_size: 1,
          tidb_size: 1,
          tikv_size: 3,
        },
        testSection: {
          in_order: false,
          testList: [],
          labels: ''
        },

        ruleSection: [{
          type: 'IMMEDIATELY',
          value: ''
        }],


      },
      validationRules: {
        'name': [{required: true, validator: checkName, trigger: 'blur'}],
        'creator': [{required: true, validator: checkEmpty, trigger: 'blur'}],
        'catSection.selected_cat': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_ver.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_ver.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_ver.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tikv_ver.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tikv_ver.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tikv_ver.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tidb_ver.type': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tidb_ver.value': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.tidb_ver.platform': [{required: true, validator: checkString, trigger: 'blur'}],
        'catSection.pd_size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'catSection.tikv_size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'catSection.tidb_size': [{required: true, validator: checkNumber, trigger:'blur'}],
        'testSection.testList':[{required: true, validator: checkArrayEmpty, trigger: 'change'}],
        'miscConfigSection.day': [{validator: checkNumber, trigger: 'blur'}],
        'miscConfigSection.hour': [{validator: checkNumber, trigger: 'blur'}],
        'miscConfigSection.min': [{validator: checkNumber, trigger: 'blur'}],
      },
      annotationInfo: {
        annotation: '',
        author: ''
      },
      makeAnnotation: false,
      annotationRule: {
        'author': [{required: true, validator: checkName, trigger: 'blur'}],
      }
    }
  },

  created() {
    this.getAllBoxes()
  },


  mounted() {
    console.log('mounted, ', this.$refs.search)
    this.$refs.search.focus()
    this.getConfigTemplates()
  },

  methods: {
    getConfigTemplates() {
      ajax.getConfigTemplatesByTag('tikv').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.tikvConfigNames.push(c.name)
            })
          }
        }
      })
      ajax.getConfigTemplatesByTag('tidb').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.tidbConfigNames.push(c.name)
            })
          }
        }
      })
      ajax.getConfigTemplatesByTag('pd').then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list) {
            (result.data.data.list).forEach(c => {
              this.pdConfigNames.push(c.name)
            })
          }
        }
      })
    },

    showBenchEntry (box) {
      if(box.config.type == 'bench') {
        if(box.summary.has_pending == true &&  box.summary.experiments.length >=2 && box.summary.experiments[box.summary.experiments.length-2].destroyed == false) {
          return true
        } else if(box.summary.has_pending == false) {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    },

    // fetch all boxes
    getAllBoxes() {
      this.allBoxes = []
      this.loading = true
      this.allBoxesNames = []
      ajax.getBox(0, 100).then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list == null) {
            this.allBoxes = []
            this.loading = false
            this.total_box_count = result.data.data.total
            this.allBoxesNames = []
          } else {
            this.allBoxes = result.data.data.list
            console.log('all boxes: ', this.allBoxes)
            this.totalBoxes = this.allBoxes
            this.total_box_count = result.data.data.total
            this.loading = false
            this.allBoxes.forEach(i => {
              var box = i
              this.allBoxesNames.push(box.name)
            })
          }
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get all boxes Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    // create a new box
    handlCreateNewBox() {
      this.clearBoxForm()
      this.activeName = []
      this.createBoxDialog = true
      this.fetchCreatedCatPool()
      this.fetchCreatedTestTemplate()
      this.fetchConfigMaps()
    },

    // fetch all created test case
    fetchCreatedTestTemplate() {
      ajax.getTestTemplate(0, 10000, '').then((result) => {
        console.log('total case', result.data.data)
        if(result.data.code == 200) {
          this.createdTestTemplateNames = []
          if(result.data.data.list == null){
            this.createdTestTemplateNames = []
          } else {
            var testList = result.data.data.list
            testList.forEach(c => {
              var testCase = c
              if(testCase.status == 'DONE') {
                this.createdTestTemplateNames.push(testCase.name)
              }
            })
          }
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    fetchCreatedCatPool() {
      ajax.getAllFilteredCat(0, 10000, '').then((result) => {
        var totalCat = []
        if(result.data.data.list == null) {
          totalCat = []
        } else {
          totalCat = result.data.data.list
        }
        totalCat.forEach(i => {
          var item = i
          if(item.status == 'NORMAL' && item.stage == 'RUNNING' && item.destroyed == false) {
            this.createdCatNames.push(item.name)
          }
        })
        console.log('this.createdCatName: ', this.createdCatNames)
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    fetchConfigMaps() {
      ajax.getConfigMaps().then(result => {
        if(result.data.code == 200) {
          this.configMaps = result.data.data
        } else {
          this.$notify.error ({
            title: 'Error',
            message: 'Get config maps failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    // submit a new box
    submitNewForm(formName) {
      let submitBoxForm = {}
      console.log('this.boxDetail', this.boxForm)
      this.$refs[formName].validate((valid) => {
      if(this.boxForm.catSection.choice == "create"){
        console.log('inside create new cat')
        submitBoxForm = {
          name: this.boxForm.name,
          creator: this.boxForm.creator,
          description: this.boxForm.description,
          // without_cat: this.boxForm.without_cat,
          cat: {
            labels: this.boxForm.catSection.labels,
            selected_cat: this.boxForm.catSection.selected_cat,
            pd_config: {
              version: {
                type: this.boxForm.catSection.pd_ver.type,
                value: this.boxForm.catSection.pd_ver.value,
                platform: this.boxForm.catSection.pd_ver.platform,
              },
              config_template_name: this.boxForm.catSection.pd_ver.config_template_name,
              size: this.boxForm.catSection.pd_size,
              log_level: this.boxForm.catSection.pd_ver.log_level,
              resource_config: {
                requests: {
                  cpu: this.boxForm.catSection.pd_ver.resource_config.requests.cpu,
                  memory: this.boxForm.catSection.pd_ver.resource_config.requests.memory,
                  storage_class: this.boxForm.catSection.pd_ver.resource_config.requests.storage_class
                },
                limits: {
                  cpu: this.boxForm.catSection.pd_ver.resource_config.limits.cpu,
                  memory: this.boxForm.catSection.pd_ver.resource_config.limits.memory,
                  storage_class: this.boxForm.catSection.pd_ver.resource_config.limits.storage_class
                }
              }
            },
            tikv_config: {
              version: {
                type: this.boxForm.catSection.tikv_ver.type,
                value: this.boxForm.catSection.tikv_ver.value,
                platform: this.boxForm.catSection.tikv_ver.platform,
                compile_option: this.boxForm.catSection.tikv_ver.compile_option
              },
              config_template_name: this.boxForm.catSection.tikv_ver.config_template_name,
              size: this.boxForm.catSection.tikv_size,
              log_level: this.boxForm.catSection.tikv_ver.log_level,
              resource_config: {
                requests: {
                  cpu: this.boxForm.catSection.tikv_ver.resource_config.requests.cpu,
                  memory: this.boxForm.catSection.tikv_ver.resource_config.requests.memory,
                  storage_class: this.boxForm.catSection.tikv_ver.resource_config.requests.storage_class
                },
                limits: {
                  cpu: this.boxForm.catSection.tikv_ver.resource_config.limits.cpu,
                  memory: this.boxForm.catSection.tikv_ver.resource_config.limits.memory,
                  storage_class: this.boxForm.catSection.tikv_ver.resource_config.limits.storage_class
                }
              }
            },
            tidb_config: {
              version: {
                type: this.boxForm.catSection.tidb_ver.type,
                value: this.boxForm.catSection.tidb_ver.value,
                platform: this.boxForm.catSection.tidb_ver.platform,
              },
              config_template_name: this.boxForm.catSection.tidb_ver.config_template_name,
              size: this.boxForm.catSection.tidb_size,
              log_level: this.boxForm.catSection.tidb_ver.log_level,
              resource_config: {
                requests: {
                  cpu: this.boxForm.catSection.tidb_ver.resource_config.requests.cpu,
                  memory: this.boxForm.catSection.tidb_ver.resource_config.requests.memory,
                  storage_class: this.boxForm.catSection.tidb_ver.resource_config.requests.storage_class
                },
                limits: {
                  cpu: this.boxForm.catSection.tidb_ver.resource_config.limits.cpu,
                  memory: this.boxForm.catSection.tidb_ver.resource_config.limits.memory,
                  storage_class: this.boxForm.catSection.tidb_ver.resource_config.limits.storage_class
                }
              }
            }
          },
          rules: this.boxForm.ruleSection,
          tests: {
            in_order: this.boxForm.testSection.in_order,
            tests: this.boxForm.testSection.testList,
            labels: this.boxForm.testSection.labels
          },
          config: {
            slack: this.boxForm.miscConfigSection.slack,
            destroy_tidb_cluster: this.boxForm.miscConfigSection.destroy_tidb_cluster,
            type: this.boxForm.miscConfigSection.type,
            timeout: Math.round(((this.boxForm.miscConfigSection.day)*24*3600 + (this.boxForm.miscConfigSection.hour)*3600 + (this.boxForm.miscConfigSection.min)*60) / Math.pow(10, -9))
          }
        }
      } else if (this.boxForm.catSection.choice == "select"){
        submitBoxForm = {
          name: this.boxForm.name,
          creator: this.boxForm.creator,
          description: this.boxForm.description,
          cat: {
            labels: this.boxForm.catSection.labels,
            selected_cat: this.boxForm.catSection.selected_cat,
          },
          rules: this.boxForm.ruleSection,
          tests: {
            in_order: this.boxForm.testSection.in_order,
            tests: this.boxForm.testSection.testList,
            labels: this.boxForm.testSection.labels
          },
          config: {
            slack: this.boxForm.miscConfigSection.slack,
            destroy_tidb_cluster: this.boxForm.miscConfigSection.destroy_tidb_cluster,
            type: this.boxForm.miscConfigSection.type,
            timeout: Math.round(((this.boxForm.miscConfigSection.day)*24*3600 + (this.boxForm.miscConfigSection.hour)*3600 + (this.boxForm.miscConfigSection.min)*60) / Math.pow(10, -9))
          }
        }
      } else {
        submitBoxForm = {
          name: this.boxForm.name,
          creator: this.boxForm.creator,
          description: this.boxForm.description,
          without_cat: true,
          rules: this.boxForm.ruleSection,
          tests: {
            in_order: this.boxForm.testSection.in_order,
            tests: this.boxForm.testSection.testList,
            labels: this.boxForm.testSection.labels
          },
          config: {
            slack: this.boxForm.miscConfigSection.slack,
            destroy_tidb_cluster: this.boxForm.miscConfigSection.destroy_tidb_cluster,
            type: this.boxForm.miscConfigSection.type,
            timeout: Math.round(((this.boxForm.miscConfigSection.day)*24*3600 + (this.boxForm.miscConfigSection.hour)*3600 + (this.boxForm.miscConfigSection.min)*60) / Math.pow(10, -9))
          }
        }
      }

      console.log('the submit box form is: ', submitBoxForm)
      if (valid) {
        ajax.createBox(submitBoxForm).then(result => {
          if (result.data.code != 200) {
            this.$notify({
              title: "ERROR",
              type: 'error',
              message: result.data.message,
              duration: 0
            });
            return
          }
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Create New Box Successfully'
          })
          this.createBoxDialog = false
          this.getAllBoxes()
        }).catch(resp =>  {
          this.$notify.error({
            title: 'Failed to Create A New Box',
            message: resp.message,
            duration: 0
          })
        })
      } else {
        this.$message.error('Please check the form, some form items are invalid')
        this.activeName = ['miscConfigSection', 'catSection', 'testSection', 'ruleSection']
        return false;
      }
      })
    },

    // clear box form
    clearBoxForm() {
      console.log('click clear box form')
      this.boxForm = {
        name: '',
        creator: '',
