    // tigger box to generate a new experiment
    handleManualTrigger(boxID, boxName, has_pending ) {
      var warningMsg = ''
      if(has_pending) {
        warningMsg = warningMsg + 'Exists pending experiment in the queue. Continue triggering a new experiment will cover the pending experiment.'
      } else {
        warningMsg = warningMsg + 'Continue triggering a new experiment immediately'
      }
      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Continue',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        ajax.triggerBoxByID(boxID).then((result) =>{
          if(result.data.code !== 200) {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Manual Trigger Box Failed'
            })
            return
          }
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Manual Trigger Box: ' + boxName + ' Successfully'
          })
          this.getAllBoxes()
        }).catch((resp) => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

