    // handle PD version type change, clear its old value
    handlePDVerTypeChange() {
      this.boxForm.catSection.pd_config.version.value = ''
    },

    // handle TiDB version type change, clear its old value
    handleTiDBVerTypeChange() {
      this.boxForm.catSection.tidb_config.version.value = ''
    },

    // handle TiKV version type change, clear its old value
    handleTiKVVerTypeChange() {
      this.boxForm.catSection.tidb_config.version.value = ''
    },

    // handle experiment trigger rule, clear its old value
    handleTriggerRuleChange() {
      if(this.boxForm.ruleSection[0].type == 'IMMEDIATELY') {
        this.boxForm.ruleSection[0].value == ''
      }
    },

    handleResume(boxID, boxName) {
      // console.log('click box resume, BOXSTATUS', boxStatus)
      this.$confirm('Continue resuming the box to generate new experiment:?', 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        ajax.resumeBoxByID(boxID).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Resume Box '+ boxName + ' Successfully'
            })
            this.getAllBoxes()
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Resume Box '+ boxName,
            })
          }
        }).catch(resp => {
          this.$notify.error({
            title: 'Error in resume box by id',
            message: resp.message,
            duration: 0
          })
        })
      }).catch((resp) => {
        this.$notify.error({
          title: 'Resume box Failed',
          message: resp.message,
          duration: 0
        })
        // console.log('click cancel')
        return
      })
    },

    processAbnormalExperiment(boxID, experimentID) {
      console.log('process abnormal experiment')
      this.makeAnnotation = true
      this.markExperimentID = experimentID
      this.markBoxID = boxID
    },

    getAnnotationInfo(boxID, experimentID, annotationMsg, authorMsg) {
      this.makeAnnotation = true
      this.markExperimentID = experimentID
      this.markBoxID = boxID
      this.annotationInfo = {
        annotation: annotationMsg,
        author: authorMsg
      }
    },

    markProcessExperiment(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          ajax.markProcessExperiment(this.markBoxID, this.markExperimentID, this.annotationInfo).then(result => {
            if(result.data.code == 200) {
              this.$notify({
                title: "Successful",
                type: 'success',
                message: 'Make Annotation Successfully'
              })
              this.makeAnnotation = false
              this.getAllBoxes()
            } else {
              this.$notify.error({
                title: 'Error',
                message: 'Submit annotation Failed',
                duration: 0
              })
            }
          }).catch(resp => {
            this.$notify.error({
              title: 'Error',
              message: resp.message,
              duration: 0
            })
          })
        } else {
          this.$message.error('Please check the form, some form items are invalid')
          return false;
        }
      })
    },

    searchNameFunc() {
      if(this.searchBoxName == '') {
        this.getAllBoxes()
      } else {
        this.allBoxes = []
        var returnBox = []
        this.allBoxesNames.forEach((n, index) => {
          var name = n
          if(name.toLowerCase().includes(this.searchBoxName.toLowerCase())) {
            returnBox.push(this.totalBoxes[index])
          }
        })
        this.allBoxes = returnBox
      }
    },

    benchResult(boxID) {
      let route = this.$router.resolve({path: '/bench', query:{boxid: boxID}})
      window.open(route.href, '_blank')
    }
  }
}
