<template>
 <div>
   <div style="margin-bottom: 0px; margin-left: 25px; margin-top: 10px">
      <Divider orientation="left" style="font-size:20px;"  height="5px">Bench Result</Divider>
    </div>
   <div v-if="noData">
     <p style="font-size: 20px; margin-left: 40px;">This bench box has no test result yet!</p>
   </div>
   <el-form style="margin-top: 20px" v-if="!noData">
     <el-row>
       <el-col :span="12">
         <el-form-item label="Select Box" style="font-weight: 500" v-if="getBoxName">
            <el-select v-model="selectedBox" filterable placeholder="Please search or select a box" style="width: 300px" @change="boxChanged">
              <el-option v-for="box in boxData" :key="box.boxId" :label="box.label" :value="box.boxId" ></el-option>
            </el-select>
        </el-form-item>
       </el-col>
       <el-col :span="12">
        <el-date-picker v-if="getBoxName"
            style="margin-bottom: 15px; float: right"
            v-model="initTimeValue"
            type="datetimerange"
            text="Select Date"
            :picker-options="pickerOptions"
            range-separator="To"
            start-placeholder="Start Date"
            end-placeholder="End Date"
            align="right"
            @change="handleUpdateTimeRange">
          </el-date-picker>
       </el-col>
     </el-row>

     <el-form-item label="Select Cases" style="font-weight: 500" v-if="getBoxName">
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">All Cases</el-checkbox>
        <el-checkbox-group v-model="checkedCasesNames" @change="handleCheckedCasesChange" style="margin-left: 97px">
          <el-checkbox v-for="caseName in allCasesNames" :key="caseName" :label="caseName">{{caseName}}</el-checkbox>
        </el-checkbox-group>
     </el-form-item>
   </el-form>
   <masonry :cols="{default: 3, 2200: 2, 500: 1}" :gutter="{default: '10px', 700: '10px'}" v-loading="loading" element-loading-text="Loading" v-if="!noData">
    <div style="margin-bottom: 10px" v-for="(c, index) in checkedCasesNames" :key="index">
      <el-card class="card__background">
        <div class="benchChart" ref="benchCharts" :style="{ height: '300px'}"></div>
      </el-card>
    </div>
   </masonry>
 </div>
</template>

<script>
import ajax from '../request/index'
import _ from 'lodash'

export default {
  data () {
    return {
      noData: false,
      initTimeValue: '',
      getBoxName: false,
      loading: false,
      boxID: this.$route.query.boxid,
      checkedCases: {},
      checkedCasesNames: [],
      allCases: [],
      allCasesNames: [],
      isIndeterminate: false,
      checkAll: true,
      allBoxes: [],
      boxData:[],
      selectedBox: 'bench-1',
      config_ver: [],
      run_ver: [],
      min_start: '',
      max_end: '',
      select_start_time: new Date(),
      select_end_time: new Date(),
      pickerOptions: {
        shortcuts: [{
          text: 'Last Week',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: 'Last Month',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
    }
  },

  created() {
    new Date(this.select_start_time.setTime(this.select_start_time.getTime() - 3600 * 1000 * 24 * 7));
    this.min_start = this.select_start_time.getFullYear() + "-" + (this.select_start_time.getMonth() + 1) + "-" + this.select_start_time.getDate()
    this.max_end = this.select_end_time.getFullYear() + "-" + (this.select_end_time.getMonth() + 1) + "-" + (this.select_end_time.getDate() + 1)
    this.fetchTestResult()
    this.fetchAllBenchBoxes()
  },

  watch: {
    checkedStatus: {
      handler: function (newVal, oldVal) {
        console.log('in warch: checkstatus: ', this.checkedStatus)
      },
      deep: true
    },
    selectedBox: function (val) {
      this.selectedBox = val
    }
  },

  updated() {
    this.$nextTick(function() {
      this.drawLine()
    })
  },

  methods: {
    handleUpdateTimeRange(time) {
      this.select_start_time = time[0]
      this.select_end_time = time[1]
      this.min_start = this.select_start_time.getFullYear() + "-" + (this.select_start_time.getMonth() + 1) + "-" + this.select_start_time.getDate()
      this.max_end = this.select_end_time.getFullYear() + "-" + (this.select_end_time.getMonth() + 1) + "-" + (this.select_end_time.getDate() + 1)
      this.fetchTestResult()
    },

    // fetch all boxes and get box names for future select
    fetchAllBenchBoxes() {
      console.log("fetching all boxes")
      this.allBoxes = []
      ajax.getBox(0, 100).then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list == null) {
            this.allBoxes = []
          } else {
            this.allBoxes = result.data.data.list
            // console.log('all boxes: ', this.allBoxes)
            this.allBoxes.forEach(b=> {
              var box = b
              if(box.config.type == 'bench') {
                this.boxData.push({value: box.name, label: box.name, boxId: box.id})
              }
            })
            // console.log('all boxData: ', this.boxData)
          }
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get all boxes Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

   // draw line chart
    drawLine(){
      // prepare dominitialize echarts instance
      var charts = document.getElementsByClassName('benchChart')
      var qps = []
      var eps = []
      var avgms = []
      var pct90 = []
      var pct99 = []
      var legend =[]
      var seriesData = []
      var legendData = []
      var hasSecondYAxis = false
      var yAxisData = []
      for(var caseIndex = 0; caseIndex < charts.length; caseIndex++){

        // init variable for chart data
        var chart = this.$echarts.init(charts[caseIndex])
        qps = []
        eps = []
        avgms = []
        pct90 = []
        pct99 = []
        legend =[]
        seriesData = []
        legendData = []
        yAxisData = []
        var hasSecondYAxis = false

        // parse data for each experiment
        _.values(this.checkedCases)[caseIndex].forEach(e => {
          var exp = e
          if(exp.qps == 0) {
            qps.push(null)
          } else {
            qps.push({'value': [exp.create_time,exp.qps]})
          }

          if(exp.avgms == 0) {
            avgms.push(null)
          } else {
            avgms.push({'value': [exp.create_time,exp.avgms]})
          }

          if(exp.eps == 0) {
            eps.push(null)
          } else {
            eps.push({'value': [exp.create_time,exp.eps]})
          }

          if(exp.pct_90 == 0) {
            pct90.push(null)
          } else {
            pct90.push({'value': [exp.create_time,exp.pct_90]})
          }

          if(exp.pct_99 == 0) {
            pct99.push(null)
          } else {
            pct99.push({'value': [exp.create_time,exp.pct_99]})
          }

          // if(exp.QueryResults != null) {
          //   var queryLen = exp.QueryResults.length
          //   for(var queryIdx = 0; queryIdx < queryLen; queryIdx++) {
          //     // var query
          //   }
          //   queryResult.push({'value': [exp.create_time, exp.QueryResults.cost]})
          // } else {
          //   queryResult.push(null)
          // }
        })

        // check if the test attribute has value, if it has, add to seriesData
        if(qps.some(q => { return q != null })) {
          seriesData.push({
            name:'QPS',
            type:'line',
            data: qps,
          })
          legendData.push('QPS')
        }
        if(avgms.some(avg => { return avg != null})) {
          seriesData.push({
            name:'Avgms',
            type:'line',
            data: avgms,
            yAxisIndex: 1,
          })
          legendData.push('Avgms')
          hasSecondYAxis = true
        }
        if(eps.some(e => {return e != null})) {
          seriesData.push({
            name:'EPS',
            type:'line',
            data: eps,
          })
          legendData.push('EPS')
        }
        if(pct90.some(p => {return p != null})) {
          seriesData.push({
            name:'Pct90',
            type:'line',
            data: pct90,
            yAxisIndex: 1,
          })
          legendData.push('Pct90')
          hasSecondYAxis = true
        }
        if(pct99.some(p => { return p != null})) {
          seriesData.push({
            name:'Pct99',
            type:'line',
            data: pct99,
            yAxisIndex: 1,
          })
          legendData.push('Pct99')
          hasSecondYAxis = true
        }

        // if(queryResult.some(q => { return q != null})) {
        //   seriesData.push({
        //     name:'Query Result',
        //     type:'line',
        //     data: queryResult,
        //   })
        //   legendData.push('Query Result')
        // }
        // check if it needs two y axis
        if(hasSecondYAxis) {
          yAxisData.push(
            {
              type: 'value',
              position: 'left',
            },
            {
              type: 'value',
              axisLabel : {
                formatter: '{value} ms'
              },
              position: 'right',
            }
          )
        } else {
          yAxisData.push({
              type: 'value',
              position: 'left',
          })
        }

        // set the options to chart
        chart.setOption({

          grid: {
            left: 80,
            right: 80,
            top: 80
          },
          title: {
              text: this.checkedCasesNames[caseIndex]
          },
          tooltip: {
              trigger: 'axis',
          },
          legend: {
              top:  '40px',
              data: legendData
          },
          toolbox: {
            right: 30,
            feature: {
              dataZoom : {show: true, title: 'zoom in'},
                saveAsImage: {
                    show: true,
                    title: 'Save as image'
                }
            }
          },
          xAxis: {
              type: 'time',
              boundaryGap: false,
              min: this.min_start,
              max: this.max_end,
              axisLabel: {
                show: true
              }
          },
          yAxis: yAxisData,
          series: seriesData
        })
      }
    },

    // fetch the test result of a box
    fetchTestResult() {
      this.loading = true
      ajax.getBenchResult(this.boxID, this.select_start_time, this.select_end_time).then(result => {
        this.noData = true
        if(result.data.code == 200) {
          console.log('get bench result: ', result.data.data)
          if(result.data.data.total == 0 ) {
            this.noData = true
          } else {
            this.noData = false
            this.allCasesNames = []
            this.allCases = result.data.data.list
            this.checkedCases = this.allCases
            this.allCasesNames = _.keys(this.allCases)
            this.selectedBox = _.flatten(_.values(this.allCases))[0].box
            this.getBoxName = true
            this.checkedCasesNames = this.allCasesNames
          }
          this.loading = false
        } else {
          this.$notify.error({
            title: 'ERROR',
            message: 'fetch bench result Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'ERROR',
          message: resp.message,
          duration: 0
        })
      })
    },

    // handle check all changed
    handleCheckAllChange(val) {
      this.checkedCasesNames = val ? this.allCasesNames : []
      this.checkedCases = val ? this.allCases : {}
      this.isIndeterminate = false
    },

    // handle checked cases changed
    handleCheckedCasesChange(value) {
      console.log('value is', value)
      this.checkedCases = {}
      let checkedCount = value.length
      this.checkAll = checkedCount === this.allCasesNames.length
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.allCasesNames.length
      value.forEach(c => {
        for(var v in this.allCases) {
          if(v == c ) {
            this.checkedCases[v] = this.allCases[v]
          }
        }
      })

    },

    // handle box changed
    boxChanged(boxID) {
      console.log('box changed, the value passed is: ', boxID)
      this.boxID = boxID
      this.fetchTestResult()
    }
  }
}
</script>

<style>
</style>



