<template>
<div>
  <div class="create-button"><el-button type="primary" @click="clickCreateConfigTemplate"> + Create Config Template</el-button></div>

  <!-- TODO: Add config filter items -->

  <!-- TODO: config display table -->
  <el-table :highlight-current-row="true" :data="configTemplateList" :header-cell-style="{background: '#ebeef5'}">>
    <el-table-column v-for="(label,index) in configTableLabels" :key="index" header-align="center" :align="index == 0 ? 'left' : 'center'" :label="label">
      <template slot-scope="scope">{{scope.row[configTableValues[index]]}}</template>
    </el-table-column>
    <el-table-column header-align="center" align="center" label="Operation">
      <template slot-scope="scope">
        <el-tooltip v-for="(tooltip, index) in opToolTip" :key="index" :content="tooltip">
          <el-button :type="index == 0 ? '': 'danger'" :icon="index == 0 ? 'el-icon-edit' : 'el-icon-delete'" circle @click="index == 0 ? clickEditConfigTemplate(scope.row.id) : clickDeleteConfigTemplate(scope.row.id)"></el-button>
        </el-tooltip>
      </template>
    </el-table-column>
  </el-table>

  <!-- TODO: config form -->
  <el-dialog :close-on-click-modal="false" title="Config Template" :visible.sync="configTemplateDialog" top="10vh" :lock-scroll='true'>
    <el-form :model="configTemplateForm" :rules="rules" ref="configTemplateForm" label-width="9rem">
      <el-form-item label="Config Name: " prop="name">
        <el-input v-model="configTemplateForm.name" placeholder="Enter config name"/>
      </el-form-item>

      <el-form-item label="Tag: " prop="tag">
        <el-radio-group v-model="configTemplateForm.tag">
          <el-radio v-for="(tag, index) in configTags" :key="index" :label="configTags[index]"> {{configTags[index]}}</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="Description: " prop="description">
        <el-input v-model="configTemplateForm.description" placeholder="Please briefly describe this config template"/>
      </el-form-item>

      <el-form-item label="Content: " prop="content">
        <el-input class="tomlInput" type="textarea" v-model="configTemplateForm.content" :autosize="{minRows:10}" placeholder="Please enter toml file"/>
      </el-form-item>
    </el-form>

    <div slot="footer" class="dialog-footer">
      <el-button @click="configTemplateDialog = false">Cancel</el-button>
      <el-button @click="resetForm('configTemplateForm')">Reset</el-button>
      <el-button v-if="create" type="primary" @click="submitForm('configTemplateForm')"> Create </el-button>
      <el-button v-else type="primary" @click="submitForm('configTemplateForm')"> Save </el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import ajax from '../request/index'

export default {
  data() {
    var checkEmpty = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkString = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(regx.test(value)) {
        return callback(new Error('Must be string'))
      } else {
        callback()
      }
    })

    return {
      configTemplateForm: {
        name: '',
        tag: 'tidb',
        description: '',
        content: ''
      },
      configTemplateDialog: false,
      configTemplateDetail: '',
      create: false,
      configTemplateList: [],
      configTableLabels: ['Name', 'Tag', 'Create Time', 'Update Time'],
      configTableValues: ['name', 'tag', 'create_time', 'update_time'],
      operations: ['edit', 'delete'],
      opToolTip: ['Edit config template', 'Delete config template'],
      configTags: ['tidb', 'tikv', 'pd'],
      rules: {
         name: [{required: true, validator: checkString, trigger: 'blur'}],
         content: [{required: true, validator: checkString, trigger: 'blur'}]
      }
    }
  },

  created() {
  },

  mounted() {
    console.log('DOM is ready')
    this.getConfigTemplateList()
  },

  updated() {
    console.log('data updated')
    if(!this.configTemplateDialog) {
      this.create = false
      this.resetForm('configTemplateForm')
    }
  },

  methods: {
    // get config list
    getConfigTemplateList() {
      ajax.getConfigTemplateList().then(result => {
        if(result.data.code == 200) {
          if(result.data.data.total == 0) {
            this.configTemplateList = []
          } else {
            this.configTemplateList = result.data.data.list
            console.log('config template list: ', this.configTemplateList)
          }
        } else {
          this.$notify.error({
            title: 'ERROR',
            message: 'Get config template list failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'ERROR',
          message: resp.message,
          duration: 0
        })
      })
    },

    // create config template
    clickCreateConfigTemplate() {
      this.configTemplateDialog = true
      this.create = true
    },

    // edit config template
    clickEditConfigTemplate(configID) {
      console.log('edit, name: ', configID)
      this.configTemplateDialog = true
      ajax.getConfigTemplateByID(configID).then(result => {
        if(result.data.code == 200) {
          this.configTemplateDetail = result.data.data
          console.log('config detail: ', this.configTemplateDetail)
          this.fillForm()
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get config detail failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    resetForm(formName) {
      console.log('click reset')
      if (this.$refs[formName] != null) {
        this.$refs[formName].resetFields()
      }
    },

    fillForm() {
      this.configTemplateForm = {
        name: this.configTemplateDetail.name,
        content: this.configTemplateDetail.content,
        description: this.configTemplateDetail.description,
        tag: this.configTemplateDetail.tag
      }
    },

    submitForm(formName) {
      console.log('submit')
      this.$refs[formName].validate((valid) => {
        if(valid) {
          if(this.create) {
            console.log('create')
            console.log('config form: ', this.configTemplateForm)
            ajax.createConfigTemplate(this.configTemplateForm).then(result => {
              if(result.data.code == 200) {
                this.$notify({
                  title: "SUCCESS",
                  type: 'success',
                  message: 'Create config successfully!'
                });
                this.configTemplateDialog = false
                this.create = false
                this.getConfigTemplateList()
              } else {
                this.$notify.error({
                  title: 'Error',
                  message: 'Get config detail failed',
                  duration: 0
                })
              }
            }).catch(resp => {
               this.$notify.error({
                title: 'Error',
                message: resp.message,
                duration: 0
              })
            })
          } else {
            console.log('edit')
            console.log('config form: ', this.configTemplateForm)
            ajax.updateConfigTemplateByID(this.configTemplateDetail.id, this.configTemplateForm).then(result => {
              if(result.data.code == 200) {
                this.$notify({
                  title: 'SUCCESS',
                  type: 'success',
                  message: 'Create config successfully'
                })
                this.configTemplateDialog = false
                this.create = false
                this.getConfigTemplateList()
              } else {
                this.$notify.error({
                  title: 'Error',
                  message: 'Get config detail failed',
                  duration: 0
                })
              }
            }).catch(resp => {
               this.$notify.error({
                title: 'Error',
                message: resp.message,
                duration: 0
              })
            })
          }
        }
      })
    },

    // delete config template
    clickDeleteConfigTemplate(configID) {
      console.log('delete')
      ajax.deleteConfigTemplateByID(configID).then(result => {
        if(result.data.code == 200) {
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Delete config successfully'
          })
          this.getConfigTemplateList()
        } else {
        this.$notify.error({
          title: 'Error',
          message: 'Get config detail failed',
          duration: 0
        })
      }
      }).catch(resp => {
          this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    }
  }
}
</script>

<style>
.create-button {
  margin-bottom: 15px
}

.tomlInput .textarea {
  background: #2e2f30;
}
</style>




