import Vue from 'vue'
import Router from 'vue-router'
import TestCase from '@/views/TestCase'
import BoxDetail from '@/views/BoxDetail'
import Cat from '@/views/Cat'
import Experiment from '@/views/Experiment'
import CatDetail from '@/views/CatDetail'
import ExperimentDtail from '@/views/ExperimentDetail'
import Box from '@/views/Box'
import Summary from '@/views/Summary'
import Admin from '@/views/Admin'
import Bench from '@/views/Bench'
import ConfigTemplate from '@/views/ConfigTemplate'

Vue.use(Router)

const NotFoundView = Vue.component('NotFoundView', {
  template: '<h1>...Ops, 404</h1>'
})

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/summary'
    },
    {
      path: '/box',
      name: 'Box',
      component: Box
    },
    {
      path: '/404',
      name: 'NotFoundView',
      component: NotFoundView
    },
    {
      path: '/testcase',
      name: 'TestCase',
      component: TestCase
    },
    {
      path: '/cat',
      name: 'Cat',
      component: Cat
    },
    {
      path: '/boxdetail',
      name: 'BoxDetail',
      component: BoxDetail
    },
    {
      path: '/experiment',
      name: 'Experiment',
      component: Experiment
    },
    {
      path: '/catdetail',
      name: 'CatDetail',
      component: CatDetail
    },
    {
      path: '/experimentdetail',
      name: 'ExperimentDetail',
      component: ExperimentDtail
    },
    {
      path: '/summary',
      name: 'Summary',
      component: Summary
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin
    },
    {
      path: '/bench',
      name: 'Bench',
      component: Bench
    },
    {
      path: '/config',
      name: 'Config',
      component: ConfigTemplate
    }
  ]
})



