<template>
<div v-loading="loading" element-loading-text="Loading">
  <el-button v-on:click="goBack" style="cursor: pointer; margin-top: 18px"><Icon type="md-arrow-back" />Go Back</el-button>

  <Divider orientation="left" style="width: 30%">Cat Basic Info</Divider>

  <el-row>
    <el-card class="catDetailCard">
      <el-form class="basicForm" inline :data="catDetail">
        <el-form-item label="Cat Name: "> {{catDetail.name}}</el-form-item>
        <Divider type="vertical"></Divider>
        <el-form-item label="Cat Status/Stage">
            <el-tag :type="tag_type">{{cat_status}}</el-tag>
            <el-tag :type="tag_type">{{cat_stage}}</el-tag>
        </el-form-item>
      </el-form>

      <el-row :gutter="20" style="margin-top: 20px">

        <el-col :span="8" v-for="(cluster, index) in config_ver_array" :key="index">
          <el-card shadow="hover">
            <div slot="header">{{clusterNames[index]}} (size = {{cluster_sizes[index]}})</div>

            <div class="cat__detail-card-items">
              <div class="cat__basicInfo-items"> Compile Platform </div>
              <div v-if="catDetail.config != null && catDetail.config[config_ver_array[index]].version.platform" class="no-copy-block">{{catDetail.config[config_ver_array[index]].version.platform}}</div>
              <div v-else class="no-copy-block">--</div>
              <div class="underline"></div>
            </div>

            <div class="cat__detail-card-items" v-if="basicInfoTextArr.length != 0 && basicInfoTextArr[index] != ''">
              <div class="cat__basicInfo-items"> Config Version </div>
               <el-tooltip>
                <div slot="content">{{basicInfoTextArr[index]}}</div>
                <div class="no-copy-block">
                  <span>hash: </span>
                  <span class="cat__basicInfo-copy-text">{{(basicInfoTextArr[index]).substring(0,7)}}...</span>
                  <span class="command__copy-table" v-clipboard="basicInfoTextArr[index]" @success="handleSuccess">
                    <el-tooltip placement="top">
                      <div slot="content">copy</div>
                      <Icon class="command__copy-icon" type="md-copy"/>
                    </el-tooltip>
                  </span>
                </div>
              </el-tooltip>
            </div>

            <div class="cat__detail-card-items" v-else-if="basicInfoTextArr.length != 0 && basicInfoTextArr[index] == ''">
              <div class="cat__basicInfo-items"> Config Version </div>
              <div class="no-copy-block"> {{catDetail.config[config_ver_array[index]].version.type}}:{{catDetail.config[config_ver_array[index]].version.value}} </div>
              <div class="underline"></div>
            </div>

            <div class="cat__detail-card-items" v-if="catDetail.run_version && basicInfoTextArr.length != 0 && basicInfoTextArr[index+3] != ''">
              <div class="cat__basicInfo-items"> Run Version </div>
              <el-tooltip>
                <div slot="content">{{basicInfoTextArr[index+3]}}</div>
                <div class="no-copy-block">
                  <span>hash: </span>
                  <span class="cat__basicInfo-copy-text">{{(basicInfoTextArr[index+3]).substring(0,7)}}...</span>
                  <span class="command__copy-table" v-clipboard="basicInfoTextArr[index+3]" @success="handleSuccess">
                    <el-tooltip placement="top">
                      <div slot="content">copy</div>
                      <Icon class="command__copy-icon" type="md-copy"/>
                    </el-tooltip>
                  </span>
                </div>
              </el-tooltip>
              <div class="underline"></div>
            </div>

            <div class="cat__detail-card-items" v-else>
              <div class="cat__basicInfo-items"> Run Version </div>
              <div class="no-copy-block"> -- </div>
              <div class="underline"></div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-card>
  </el-row>

  <Divider orientation="left" style="width: 30%">Cluster Info</Divider>
   <masonry v-if=" copyTextArr.length == 4 && catDetail.cluster != null" :cols="{default: 3, 1900: 3, 1500: 2, 1000: 1}"  :gutter="{default: '50px', 700: '20px'}">
    <el-card class="catDetailCard connect-monitor-card" shadow="hover">
      <div slot="header" class="clearfix">Connect and Monitor</div>
      <div v-if=" !catDetail.cluster.tidb_service" style="text-align: center">No Data</div>
      <div class="service__header connect-monitor-header" style="border-top: 1px solid #ebeef5;"> TiDB Service </div>
      <div class="command" v-if="copyTextArr[0]">
        <div class="command__name">MySQL connection:</div>
        <div type="text" class="command__line">{{copyTextArr[0]}}</div>
        <div class="command__copy" v-clipboard="copyTextArr[0]" @success="handleSuccess">
          <el-tooltip content="copy" placement="top">
            <Icon class="command__copy-icon" type="md-copy"/>
          </el-tooltip>
        </div>
      </div>

      <div v-else class="command">
        <div class="command__name">MySQL connection:</div>
        <div> -- </div>
      </div>


      <div class="service__header"> Prometheus Service </div>
      <div class="command">
        <div class="command__name">URL:</div>
        <div class="command__line url__line"><a :href="'http://172.16.30.11:' + catDetail.cluster.prometheus_service.node_port"> http://172.16.30.11:{{catDetail.cluster.prometheus_service.node_port}}</a></div>
      </div>

      <div class="service__header"> Grafana Service </div>
      <div class="command">
        <div class="command__name">URL:</div>
        <div class="command__line url__line"><a :href="'http://172.16.30.11:' + catDetail.cluster.grafana_service.node_port">http://172.16.30.11:{{catDetail.cluster.grafana_service.node_port}}</a></div>
      </div>
    </el-card>

    <el-card v-for="(detailCard, card) in detail_array" :key="card" class="catDetailCard" shadow="hover">
      <div slot="header" class="clearfix">{{detailCard}}</div>

      <!-- show No Data if there is no data -->
      <div v-if=" !catDetail.cluster[status_array[card]]" style="text-align: center">No Data</div>

      <!-- show Detail if there exits data -->
      <el-collapse v-else v-for="(item, index) in catDetail.cluster[status_array[card]]" :key="index">
        <div class="service__header tidb-service__header"> {{item.name}}</div>

        <div v-if="detailCard == 'TiDB Detail'" class="command" v-for="(name, nameIndex1) in itemNames1" :key="nameIndex1">
          <div class="command__name">{{name}}</div>
          <div type="text" class="command__line" v-if="copyTextArr[1 + card][index][nameIndex1]">{{copyTextArr[1 + card][index][nameIndex1]}}</div>
          <div class="command__copy" v-if="copyTextArr[1 + card][index][nameIndex1]" v-clipboard="copyTextArr[1 + card][index][nameIndex1]" @success="handleSuccess">
            <el-tooltip content="copy" placement="top">
              <Icon class="command__copy-icon" type="md-copy"/>
            </el-tooltip>
          </div>
          <el-tooltip v-else>
            <template slot="content">In pendding stage</template>
            <div style="color: black;"> -- </div>
          </el-tooltip>
        </div>

        <div v-if="detailCard != 'TiDB Detail'" class="command">
          <div class="command__name">Log Address</div>
          <div type="text" class="command__line" v-if="copyTextArr[1 + card][index][0]">{{copyTextArr[1 + card][index][0]}}</div>
          <div class="command__copy" v-if="copyTextArr[1 + card][index][0]" v-clipboard="copyTextArr[1 + card][index][0]" @success="handleSuccess">
            <el-tooltip content="copy" placement="top">
              <Icon class="command__copy-icon" type="md-copy"/>
            </el-tooltip>
          </div>
          <el-tooltip v-else>
            <template slot="content">In pendding stage</template>
            <div style="color: black;"> -- </div>
          </el-tooltip>
        </div>

        <el-collapse-item class="tidb-detail-collapse">
          <div class="command" v-for="(name, nameIndex2) in itemNames2" :key="nameIndex2">
            <div class="command__name">{{name}}</div>
            <div type="text" class="command__line" v-if="detailCard != 'TiDB Detail' && copyTextArr[1 + card][index][nameIndex2 + 1]">{{copyTextArr[1 + card][index][nameIndex2+1]}}</div>
            <div class="command__copy" v-if="detailCard != 'TiDB Detail' && copyTextArr[1 + card][index][nameIndex2+1]" v-clipboard="copyTextArr[1 + card][index][nameIndex2+1]" @success="handleSuccess">
              <el-tooltip content="copy" placement="top">
                <Icon class="command__copy-icon" type="md-copy"/>
              </el-tooltip>
            </div>

            <div type="text" class="command__line" v-else-if="detailCard == 'TiDB Detail' && copyTextArr[1 + card][index][nameIndex2+2]">{{copyTextArr[1 + card][index][nameIndex2+2]}}</div>
            <div class="command__copy" v-else-if="detailCard == 'TiDB Detail' && copyTextArr[1 + card][index][nameIndex2+2]" v-clipboard="copyTextArr[1 + card][index][nameIndex2+2]" @success="handleSuccess">
              <el-tooltip content="copy" placement="top">
                <Icon class="command__copy-icon" type="md-copy"/>
              </el-tooltip>
            </div>

            <el-tooltip v-else content="In pendding stage">
              <div style="color: black;"> -- </div>
            </el-tooltip>
          </div>
        </el-collapse-item>
      </el-collapse>
    </el-card>
  </masonry>

  <el-row v-else :gutter="10">
    <el-col :span="6">
      <el-card class="catDetailCard">
        <div slot="header" class="clearfix">Connect and Monitor</div>
        Waiting cluster to build...
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card class="catDetailCard">
        <div slot="header" class="clearfix">TiDB Detail</div>
        Waiting cluster to build...
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card class="catDetailCard">
        <div slot="header" class="clearfix">TiKV Detail</div>
        Waiting cluster to build...
      </el-card>
    </el-col>
    <el-col :span="6">
      <el-card class="catDetailCard">
        <div slot="header" class="clearfix">PD Detail</div>
        Waiting cluster to build...
      </el-card>
    </el-col>
  </el-row>
</div>
</template>

<script>
import ajax from '../request/index'
import Clipboards from 'clipboard'
import _ from 'lodash'

export default {
  data() {
    var checkEmpty = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      } else {
        callback()
      }
    })

    var checkString = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(regx.test(value)) {
        return callback(new Error('Must be string'))
      } else {
        callback()
      }
    })

    var checkNumber = ((rule, value, callback) => {
      if(!value) {
        return callback(new Error('Cannot be empty'))
      }
      var regx = /^[0-9]*$/
      if(!regx.test(value)) {
        return callback(new Error('Must be a number'))
      } else {
        callback()
      }
    })

    var checkUnique = ((rule, value, callback) => {
      let isExist = this.createdCatNames.includes(value)
      if(isExist) {
        return callback(new Error('The Name already exists, please try another one'))
      } else {
        callback()
      }
    })
    return {
      udpateCatFormDialog: false,
      catID: this.$route.query.id,
      catDetail: '',
      copyTextArr: [],
      basicInfoTextArr: [],
      myIndex: 1,
      cat_status: '',
      cat_stage: '',
      tag_type: '',
      updateBasicInfoIndex: 1,
      detail_array: ['TiDB Detail', 'TiKV Detail', 'PD Detail'],
      status_array: ['tidb_status', 'tikv_status', 'pd_status'],
      itemNames1: ['MySQL Connection', 'Log Address' ],
      itemNames2: ['Pod IP', ' Node IP', 'Container ID', 'Image'],
      itemNames3: ['Compile Platform', 'Config Version', 'Run Version'],
      config_ver_array: ['tidb_config', 'tikv_config', 'pd_config'],
      run_ver_arrary: ['tidb_version', 'tikv_version', 'pd_version'],
      cluster_sizes: [],
      clusterNames: ['TiDB', 'TiKV', 'PD'],
      loading: true,
    }
  },

  created() {
    this.getCatDetail(this.catID)
  },

  mounted() {
    console.log("this.copyTextArr.length: ", this.copyTextArr.length)
  },

  updated() {
    console.log('the catdetail: ', this.catDetail)

    // set cat state and stage
    if(!this.catDetail.destroyed) {
      if(this.catDetail.state == "NORMAL") {
        this.cat_status = 'Normal'
        this.tag_type = 'success'
      } else {
        this.cat_status = 'Abnormal'
        this.tag_type = 'danger'
      }
    } else {
      if(this.catDetail.state == 'NORMAL') {
        this.cat_status = 'Normal & Destroyed'
        this.tag_type = 'success'
      } else {
        this.cat_status = 'Abnormal & Destroyed'
        this.tag_type = 'warning'
      }
    }
    this.cat_stage = (this.catDetail.stage).charAt(0) + (this.catDetail.stage).toLowerCase().slice(1)

    // get copy text in basic info section
    if(this.updateBasicInfoIndex == 1) {
      this.updateBasicInfoIndex++
      this.cluster_sizes.push(this.catDetail.tidb_size, this.catDetail.tikv_size, this.catDetail.pd_size)
      let tempConfig_ver = this.catDetail.config
      let tempRun_ver = this.catDetail.run_version

      // get copy text of config version and run version
      for(let i = 0; i < this.config_ver_array.length; i++) {
        if(tempConfig_ver[this.config_ver_array[i]].version.type == 'hash') {
          this.basicInfoTextArr.push(tempConfig_ver[this.config_ver_array[i]].version.value)
        } else {
          this.basicInfoTextArr.push('')
        }
      }

      if(!tempRun_ver) {
        this.basicInfoTextArr.push('', '', '')
      } else {
        for(let j = 0; j < this.run_ver_arrary.length; j++) {
          if(tempRun_ver[this.run_ver_arrary[j]].type == 'hash') {
            this.basicInfoTextArr.push(tempRun_ver[this.run_ver_arrary[j]].value)
          } else {
            this.basicInfoTextArr.push('')
          }
        }
      }
    }

    // get copy text in cluster info section
    if(this.myIndex == 1 && this.catDetail.cluster && (this.catDetail.cluster.tidb_status || this.catDetail.cluster.tikv_status || this.catDetail.cluster.pd_status)) {
      this.myIndex++
      let status_attr_array = []
      let status_copy_text = []
      if(this.catDetail.cluster.tidb_status) {
        // debugger
        this.copyTextArr.push("mysql -h " + this.catDetail.cluster.tidb_status[0].node_ip + " -P " + this.catDetail.cluster.tidb_service.node_port + " -u root")
      } else {
        // debugger
        this.copyTextArr.push('')
      }

      // check if there exists data in status_array[i]
      for(let i = 0; i < this.status_array.length; i++) {
        // debugger
        let status = null
        if(this.catDetail.cluster[this.status_array[i]]) {
          status = this.catDetail.cluster[this.status_array[i]]
        } else {
          status = null
        }

        if(status) {
          // debugger
          status_copy_text = []
          for(let j = 0; j < status.length; j++) {
            status_attr_array = []
            if(this.status_array[i] == 'tidb_status') {
              status_attr_array.push('mysql -h ' + status[j].pod_ip + ' -P 4000 -u root')
            }
            status_attr_array.push(status[j].log_addr)
            status_attr_array.push(status[j].pod_ip)
            status_attr_array.push(status[j].node_ip)
            status_attr_array.push(status[j].container_id)
            status_attr_array.push(status[j].image)
            status_copy_text.push(status_attr_array)
          }
        } else {
          status_copy_text.push('', '', '', '', '', '')
        }
        this.copyTextArr.push(status_copy_text)
      }
    }
    console.log('copyTextArr: ', this.copyTextArr)
    this.loading = false
  },

  methods: {
    handleSuccess() {
      this.$message.success('Copied!')
    },

    getCatDetail(catID) {
      ajax.getCatDetailByID(catID).then(result => {
        if(result.data.code == 200) {
          this.catDetail = result.data.data
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Failed to Get Cat Detail'
          })
        }
      }).catch (resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    goBack() {
      console.log('go back clicked')
      this.$router.go(-1)
    },
  }
}
</script>
<style>


  .ivu-divider-inner-text {
    font-size: 25px;
    font-weight: 400;
  }

  .basicForm {
    margin: 1.5em 0 0 0;
  }

  .basicForm .el-form-item .el-form-item__label{
    font-size: 15px;
  }

  .basicForm .el-form .ivu-divider, .ivu-divider-vertical {
    margin: 0 2px;
    width: 2px;
    height: 2em;
    top: 0.5em;
  }
</style>




