<template>
<div>
    <div>
      <div style=" margin-bottom: 15px; margin-top: 10px;">
        <div style="font-size: 15px; margin-right: 15px;"><strong>Filter Experiments:</strong> </div>
        <el-tag>Status</el-tag>
        <el-checkbox-group style="display: inline-block;" v-model="experimentStatusCheckList" @change="handleExperimentStatusCheckListChange">
          <el-checkbox label="Normal" style="margin-left: 5px"></el-checkbox>
          <el-checkbox label="Abnormal" style="margin-left: 10px"></el-checkbox>
        </el-checkbox-group>
        <el-tag style="margin-top: 10px; margin-left: 45px">Stage</el-tag>
          <el-checkbox-group style="display: inline-block" v-model="experimentStageCheckList" @change="handleExperimentStageCheckListChange">
            <el-checkbox label="Pending" style="margin-left: 5px"></el-checkbox>
            <el-checkbox label="Preparing" style="margin-left: 10px"></el-checkbox>
            <el-checkbox label="Running" style="margin-left: 10px"></el-checkbox>
            <el-checkbox label="Finished" style="margin-left: 10px"></el-checkbox>
          </el-checkbox-group>
        <el-tag style="margin-top: 10px; margin-left: 45px">Resources</el-tag>
        <el-checkbox-group style="display: inline-block" v-model="experimentDestroyedCheckList" @change="handleExperimentDestroyedCheckListChange">
          <el-checkbox label="Released" style="margin-left: 5px"></el-checkbox>
          <el-checkbox label="Not Released" style="margin-left: 10px"></el-checkbox>
        </el-checkbox-group>
      </div>

     <el-table :highlight-current-row="true" v-loading="loading" element-loading-text="Loading" :data="filteredExperiment" :header-cell-style="{background: '#ebeef5'}">
        <el-table-column header-align="center" align="left" label="Experiment Name" prop="name" class="experimentTableList"></el-table-column>

        <el-table-column header-align="center" align="center" label="Status/Stage">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status == 'NORMAL'" type="success">{{(scope.row.stage).charAt(0) + (scope.row.stage).toLowerCase().slice(1)}}</el-tag>
            <el-tag v-else-if="scope.row.status == 'ABNORMAL'" type='danger'>{{(scope.row.stage).charAt(0) + (scope.row.stage).toLowerCase().slice(1)}}</el-tag>
          </template>
        </el-table-column>

        <el-table-column header-align="center" align="center" label="Release Resources" >
          <template slot-scope="scope">
            <el-tooltip v-if="scope.row.stage == 'PENDING'" class="item" effect="dark" content="Waiting experiment to run">
              <div>--</div>
            </el-tooltip>
            <el-tooltip v-else-if="scope.row.destroyed == true" content="Resources released">
              <Icon type="ios-color-fill-outline" style="font-size: 30px" color="green"/>
            </el-tooltip>
            <el-tooltip v-else-if="scope.row.destroyed == false" content="Resources not released">
              <Icon type="ios-beaker" style="font-size: 30px" color="#f4c842"/>
            </el-tooltip>
          </template>
        </el-table-column>

        <el-table-column header-align="center" align="center" label="Create Time" prop="create_time"> </el-table-column>
        <el-table-column header-align="center" align="center" label="Update Time" prop="update_time"> </el-table-column>

        <el-table-column label="Cat Version">
            <template slot-scope="scope">
              <el-tooltip transition="none" class="item" effect="dark">
                <div slot="content" v-if="scope.row.stage == 'PENDING'">Waiting experiment to run</div>
                <div slot="content" v-else-if="scope.row.run_version == null"> Waiting cat to run </div>
                <div v-if="scope.row.stage == 'PENDING'"> -- </div>
                <div v-else-if="scope.row.run_version == null"> -- </div>
              </el-tooltip>

              <el-tooltip transition="none" placement="top">
                <div slot="content" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">Config version: {{scope.row.config.pd_config.version.type}}-{{scope.row.config.pd_config.version.value}}
                      <br>Run version: {{scope.row.run_version.pd_version.type}}-{{scope.row.run_version.pd_version.value}}</div>
                <el-tag size="mini" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">PD: ({{(scope.row.config.pd_config.version.value).substring(0,7)}})({{(scope.row.run_version.pd_version.value).substring(0,7)}})</el-tag>
              </el-tooltip>

              <el-tooltip transition="none" placement="top">
                <div slot="content" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">Config version: {{scope.row.config.tikv_config.version.type}}-{{scope.row.config.tikv_config.version.value}}
                      <br>Run version: {{scope.row.run_version.tikv_version.type}}-{{scope.row.run_version.tikv_version.value}}</div>
                <el-tag size="mini" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null" style="margin-top: 5px">TiKV: ({{(scope.row.config.tikv_config.version.value).substring(0,7)}})({{(scope.row.run_version.tikv_version.value).substring(0,7)}})</el-tag>
              </el-tooltip>

              <el-tooltip transition="none" placement="top">
                <div slot="content" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null">Config version: {{scope.row.config.tidb_config.version.type}}-{{scope.row.config.tidb_config.version.value}}
                      <br>Run version: {{scope.row.run_version.tidb_version.type}}-{{scope.row.run_version.tidb_version.value}}</div>
                <el-tag size="mini" v-if="scope.row.stage != 'PENDING' && scope.row.run_version != null" style="margin-top: 5px">TiDB: ({{(scope.row.config.tidb_config.version.value).substring(0,7)}})({{(scope.row.run_version.tidb_version.value).substring(0,7)}})</el-tag>
              </el-tooltip>
            </template>
          </el-table-column>

        <el-table-column header-align="center" align="center" label="Operations">
          <template slot-scope="scope">
            <el-tooltip class="item" effect="dark">
              <div v-if="scope.row.status == 'ABNORMAL'" slot="content">Handle abnormal</div>
              <div v-else-if="scope.row.status == 'NORMAL'" slot="content">Stop experiment</div>
              <el-button v-if="scope.row.stage == 'PENDING'" circle><Icon type="ios-close" size="150px"></Icon></el-button>
              <el-button v-else-if="scope.row.status == 'ABNORMAL' && scope.row.destroyed == false" type="danger" circle @click.native='handleStopExperiment(scope.row.box ,scope.row.id, scope.row.box_index, scope.row.destroy_cat)'><Icon type="ios-pause" size="100px"></Icon></el-button>
              <el-button v-else-if="scope.row.status == 'NORMAL' && scope.row.destroyed == false" type="danger" circle @click.native='handleStopExperiment(scope.row.box  ,scope.row.id, scope.row.box_index, scope.row.destroy_cat)'><Icon type="ios-pause" size="100px"></Icon></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="Redo this experiment with same box config">
              <el-button v-if="scope.row.destroyed == false" type="warning" circle @click.native="handleExperimentRedoClick(scope.row.box,scope.row.id)"><Icon type="md-refresh"></Icon></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="Experiment detail">
              <el-button type="primary" circle @click.native="handleExperimentDetailClick(scope.row.id, scope.row.box)"><Icon type="ios-paper-outline"></Icon></el-button>
            </el-tooltip>
          </template>
        </el-table-column>

     </el-table>

     <div style="text-align: center; margin-top: 15px">
        <el-pagination v-if="searchExperimentName == ''" layout="prev, pager, next" :page-size="pageSize" :current-page="currentPage" :total="total_experiment_count" @current-change="handleCurrentChange"></el-pagination>
      </div>
    </div>
</div>
</template>

<script>
import ajax from '../request/index'
export default {
  data() {

    let activeTab = 'experiment';
    location.hash && (activeTab = location.hash.slice(2));
    console.log('active Tab', activeTab)

    return {
      loading: false,
      activeTab,
      pageSize: 10,
      filteredString: '',
      currentPage: 1,
      experimentCheckList: [],
      experimentTable: [],
      filteredExperiment: [],
      experimentNames: [],
      total_experiment_count: 0,
      allExperiments: [],
      allExperimentsNames: [],
      searchExperimentName: '',
      statusString: [],
      stageString: [],
      destroyedString: [],
      experimentStatusCheckList: [],
      experimentStageCheckList: [],
      experimentDestroyedCheckList: []
    }
  },

  created() {
    this.getFilteredExperiment()
    this.fetchAllExperiments()
  },

  methods: {
    fetchAllExperiments() {
      ajax.getAllFilteredExperiment(0, 1000, '').then(result => {
        if(result.data.code == 200) {
          this.allExperiments = result.data.data.list
          this.allExperiments.forEach((e, index) => {
            var exp = e
            this.allExperimentsNames.push(exp.name)
          })
          console.log('all experiments names: ', this.allExperimentsNames)
        } else {
          this.$notify.error({
            title: 'ERROR',
            message: 'Fetch all experiment Failed',
            duration: 0
          })
        }
      }).catch(resp =>{
        this.$notify.error({
          title: 'ERROR',
          message: resp.message,
          duration: 0
        })
      })
    },

    handleCurrentChange(current_page) {
      console.log('current_page: ',current_page)
      this.currentPage = current_page;
      this.getFilteredExperiment((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString);
    },

    getFilteredExperiment(offset = 0, size = 10, filteredString = '') {
      this.filteredExperiment = []
      this.loading = true
      ajax.getAllFilteredExperiment(offset, size, this.filteredString).then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list == null) {
            this.filteredExperiment = []
            this.total_experiment_count = result.data.data.total
            this.loading = false
          } else {
            this.filteredExperiment = result.data.data.list
            this.total_experiment_count = result.data.data.total
            console.log('thisfilteredExperiment: ', this.filteredExperiment)
            console.log('total experiment count', this.total_experiment_count)
            this.loading = false
          }

        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get all experiment Failed'
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })

    },

    handleExperimentCheckListChange(statusString, stageString, destroyedString) {
      this.filteredString = this.statusString + this.stageString + this.destroyedString
      console.log('handle cat checked list change , filteredString is: ', this.filteredString)
      this.getFilteredExperiment((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString)
    },

    handleExperimentStatusCheckListChange() {
      this.currentPage = 1
      console.log('the current page after changing check list', this.currentPage)
      console.log('checked cat status are: ', this.experimentStatusCheckList)
      this.statusString = ''
      this.experimentStatusCheckList.forEach(i => {
        var item = i.toLowerCase()

        this.statusString = this.statusString + '&status=' + item
      })

      console.log('statusString: ', this.statusString)
      this.handleExperimentCheckListChange(this.statusString, this.stageString, this.destroyedString)
    },

    handleExperimentStageCheckListChange() {
      this.currentPage = 1
      console.log('the current page after changing check list', this.currentPage)
      console.log('checked cat stage are: ', this.experimentStageCheckList)
      this.stageString = ''
      this.experimentStageCheckList.forEach(i => {
        var item = i.toLowerCase()

        this.stageString = this.stageString + '&stage=' + item
      })

      console.log('stageString: ', this.stageString)
      this.handleExperimentCheckListChange(this.stageString, this.stageString, this.destroyedString)
    },

    handleExperimentDestroyedCheckListChange() {
      this.currentPage = 1
      console.log('the current page after changing check list', this.currentPage)
      console.log('checked cat destroyed are: ', this.experimentDestroyedCheckList)
      this.destroyedString = ''
      this.experimentDestroyedCheckList.forEach(i => {
        var item = i
        if(item == 'Released') {
          this.destroyedString = this.destroyedString + '&destroyed=true'
        } else {
          this.destroyedString = this.destroyedString + '&destroyed=false'
        }
      })

      console.log('destroyedString: ', this.destroyedString)
      this.handleExperimentCheckListChange(this.destroyedString, this.stageString, this.destroyedString)

    },

    handleExperimentDetailClick(experimentID, boxID){
      console.log('click experiment detail')
      let route = this.$router.resolve({path: '/experimentdetail', query: {boxid: boxID, experimentid: experimentID}})
      window.open(route.href)
      // location.href = route.href
    },

    handleStopExperiment(boxID, experimentID, experimentIndex,destroy_tidb_cluster) {
      console.log('handle stop experiment')
      var warningMsg = ''
      if(destroy_tidb_cluster) {
        console.log('destroy_tidb_cluster is true')
        warningMsg = warningMsg + 'The cat testing in this experiment will be destroyed if you continue stopping experiment #'+ experimentID
      } else {
        warningMsg = warningMsg + 'Continue stopping experiment # ' + experimentID
      }
      this.$confirm(warningMsg, 'Warning', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'Cancel',
        type:'warning',
        center: true
      }).then(() => {
        console.log('click yes')
        ajax.destroyExperimentByID(boxID, experimentID).then(result => {
          if(result.data.code == 200) {
            this.$notify({
              title: 'Success',
              type: 'success',
              message: 'Stop Experiment: ' + experimentIndex + ' Successfully'
            })
            // this.getExperiments(boxID, (this.currentPage - 1) * this.pageSize, this.pageSize)
            this.getFilteredExperiment((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString);
          } else {
            this.$notify.error({
              title: 'Error',
              message: 'Failed to Stop Experiment: ' + experimentIndex
            })
          }
        }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
      }).catch(() => {
        console.log('click cancel')
        return
      })
    },

    handleExperimentRedoClick(boxID, experimentId) {
      ajax.redoExperimentByID(boxID, experimentId).then((result) => {
        if(result.data.code == 200) {
          this.$notify({
            title: 'Success',
            type: 'success',
            message: 'Redo Experiment: ' +  experimentId + ' Successfully'
          })
          this.getFilteredExperiment((this.currentPage - 1) * this.pageSize, this.pageSize, this.filteredString);
        } else {
          this.$notify.error({
            title: 'Failed',
            message: 'Failed to Redo Experiment: ' +  experimentId
          })
        }

      }).catch(resp => {
          this.$notify.error({
            title: 'Error',
            message: resp.message
          })
        })
    },

    searchNameFunc() {
      if(this.searchExperimentName == '') {
        this.getFilteredExperiment(0, 10, this.filteredString)
      } else {
        var returnExperiment = []
        this.allExperimentsNames.forEach((n, index) => {
          var name = n
          if(name.toLowerCase().includes(this.searchExperimentName.toLowerCase())) {
            console.log('cat is', index, this.allExperiments[index])
            returnExperiment.push(this.allExperiments[index])
          }
        })
        this.filteredExperiment = []
        this.filteredExperiment = returnExperiment
        this.total_experiment_count = this.filteredExperiment.length
      }
    },
  }

}
</script>

<style>
.experiment_card .el-card__body {
  padding: 0px;
}
.card_header {
    float: right;
    position: relative;
    left: -50%; /* or right 50% */
    text-align: left;
    font-size: 18px;
    font-weight: 500;
    margin-top: 10px;
  }

.experiment_card .el-card__header {
  padding: 10px 10px
}
</style>





