<template>
<!-- -webkit-linear-gradient(top, #9fbbf2, #c9cacc); -->
<div>
  <div class="table-animation" style="height: 700px">
    <div v-on:click="resizeFunc" v-if="!zoomInGraph" class="expSmall">
      <el-card>
        <div style="font-size: 30px; text-align: center; margin-bottom: 15px">Running experiments at specific time</div>
        <div style="margin-bottom: 10px">
          <div style="display: inline-block; font-size: 15px; margin-right: 10px">Pick Date</div>
          <el-date-picker style="margin-bottom: 15px;"
            v-model="value5"
            type="datetimerange"
            text="Select Date"
            :picker-options="pickerOptions"
            range-separator="To"
            start-placeholder="Start Date"
            end-placeholder="End Date"
            align="right"
            @change="handleUpdateTimeRange">
          </el-date-picker>
        </div>
        <d3-timeline v-loading="loadingExperiment" element-loading-text="Loading"
          ref="d3Timeline"
          :data="experimentData"
          :options="options"
          width="100%"
          height="600px">
        </d3-timeline>
      </el-card>
    </div>
    <div v-if="zoomInGraph" class="expBig" >
      <el-card>
        <div style="font-size: 30px; text-align: center; margin-bottom: 40px">Running experiments at specific time</div>
        <div style="margin-bottom: 20px">
          <div style="display: inline-block; font-size: 15px; margin-right: 10px">Pick Date</div>
          <el-date-picker style="margin-bottom: 15px;"
            v-model="value5"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="To"
            start-placeholder="Start Date"
            end-placeholder="End Date"
            align="right"
            @change="handleUpdateTimeRange">
          </el-date-picker>
        </div>
        <d3-timeline v-loading="loadingExperiment" element-loading-text="Loading"
          ref="d3Timeline"
          :data="experimentData"
          :options="options"
          width="100%"
          height="600px">
        </d3-timeline>
      </el-card>
    </div>

    <div v-loading="loadingResource" element-loading-text="Loading" v-if="!zoomInGraph" class="chartBig" style="width: 80%">
      <el-card>
        <div style="font-size: 30px; text-align: center; margin-bottom: 15px">Real-time resource utilization </div>
        <div>
          <ve-pie :data="usageRateData" style="margin-top: 60px"></ve-pie>
        </div>
      </el-card>
    </div>
    <div v-loading="loadingResource" element-loading-text="Loading" v-if="zoomInGraph" class="chartSmall"  v-on:click="resizeFunc" style="width: 80%">
      <el-card>
        <div style="font-size: 30px; text-align: center; margin-bottom: 15px">Real-time resource utilization</div>
        <div>
          <ve-pie :data="usageRateData" style="margin-top: 60px"></ve-pie>
        </div>
      </el-card>
    </div>
  </div>
</div>
</template>

<script>
import ajax from '../request/index'
export default {

  data() {
    $(document).ready(function(){
      $("button").click(function(){
          $("#div1").width(500).height(500);
      });
    });
    return {
      totalExperimentLimitSize: 0,
      runningExperiment: [],
      normalExperimentNo: 0,
      abnormalExperimentNo: 0,
      zoomInGraph: false,
      items: [],
      loadingResource: false,
      loadingExperiment: false,
      select_start_time: new Date(),
      select_end_time: new Date(),
      value5: '',
      experimentData: [],
      pickerOptions: {
        shortcuts: [{
          text: 'Last Week',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: 'Last Month',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      experiments: {},
      options: {
        intervalCornerRadius:0,
        symbolSize: 500,
        groupLabelFontSize: 15,
        groupLabelFontWeight: 400,
        groupLabelFontOpacity: 0.5,
        groupLaneWidth:100,
        tickSize: 10,
        tickPadding: 12,
        axisXLaneHeight: 50,
        axisFontSize: 30,
        axisFontWeight: 1000,
        axisXLabel: 'Time',
        axisYLabel: 'Experiments',
        borderRadius: 0,
        borderWidth: 0,
        continue_check_index: 0,
        boundingLineWidth: 1,
        currentTimeLineWidth: 1,
        max_totalExperimentLimitSize: 0,
      },
      usageRateData: {
        columns: ['Type', 'Number'],
        rows: [
          { 'Type': 'Normal Utilization', 'Number':  0},
          { 'Type': 'Free', 'Number': 0 },
          { 'Type': 'Abnormal Utilization', 'Number': 0 }
        ]
      },
      lastest8Experiment: [],
      time1: new Date(),
      time2: new Date(),
    }
  },
  created() {
    new Date(this.select_start_time.setTime(this.select_start_time.getTime() - 3600 * 1000 * 24 * 7));
    console.log('this.select_start_time and this.select_end_time: ', this.select_start_time, this.select_end_time)
    this.fetchAllExperimentSizeLimit()
  },

  mounted: function() {
  },

  methods: {
    fetchAllExperimentSizeLimit() {
      ajax.getAllExperimentSizeLimit().then(result => {
        if(result.data.code == 200) {
          this.totalExperimentLimitSize = result.data.data
          this.max_totalExperimentLimitSize = this.totalExperimentLimitSize
          this.time1 = new Date()
          this.fetchExperiment(this.select_start_time, this.select_end_time)
          this.fetchResourceUsage()
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get All Experiment Size Limit Failed',
            duration: 0
          })
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message,
          duration: 0
        })
      })
    },

    fetchExperiment(starttime, endtime) {
      this.loadingExperiment = true
      ajax.getSummaryExperiments(starttime, endtime).then(result=>{
        if(result.data.code == 200) {
          if(result.data.data.list == null) {
            this.experiments = []
            this.loadingExperiment = false
          } else {
            this.experiments = result.data.data.list
          }
          var groupIndex = 0

          var earliest_end_experiment_index = 0
          var earliest_end_time = new Date(this.experiments[0].end_time)
          this.experiments.forEach((item, index) => {
            var exp = item
            var statusColor = ''
            if(exp.status == 'NORMAL') {
              statusColor = 'normal_experiment_css'
            } else {
              statusColor = 'abnormal_experiment_css'
            }

            if(index < this.totalExperimentLimitSize) {

                // set bar group according to start time and end time
                if(groupIndex == this.totalExperimentLimitSize) {
                  groupIndex = 0
                }
                groupIndex = groupIndex + 1
                var startTime = new Date(exp.start_time)
                if(exp.end_time == '') {
                  var endTime = new Date()
                } else {
                  var endTime = new Date(exp.end_time)
                }
                var expToAdd = {
                  from: startTime,
                  to: endTime,
                  title: exp.name,
                  label: exp.name,
                  group: 'Slot ' + groupIndex,
                  className: statusColor
                }
                this.experimentData.push(expToAdd)
                this.lastest8Experiment.push(expToAdd)
            } else {
                 if(exp.end_time == '') {
                    var endTime = new Date()
                  } else {
                    var endTime = new Date(exp.end_time)
                  }
                  var startTime = new Date(exp.start_time)
                  for (var i = 0; i < this.max_totalExperimentLimitSize; i++ ) {
                    if(startTime >= this.lastest8Experiment[i].to) {
                      var expToAdd = {
                        from: startTime,
                        to: endTime,
                        title: exp.name,
                        label: exp.name,
                        group: this.lastest8Experiment[i].group,
                        className: statusColor
                      }
                      this.lastest8Experiment.splice(i, 1, expToAdd)
                      this.experimentData.push(expToAdd)
                      break
                    }
                    if(i == this.max_totalExperimentLimitSize - 1) {
                      this.max_totalExperimentLimitSize = this.max_totalExperimentLimitSize + 1
                      var expToAdd = {
                        from: startTime,
                        to: endTime,
                        title: exp.name,
                        label: exp.name,
                        group: 'Slot' + this.max_totalExperimentLimitSize,
                        className: statusColor
                      }
                      this.experimentData.push(expToAdd)
                      this.lastest8Experiment.push(expToAdd)
                      break
                    }
                  }

            }
            this.loadingExperiment = false
          })
        } else {
          this.$notify.error({
            title: 'Error',
            message: 'Get Experiments Summary Failed',
            duration: 0
          })
        }
        this.time2 = new Date() - this.time1
      })
    },

    sortLast8Experiment(a,b) {
      if (a.to < b.to)
        return -1;
      if (a.to > b.to)
        return 1;
      return 0;
    },

    fetchResourceUsage() {
      this.loadingResource = true
      ajax.getSummaryExperiments(new Date(), new Date()).then(result => {
        if(result.data.code == 200) {
          if(result.data.data.list == null) {
            this.runningExperiment = []
            this.loadingResource = false
          } else {
             this.runningExperiment = result.data.data.list
          }
          this.abnormalExperimentNo = 0
          this.normalExperimentNo = 0
          this.runningExperiment.forEach(i => {
            var experiment = i
            if(experiment.status == 'NORMAL') {
              this.normalExperimentNo = this.normalExperimentNo + 1
            } else if(experiment.status == 'ABNORMAL') {

              this.abnormalExperimentNo = this.abnormalExperimentNo + 1
            }
          })
          this.usageRateData.rows[0].Number = this.normalExperimentNo
          this.usageRateData.rows[2].Number = this.abnormalExperimentNo
          this.usageRateData.rows[1].Number = (this.totalExperimentLimitSize - this.normalExperimentNo - this.abnormalExperimentNo)
          this.loadingResource = false
          console.log('abnormal ', this.abnormalExperimentNo)
          console.log('normal:' , this.normalExperimentNo)
          console.log('this.totalExperimentLimitSize', this.totalExperimentLimitSize)
        }
      }).catch(resp => {
        this.$notify.error({
          title: 'Error',
          message: resp.message
        })
      })
    },

    handleUpdateTimeRange(time) {
      this.select_start_time = time[0]
      this.select_end_time = time[1]
      this.$refs.d3Timeline.updateTimeRange(this.select_start_time, this.select_end_time)
    },

    resizeFunc() {
      this.zoomInGraph = !this.zoomInGraph
    }
  }
}
</script>

<style>

.experiment {
    height: 100px;
    width: 300px;
    padding: 10px;
    margin: 3px;
    background-color: lightblue;
}

.expBig {
  position: relative;
  transform:  translate(-9%, -1%) scale(0.8);
}

.hello {
  width:100px;
  height:100px;
  background:red;
  position:relative;
  animation:myfirst 5s;
  -moz-animation:myfirst 5s; /* Firefox */
  -webkit-animation:myfirst 5s; /* Safari and Chrome */
  -o-animation:myfirst 5s; /* Opera */
}

@keyframes myfirst
{
0%   {background:red; left:0px; top:0px;}
25%  {background:yellow; left:200px; top:0px;}
50%  {background:blue; left:200px; top:200px;}
75%  {background:green; left:0px; top:200px;}
100% {background:red; left:0px; top:0px;}
}
@-moz-keyframes myfirst /* Firefox */
{
0%   {background:red; left:0px; top:0px;}
25%  {background:yellow; left:200px; top:0px;}
50%  {background:blue; left:200px; top:200px;}
75%  {background:green; left:0px; top:200px;}
100% {background:red; left:0px; top:0px;}
}

@-webkit-keyframes myfirst /* Safari and Chrome */
{
0%   {background:red; left:0px; top:0px;}
25%  {background:yellow; left:200px; top:0px;}
50%  {background:blue; left:200px; top:200px;}
75%  {background:green; left:0px; top:200px;}
100% {background:red; left:0px; top:0px;}
}

@-o-keyframes myfirst /* Opera */
{
0%   {background:red; left:0px; top:0px;}
25%  {background:yellow; left:200px; top:0px;}
50%  {background:blue; left:200px; top:200px;}
75%  {background:green; left:0px; top:200px;}
100% {background:red; left:0px; top:0px;}
}


.expSmall {
  position: relative;
  transform:  translate(-39%, -30%) scale(0.2, 0.2);
  transition-duration: 1s;
}

.chartBig {
  transform: translate(25%, -130%) scale(0.95);
  transition-duration: 1s;
}

.chartSmall {
  transform: translate(63%, -170%) scale(0.2,0.2);
  transition-duration: 1s;
}

.normal_experiment_css {
  fill: #46bf95;
  stroke: #46bf95;
  stroke-opacity: 1;
  cursor: pointer;
}

.abnormal_experiment_css {
  fill: #FF7C7C;
  stroke: #FF7C7C;
  stroke-opacity: 1;
  cursor: pointer;
}

/* .is-active {
  transform: translate(1%, 1%) scale(0.8);
  transition-duration: 3s;
} */
</style>




