class Ajax {
  getFilteredExperimentByBoxID (boxID, offset, size, filteredString) {
    return axios.get(`${Proxy}/boxes/${boxID}/experiments?offset=${offset}&size=${size}${filteredString}`)
  }

  getAllFilteredExperiment (offset, size, filteredString) {
    return axios.get(`${Proxy}/experiments?offset=${offset}&size=${size}${filteredString}`)
  }

  getAllFilteredCat (offset, size, filteredString) {
    return axios.get(`${Proxy}/cats?offset=${offset}&size=${size}${filteredString}`)
  }

  getTestTemplate (offset, size, filteredString) {
    return axios.get(`${Proxy}/tests/templates?offset=${offset}&size=${size}${filteredString}`)
  }

  getTestTemplateDetailByName (testTemplateName) {
    return axios.get(`${Proxy}/tests/templates/${testTemplateName}`)
  }

  updateTestTemplate (testTemplateName, testData) {
    return axios.put(`${Proxy}/tests/templates/${testTemplateName}`, testData)
  }

  createTestTemplate (data) {
    return axios.post(`${Proxy}/tests/templates`, data)
  }

  deleteTestTemplateByName (testTemplateName) {
    return axios.delete(`${Proxy}/tests/templates/${testTemplateName}`)
  }

  getBox (offset, size) {
    return axios.get(`${Proxy}/boxes?offset=${offset}&size=${size}`)
  }

  // getBox () {
  //   return axios.get(`${Proxy}/boxes`)
  // }

  createBox (boxData) {
    return axios.post(`${Proxy}/boxes`, boxData)
  }

  updateBoxByID (boxID, boxData) {
    console.log(boxData)
    return axios.put(`${Proxy}/boxes/${boxID}`, boxData)
  }

  deleteBoxByID (boxID) {
    return axios.delete(`${Proxy}/boxes/${boxID}`)
  }

  stopBoxByID (boxID) {
    return axios.post(`${Proxy}/boxes/${boxID}/stop`)
  }

  triggerBoxByID (boxID) {
    return axios.post(`${Proxy}/boxes/${boxID}/trigger`)
  }

  resumeBoxByID (boxID) {
    return axios.post(`${Proxy}/boxes/${boxID}/recover`)
  }

  getBoxDetailByID (boxID) {
    return axios.get(`${Proxy}/boxes/${boxID}`)
  }

  getCat () {
    return axios.get(`${Proxy}/cats`)
  }

  getCatDetailByID (catID) {
    return axios.get(`${Proxy}/cats/${catID}/detail`)
  }

  createCat (catData) {
    return axios.post(`${Proxy}/cats`, catData)
  }

  getCatDetailByName (catName) {
    return axios.get(`${Proxy}/cats/byname/${catName}`)
  }

  upgradeCatByID (catID, catData) {
    return axios.post(`${Proxy}/cats/${catID}/upgrade`, catData)
  }

  destroyCat (catID) {
    return axios.delete(`${Proxy}/cats/${catID}`)
  }

  getAllExperiments () {
    return axios.get(`${Proxy}/experiments`)
  }

  getExperimentsByBoxID (boxID, offset, size, checkList) {
    return axios.get(`${Proxy}/boxes/${boxID}/experiments?offset=${offset}&size=${size}&checkList=${checkList}`)
  }

  getExperimentsDetailByID (boxID, experimentID) {
    return axios.get(`${Proxy}/boxes/${boxID}/experiments/${experimentID}`)
  }

  destroyExperimentByID (boxID, experimentID) {
    return axios.post(`${Proxy}/boxes/${boxID}/experiments/${experimentID}/destroy`)
  }

  redoExperimentByID (boxID, experimentID) {
    return axios.post(`${Proxy}/boxes/${boxID}/experiments/${experimentID}/redo`)
  }

  getTestByID (boxID, experimentID) {
    return axios.get(`${Proxy}/boxes/${boxID}/experiments/${experimentID}/tests`)
  }

  getTestDetailByName (boxID, experimentID, testName) {
    return axios.get(`${Proxy}/boxes/${boxID}/experiments/${experimentID}/tests/${testName}`)
  }

  getConfigMaps () {
    return axios.get(`${Proxy}/configmaps`)
  }

  cancelPendingExperiment (boxID, experimentID) {
    return axios.post(`${Proxy}/boxes/${boxID}/experiments/${experimentID}/cancel`)
  }

  getResourcesUsage (starttime, endtime) {
    return axios.get(`${Proxy}/summary/resourcesusage?starttime=${starttime}&endtime=${endtime}`)
  }

  getSummaryExperiments (starttime, endtime) {
    return axios.get(`${Proxy}/summary/experiments?starttime=${starttime}&&endtime=${endtime}`)
  }

  getExperimentSize () {
    return axios.get(`${Proxy}/config/general-experiment-running-size-limit`)
  }

  setExperimentSize (generalExperimentNumber) {
    return axios.post(`${Proxy}/config/general-experiment-running-size-limit`, generalExperimentNumber)
  }

  getBenchSize () {
    return axios.get(`${Proxy}/config/bench-experiment-running-size-limit`)
  }

  setBenchSize (benchExperimentNumber) {
    return axios.post(`${Proxy}/config/bench-experiment-running-size-limit`, benchExperimentNumber)
  }

  getAllExperimentSizeLimit () {
    return axios.get(`${Proxy}/config/all-experiment-running-size-limit`)
  }

  markProcessExperiment (boxID, experimentID, annotationInfo) {
    return axios.put(`${Proxy}/boxes/${boxID}/experiments/${experimentID}/mark`, annotationInfo)
  }

  getResourceUsage () {
    return axios.get(`${Proxy}/resource/usage`)
  }

  getBenchResult (boxID, startTime, endTime) {
    return axios.get(`${Proxy}/tests/results/box/${boxID}?startTime=${startTime}&&endTime=${endTime}`)
  }

  // getBenchResult (boxID) {
  //   return axios.get(`${Proxy}/tests/results/box/${boxID}`)
  // }

  getConfigTemplateList () {
    return axios.get(`${Proxy}/cat_config/templates`)
  }

  createConfigTemplate (configTemplateFormData) {
    return axios.post(`${Proxy}/cat_config/templates`, configTemplateFormData)
  }

  updateConfigTemplateByID (configTemplateID, configTemplateFormData) {
    return axios.put(`${Proxy}/cat_config/templates/${configTemplateID}`, configTemplateFormData)
  }

  deleteConfigTemplateByID (configTemplateID) {
    return axios.delete(`${Proxy}/cat_config/templates/${configTemplateID}`)
  }

  getConfigTemplateByID (configID) {
    return axios.get(`${Proxy}/cat_config/templates/${configID}`)
  }

  getConfigTemplatesByTag (configTemplateTag) {
    return axios.get(`${Proxy}/cat_config/templates/tag/${configTemplateTag}`)
  }
}
export default new Ajax()



